import { map } from '../editor.js'; // or wherever your LayoutModel instance lives
import {  treeSelection} from '../selection.js'; // if you  
 
 

// undoRedo.js  (or wherever you keep your command factories)
/**
 * @param {Array<{guid:string,x:number,z:number}>} before  – original positions
 * @param {Array<{guid:string,x:number,z:number}>} after   – final positions
 */
export function moveCommand(before, after) {

    return {
        do() {
            // move to AFTER
            after.forEach(a => {
                if (!treeSelection || treeSelection.length === 0) return;
            const sel = treeSelection.at(-1);

            const group = map.groups[sel.groupId];
            if (!group) return;

            const layer = group.layers[sel.layerId];
            if (!layer) return;



                const n = map.points[a.pointId];//layer.layout.findNode();
                if (n) { n.position.x = a.x; n.position.z = a.z; }
            });
          
        },
        undo() { // restore BEFORE
            before.forEach(b => {
                if (!treeSelection || treeSelection.length === 0) return;
            const sel = treeSelection.at(-1);

            const group = map.groups[sel.groupId];
            if (!group) return;

            const layer = group.layers[sel.layerId];
            if (!layer) return;


                //  const n = layer.layout.findNode(b.guid);
                const n = map.points[b.pointId];
                if (n) { n.position.x = b.x; n.position.z = b.z; }
               });
        
        },
        redo() { this.do(); }
    };
}

 