import { refreshViewport, canvasMain, focusOnVertices } from "./draw.js";
import { FSM_STATES, setFSMState, getCurrentState } from "./editing/state.js";
//import { isDragging } from "../shared/control.js";
import { SelectionMode } from "./models/models.js";
import { commit } from "./undoRedo/commit.js";
import { selectCommand } from "./undoRedo/select.js";
import { map } from "./editor.js";

export let pointIds = [];
export let lineIds = [];
export let polygonIds = [];
export let treeSelection = [];

//export let layerId = null;
//export let styleId = null;
export let locationId = null;
//export let groupId = null;
//export let polygonId = null;

export let isMapDirty = false;

export let currentSelectionMode = null;

export function setPolygonId(polygonId) {
  if (!treeSelection || treeSelection.length === 0) return;
  const sel = treeSelection.at(-1);
  const group = map.groups[sel.groupId];
  if (!group) return;
  const layer = group.layers[sel.layerId];
  if (!layer) return;
  const polygon = layer.polygons[polygonId];
  if (!polygon) return;
  const idx = polygonIds.indexOf(polygonId);
  if (idx !== -1) {
    polygonIds.splice(idx, 1);         // remove
  } else {
    polygonIds.push(polygonId);        // add
  }
  console.log("polygon id set to:", polygonId);
}

export function setLineId(lineId) {
  if (!treeSelection || treeSelection.length === 0) return;
  const sel = treeSelection.at(-1);
  const group = map.groups[sel.groupId];
  if (!group) return;
  const layer = group.layers[sel.layerId];
  if (!layer) return;
  const line = layer.lines[lineId];
  if (!line) return;
  const idx = lineIds.indexOf(lineId);
  if (idx !== -1) {
    lineIds.splice(idx, 1);         // remove
  } else {
    lineIds.push(lineId);           // add
  }
}

export function setPointId(pointId) {
  const point = map.points[pointId];
  if (!point) return;
  const idx = pointIds.indexOf(pointId);
  if (idx !== -1) {
    pointIds.splice(idx, 1);        // remove existing
  } else {
    pointIds.push(pointId);         // add new
    console.log("point id:" + pointId);
  }
}

export function setTreeSelection(groupId, layerId) {
  // Ensure the group exists
  const group = map.groups[groupId];
  if (!group) return;

  // If a layerId is provided, ensure that layer exists; otherwise allow group-only toggles
  if (layerId != null) {
    const layer = group.layers[layerId];
    if (!layer) return;
  }

  // Toggle this (groupId, layerId) entry without clearing others—enables multi-select
  const idx = treeSelection.findIndex(
    sel => sel.groupId === groupId && sel.layerId === layerId
  );
  if (idx !== -1) {
    // Already selected, so remove
    treeSelection.splice(idx, 1);
  } else {
    // Add new selection entry
    treeSelection.push({ groupId, layerId });
  }
}

export function setLocationId(lId) {
  locationId = lId;
  console.log("LocationId set to:", lId);
}

export function clearSelectionUndoRedo() {
  pointIds.length = 0;
  lineIds.length = 0;
  polygonIds.length = 0;
}

export function clearSelection() {
  const beforeBranchKeys = [...lineIds];
  const beforeNodesGuids = [...pointIds];
  const clearSelectionCmd = selectCommand(beforeNodesGuids, [], beforeBranchKeys, []);
  commit(clearSelectionCmd);
  setFSMState(FSM_STATES.IDLE);
  refreshViewport();
}

/**
 * Change the active selection mode.
 * @param {string} mode – one of SelectionMode.POINT, .LINE, or .POLYGON
 */
export function setSelectionMode(mode) {
  if (!Object.values(SelectionMode).includes(mode)) {
    console.error(`Invalid selection mode: ${mode}`);
    return;
  }
  console.log(`selection mode set:  ${mode}`);
  pointIds.length = 0;
  lineIds.length = 0;
  polygonIds.length = 0;
  locationId = null;
  currentSelectionMode = mode;
}

export function setIsDirty(v) {
  isMapDirty = v;
}
