

// FSM States
export const STATES = {
    IDLE: 'IDLE',
    PANNING: 'PANNING'
};

// FSM Events
export const EVENTS = {
    POINTER_DOWN: 'POINTER_DOWN',
    POINTER_MOVE: 'POINTER_MOVE',
    POINTER_UP: 'POINTER_UP',
    WHEEL: 'WHEEL'
};

/**
 * Creates a navigation system for the given canvas.
 * @param {HTMLCanvasElement} canvas   Canvas element to attach navigation to
 * @param {Object}   [options]         Optional configuration
 * @param {Function} [options.onUpdate]  Callback invoked after every pan/zoom
 * @returns {Object} Navigator instance with cleanup methods
 */
export function createNavigator(canvas, options = {}) {
    // options: initialPan {dx,dy}, initialScale, onUpdate callback
    const {
        onUpdate = () => { },
        initialPan = { dx: 0, dy: 0 },
        initialScale = 10
    } = options;
    if (!canvas) {
        throw new Error('Canvas element is required');
    }

    // Transform state
    let transform = {
        scale: initialScale,
        dx: initialPan.dx ?? 0,
        dy: initialPan.dy ?? 0
    };

    // FSM state
    let currentState = STATES.IDLE;
    let lastPointer = { x: 0, y: 0 };

    // Event handlers for each state
    const stateHandlers = {
        [STATES.IDLE]: {
            [EVENTS.POINTER_DOWN]: (event) => {
                const altKey = event.altKey || event.metaKey;
                // Only start panning on left mouse button or primary touch
                //   if (event.button === 0 || event.type === 'touchstart') {
                if (altKey) {
                    lastPointer.x = getEventX(event);
                    lastPointer.y = getEventY(event);
                    return STATES.PANNING;
                }
                return STATES.IDLE;
            },
            [EVENTS.POINTER_MOVE]: () => STATES.IDLE,
            [EVENTS.POINTER_UP]: () => STATES.IDLE,
            [EVENTS.WHEEL]: (event) => {
                handleZoom(event);
                return STATES.IDLE;
            }
        },

        [STATES.PANNING]: {



            //   [EVENTS.POINTER_DOWN]: () => STATES.PANNING,
            [EVENTS.POINTER_MOVE]: (event) => {
                handlePan(event);
                return STATES.PANNING;
            },
            [EVENTS.POINTER_UP]: () => STATES.IDLE,
            [EVENTS.WHEEL]: (event) => {
                // Allow zoom while panning
                handleZoom(event);
                return STATES.PANNING;
            }
        }
    };

    /**
     * Get X coordinate from mouse or touch event
     */
    function getEventX(event) {
        if (event.touches && event.touches.length > 0) {
            return event.touches[0].clientX;
        }
        return event.clientX;
    }

    /**
     * Get Y coordinate from mouse or touch event
     */
    function getEventY(event) {
        if (event.touches && event.touches.length > 0) {
            return event.touches[0].clientY;
        }
        return event.clientY;
    }

    /**
     * Convert screen coordinates to canvas coordinates
     * Handles the difference between CSS size and canvas resolution
     */
    function screenToCanvas(screenX, screenY) {
        const rect = canvas.getBoundingClientRect();
        const scaleX = canvas.width / rect.width;
        const scaleY = canvas.height / rect.height;

        const canvasX = (screenX - rect.left) * scaleX;
        const canvasY = (screenY - rect.top) * scaleY;

        return { x: canvasX, y: canvasY };
    }

    /**
     * Handle panning logic
     */
    function handlePan(event) {
        const currentX = getEventX(event);
        const currentY = getEventY(event);
        //  
        //  const deltaX = currentX - lastPointer.x;
        //  const deltaY = currentY - lastPointer.y;


        // Convert both current and previous positions to canvas space
        const curCanvas = screenToCanvas(currentX, currentY);
        const prevCanvas = screenToCanvas(lastPointer.x, lastPointer.y);

        const deltaX = curCanvas.x - prevCanvas.x;
        const deltaY = curCanvas.y - prevCanvas.y;

        // Update transform in canvas‑pixel units

        // Update transform
        transform.dx += deltaX;
        transform.dy += deltaY;

        // Update last pointer position
        lastPointer.x = currentX;
        lastPointer.y = currentY;

        // Notify of change
        onUpdate({ ...transform });
    }

    /**
     * Handle zoom logic with mouse-centered pivot
     */
    function handleZoom(event) {
        event.preventDefault();

        // Calculate zoom factor from wheel delta
        const deltaMode = event.deltaMode || 0;
        const factor = deltaMode === 1 ? 15 : 1; // Line vs pixel scrolling
        const zoomFactor = Math.exp(-event.deltaY * 0.001 * factor);

        // Get mouse position in canvas coordinates
        const mousePos = screenToCanvas(event.clientX, event.clientY);

        // Apply zoom transformation
        // The formula keeps the point under the mouse cursor stationary
        const newScale = transform.scale * zoomFactor;
        const newDx = mousePos.x - (mousePos.x - transform.dx) * zoomFactor;
        const newDy = mousePos.y - (mousePos.y - transform.dy) * zoomFactor;

        // Update transform
        transform.scale = newScale;
        transform.dx = newDx;
        transform.dy = newDy;

        // Notify of change
        onUpdate({ ...transform });
    }

    /**
     * Dispatch an event to the FSM
     */
    function dispatch(eventType, event) {
        const handler = stateHandlers[currentState]?.[eventType];
        if (handler) {
            const nextState = handler(event);
            if (nextState && nextState !== currentState) {
                currentState = nextState;
            }
        }
    }

    // Event listeners
    function onPointerDown(event) {
        dispatch(EVENTS.POINTER_DOWN, event);
    }

    function onPointerMove(event) {
        dispatch(EVENTS.POINTER_MOVE, event);
    }

    function onPointerUp(event) {
        dispatch(EVENTS.POINTER_UP, event);
    }

    function onWheel(event) {
        dispatch(EVENTS.WHEEL, event);
    }

    // Attach event listeners
    canvas.addEventListener('mousedown', onPointerDown);
    canvas.addEventListener('mousemove', onPointerMove);
    canvas.addEventListener('mouseup', onPointerUp);
    canvas.addEventListener('mouseleave', onPointerUp); // Treat mouse leave as pointer up
    canvas.addEventListener('wheel', onWheel, { passive: false });

    // Touch events
    canvas.addEventListener('touchstart', onPointerDown, { passive: false });
    canvas.addEventListener('touchmove', onPointerMove, { passive: false });
    canvas.addEventListener('touchend', onPointerUp, { passive: false });

    // Public API
    return {
        /**
         * Get current transform state
         */
        getTransform() {
            return { ...transform };
        },

        /**
         * Set transform state
         */
        setTransform(newTransform) {
            transform = { ...newTransform };
            onUpdate({ ...transform });
        },

        /**
         * Reset transform to default
         */
        reset() {
            transform = { scale: 1.0, dx: 0, dy: 0 };
            onUpdate({ ...transform });
        },

        /**
         * Get current FSM state (for debugging)
         */
        getState() {
            return currentState;
        },

        /**
         * Clean up event listeners
         */
        destroy() {
            canvas.removeEventListener('mousedown', onPointerDown);
            canvas.removeEventListener('mousemove', onPointerMove);
            canvas.removeEventListener('mouseup', onPointerUp);
            canvas.removeEventListener('mouseleave', onPointerUp);
            canvas.removeEventListener('wheel', onWheel);
            canvas.removeEventListener('touchstart', onPointerDown);
            canvas.removeEventListener('touchmove', onPointerMove);
            canvas.removeEventListener('touchend', onPointerUp);
        }
    };
}

// For non-module environments
if (typeof window !== 'undefined') {
    window.createNavigator = createNavigator;
}

