/**
 * Create and show a modal dialog purely in JS.
 * @param {'info'|'error'} type
 * @param {string} message
 */
function showDialog(type, message, onOk, onCancel) {
  // 1) Overlay
  const overlay = document.createElement('div');
  Object.assign(overlay.style, {
    position: 'fixed',
    top: 0, left: 0, right: 0, bottom: 0,
    background: 'rgba(0,0,0,0.5)',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    zIndex: 10000,
  });

  // 2) Dialog box
  const box = document.createElement('div');
  Object.assign(box.style, {
    background:  type === 'error' ? '#ffe6e6' : '#f0f8ff',
    color:       type === 'error' ? '#900'   : '#000',
    padding:     '20px',
    borderRadius:'8px',
    minWidth:    '280px',
    boxShadow:   '0 4px 12px rgba(0,0,0,0.3)',
    textAlign:   'center',
  });

  // 3) Message
  const txt = document.createElement('p');
  txt.textContent = message;
  txt.style.margin = '0 0 16px';
  box.appendChild(txt);

  // 4) OK button
  const btn = document.createElement('button');
  btn.textContent = 'OK';
  Object.assign(btn.style, {
    padding:     '8px 16px',
    border:      'none',
    borderRadius:'4px',
    cursor:      'pointer',
    background:  type === 'error' ? '#d9534f' : '#007bff',
    color:       '#fff',
  });
  btn.addEventListener('click', () => {
    document.body.removeChild(overlay);
    onOk?.();
  });
  box.appendChild(btn);

  const cancelBtn = document.createElement('button');
  cancelBtn.textContent = 'Cancel';
  Object.assign(cancelBtn.style, {
    padding:     '8px 16px',
    border:      'none',
    borderRadius:'4px',
    cursor:      'pointer',
    background:  '#6c757d',
    color:       '#fff',
    marginLeft:  '8px'
  });
  cancelBtn.addEventListener('click', () => {
    document.body.removeChild(overlay);
    onCancel?.();
  });
  box.appendChild(cancelBtn);

  // 5) Attach
  overlay.appendChild(box);
  document.body.appendChild(overlay);
}

// Convenience wrappers
export function showInfo(message, onOk, onCancel)  { showDialog('info',  message, onOk, onCancel); }
export function showError(message, onOk, onCancel) { showDialog('error', message, onOk, onCancel); }