import { MapModel, SelectionMode } from "./models/models.js";
import { refreshViewport } from "./draw.js";
import { setLocationId, setSelectionMode, setTreeSelection, treeSelection, setIsDirty } from "./selection.js";

export let map;
export let styles;
export let isGrid;
export let isSnap;
export let isPreview;
export let isDebug;


export function initMap(mapid) {
    // 1) Build an empty local map
    map = new MapModel();
    map.id = mapid;

    // 2) Auto-select the first group/layer if available
    autoSelection();

    // 3) (Optional) trigger a draw if needed
    refreshViewport();
}


export function switchGridVisibility() {
    isGrid = !isGrid;
}

export function switchPreviewVisibility() {
    isPreview = !isPreview;
}
export function switchDebugVisibility() {
    isDebug = !isDebug;
}

export function switchSnapVisibility() {
    isSnap = !isSnap;
}


export function setMap(v) {
    map = new MapModel(v);
    console.log("MapModel :", map);
  //  window.mapboot.mapid = map.id;
    const badgeSpan = document.querySelector('#locationBadge span');
    badgeSpan.textContent = map.name;
    autoSelection();

    // refreshViewport();              // if you want an immediate redraw
}


export function setGridSize(v) {
    map.settings.gridSize = v;
    // refreshViewport();              // if you want an immediate redraw
}

export function setUnit(v) {
    map.settings.unit = v;
    // refreshViewport();              // if you want an immediate redraw
}


export function setRotationSnap(v) {
    map.settings.rotationSnap = v;
    // refreshViewport();              // if you want an immediate redraw
}

export function setSelectionRadius(v) {
    map.settings.selectionRadius = v;
}


function autoSelection() {

    setSelectionMode(SelectionMode.POINT);
    isGrid = true;
    isSnap = true;
    isPreview = false;

    // Clear everything first
    // setGroupId(null);
    //  setLayerId(null);
    setLocationId(null);

    treeSelection.length = 0;

    if (Object.keys(map.groups).length === 0) return;             // group but no layers
    const firstGroupId = Object.keys(map.groups)[0];
    //  setGroupId(firstGroupId);
    const group = map.groups[firstGroupId];
    if (!group) return;

    if (Object.keys(group.layers).length === 0) return;             // group but no layers
    const firstLayerId = Object.keys(group.layers)[0];
    //  setLayerId(firstLayerId);
    //  const layer = group.layers[firstLayerId];

    setIsDirty(false);
    setTreeSelection(firstGroupId, firstLayerId);
}