
 
/* ---------- public init: builds a widget on the given host ---------------- */
export function init(options = {}) {
  const cfg = Object.assign(
    { },
    options
  );

  // expose basic config on window for other modules that rely on it
  window.mapboot = window.mapboot || {};
  window.mapboot.cfg = { target: cfg.target};

  /* ---------- helper: read ?mapid= from the current URL -------------------- */
  //window.mapboot.mapid = cfg.mapid; // new URLSearchParams(window.location.search).get('mapid');;
 
  const host =
    typeof cfg.target === "string"
      ? document.querySelector(cfg.target)
      : cfg.target;

  if (!host) {
    throw new Error("IndoorLens: target element not found");
  }

  import("./widget.js").then(({ widget }) => {
    // do this:

    
  //  window.mapboot = window.mapboot || {};
    window.mapboot = new widget(host, cfg);
  });
}
/* make the API available to non‑module scripts*/
if (typeof window !== "undefined") {
  window.mapboot = window.mapboot || {};
  window.mapboot.init = init;   // ← attaches the function
} 