import {  UnitType } from "../../shared/models.js";
import { map, setGridSize, setUnit, setRotationSnap, setSelectionRadius } from "../editor.js";
import { refreshViewport } from "../draw.js";
import { BuildNavigationTree } from "./sideMenuUI.js";
import { FSM_STATES, setFSMState } from "./../editing/state.js";



export function initMapUI() {

  const mapName = document.getElementById('mapName');
  mapName.style.marginBottom = '8px';
  mapName.addEventListener("input", (evt) => {
    if (!map) return;
    map.name = evt.target.value;
    const badgeSpan = document.querySelector('#locationBadge span');
    badgeSpan.textContent = map.name;
    BuildNavigationTree();
    updateMapUI();
  });

  const mapMetaInput = document.getElementById('mapMetaInput');
  mapMetaInput.style.marginBottom = '8px';
  mapMetaInput.addEventListener("input", (evt) => {
    if (!map) return;
    map.meta = evt.target.value;
    updateMapUI();
  });




  // 2) Grab the <select>
  const unitSelect = document.getElementById('unitSelect');
  unitSelect.style.marginBottom = '8px';

  // 3) Populate with options
  Object.entries(UnitType).forEach(([label, value]) => {
    const option = document.createElement('option');
    option.value = value;
    option.textContent = label;
    unitSelect.appendChild(option);
  });

  // 4) Pre-select from your settings
  unitSelect.value = map.settings.unit || UnitType.Meters;
  unitSelect.addEventListener('change', evt => {
    unitSelect.value = evt.target.value;
    map.referenceUnit = evt.target.value;
    setUnit(evt.target.value);
    refreshViewport();
  });


  const gridSizeInput = document.getElementById('gridSizeInput');
  gridSizeInput.style.marginBottom = '8px';
  gridSizeInput.addEventListener("input", (evt) => {
    gridSizeInput.value = evt.target.value;
    setGridSize(evt.target.value);
    refreshViewport();
  });

  const rotationSnapInput = document.getElementById('rotationSnapInput');
  rotationSnapInput.style.marginBottom = '8px';
  rotationSnapInput.addEventListener("input", (evt) => {
    rotationSnapInput.value = evt.target.value;
    setRotationSnap(evt.target.value);
  });

  const selectionRadiusInput = document.getElementById('selectionRadiusInput');
  selectionRadiusInput.style.marginBottom = '8px';
  selectionRadiusInput.addEventListener("input", (evt) => {
    selectionRadiusInput.value = evt.target.value;
    setSelectionRadius(evt.target.value);
  });

  initAllowedDomainsUI();
  setFSMState(FSM_STATES.IDLE);
}

export function updateMapUI() {
  if (!map) return;
  const mapName = document.getElementById('mapName');
  mapName.value = map.name;

  const mapMetaInput = document.getElementById('mapMetaInput');
  mapMetaInput.value = map.meta;

  const unitSelect = document.getElementById('unitSelect');
  const gridSizeInput = document.getElementById('gridSizeInput');
  const rotationSnapInput = document.getElementById('rotationSnapInput');
  const selectionRadiusInput = document.getElementById('selectionRadiusInput');
  selectionRadiusInput.value = map.settings.selectionRadius;
  unitSelect.value = map.settings.unit;
  gridSizeInput.value = map.settings.gridSize;
  rotationSnapInput.value = map.settings.rotationSnap;
  renderAllowedDomains();
}


function initAllowedDomainsUI() {
  // Ensure the map.allowedDomains array exists
  if (!map.settings.allowedDomains) map.settings.allowedDomains = [];

  const container = document.getElementById('allowed-domains-group');
  // Clear any existing nodes
  container.innerHTML = '';

  // Create label + Add button
  const label = document.createElement('label');
  label.textContent = 'Allowed Domains:';
 label.className = 'secondary-title-label';
  label.style.marginRight = '8px';

  const addBtn = document.createElement('button');
  addBtn.type = 'button';


  addBtn.style.backgroundImage = `url("/Images/Add.png")`;
  addBtn.style.backgroundSize = "contain";
  addBtn.style.backgroundRepeat = "no-repeat";
  addBtn.style.backgroundPosition = "center";
  // Icon size
  addBtn.style.backgroundSize = "14px 14px";
  // Button dimensions
  addBtn.style.width = "20px";
  addBtn.style.height = "20px";

  addBtn.className = 'toolbar-btn';
  addBtn.addEventListener('click', () => {
    map.settings.allowedDomains.push('');
    renderAllowedDomains();
  });

  const header = document.createElement('div');
  header.style.display = 'flex';
  header.style.alignItems = 'center';
  header.style.justifyContent = 'space-between';  // push Add button to the right
  header.appendChild(label);
  header.appendChild(addBtn);
  container.appendChild(header);

  // Create list area
  const list = document.createElement('div');
  list.id = 'allowed-domains-list';
  list.style.marginTop = '8px';
  container.appendChild(list);

  // Initial render
  renderAllowedDomains();
}

function renderAllowedDomains() {
  const list = document.getElementById('allowed-domains-list');
  list.innerHTML = '';
  map.settings.allowedDomains.forEach((domain, idx) => {
    const row = document.createElement('div');
    row.style.display = 'flex';
    row.style.alignItems = 'center';
    row.style.marginBottom = '4px';

    const input = document.createElement('input');
    input.type = 'text';
    input.value = domain;
    input.placeholder = "example.com";
    input.className = 'form-control form-control-sm';
    input.style.flex = '1';
    input.style.marginRight = '2px';
    input.addEventListener('input', () => {
      map.settings.allowedDomains[idx] = input.value.trim();
    });

    const delBtn = document.createElement('button');
    delBtn.type = 'button';

    delBtn.style.backgroundImage = `url("/Images/Delete.png")`;
    delBtn.style.backgroundSize = "contain";
    delBtn.style.backgroundRepeat = "no-repeat";
    delBtn.style.backgroundPosition = "center";
    // Icon size
    delBtn.style.backgroundSize = "14px 14px";
    // Button dimensions
    delBtn.style.width = "20px";
    delBtn.style.height = "20px";
    delBtn.className = 'toolbar-btn ms-2';
    delBtn.addEventListener('click', () => {
      map.settings.allowedDomains.splice(idx, 1);
      renderAllowedDomains();
    });

    row.appendChild(input);
    row.appendChild(delBtn);
    list.appendChild(row);
  });
}








