import {   LineModel, PolygonModel } from '../models/models.js';   // assumes PointModel is already imported somewhere
import { map } from '../editor.js'; // or wherever your LayoutModel instance lives
import { lineIds, pointIds, polygonIds, setPolygonId, setPointId, treeSelection } from '../selection.js'; // if you also want to auto-select
import { buildMap } from "../draw.js";
import { FSM_STATES, setFSMState } from './../editing/state.js';



/**
 * Create a command that:
 *  1. Deletes all currently selected points.
 *  2. Automatically detects any line that becomes orphaned (<=1 point) and deletes it too.
 * On undo(), restores every line (with its point‐list) and every point exactly as they were.
 */
export function deleteCommand() {

    let affectedBranches = {};
    let affectedPolygons = {};
    let affectedLocs = {};


    if (!treeSelection || treeSelection.length === 0) return;
    const sel = treeSelection.at(-1);

    const group = map.groups[sel.groupId];
    if (!group) return;

    const layer = group.layers[sel.layerId];
    if (!layer) return;

    const cacheGroupId = sel.groupId;
    const cacheLayerId = sel.layerId;

    lineIds.forEach(gId => {
        const branchToDelete = layer.lines[gId];
        if (branchToDelete) {
            // Clone the line so we can restore it later:
            const savedBranch = new LineModel({
                styleId: branchToDelete.styleId,
                pointIds: [...branchToDelete.pointIds]
            });
            affectedBranches[gId] = savedBranch;

            branchToDelete.pointIds.forEach(pointId => {
               const point = map.points[pointId];
              if (point.associatedLines.length <= 1) setPointId(pointId);
            })
            // points
            // branchToDelete.pointIds.map(pointId => setPointId(pointId));// add the affected points
        }
    });


 //   // Build a Set once – O(m) where m = pointIds.length
 //   const deletedIdSet = new Set(pointIds);
 //   // delete the affected polygons as well
 //   Object.entries(layer.polygons).forEach(([polygonId, polygon]) => {
 //       // does this polygon reuse ANY of the deleted IDs?
 //       if (polygon.pointIds?.some(id => deletedIdSet.has(id))) {
 //           setPolygonId(polygonId);
 //       }
 //   });

    polygonIds.forEach(polygonId => {
        const affectedPolygon = layer.polygons[polygonId];
        if (affectedPolygon) {
            // Clone the polygon so we can restore it later:
            const savedPolygon = new PolygonModel({
                styleId: affectedPolygon.styleId,
                pointIds: [...affectedPolygon.pointIds]
            });
            affectedPolygons[polygonId] = savedPolygon;
        }
    });


    pointIds.forEach(pointId => {
        const point = map.points[pointId];
        if (!point) return;
        point.associatedLines.forEach((gId, i) => {
            const affectedBranch = layer.lines[gId];
            if (affectedBranch) {
                // Clone the line so we can restore it later:
                const savedBranch = new LineModel({
                    styleId: affectedBranch.styleId,
                    pointIds: [...affectedBranch.pointIds]
                });

                affectedBranches[gId] = savedBranch;
            }
        });


    });
    return {
        do() {
            if (!treeSelection || treeSelection.length === 0) return;
            const sel = treeSelection.at(-1);

            const group = map.groups[sel.groupId];
            if (!group) return;

            const layer = group.layers[sel.layerId];
            if (!layer) return;

            // Reme each point from every recorded line
            pointIds.forEach(pointId => {
                const point = map.points[pointId];

                Object.entries(map.locations).forEach(([locationId, location]) => {
                    if (location.pointId && location.pointId === pointId) {
                        affectedLocs[locationId] = location;   // shallow clone is enough
                        const lIdIndex = layer.locationIds.indexOf(locationId);
                        if (lIdIndex !== -1) layer.locationIds.splice(lIdIndex, 1);
                        delete map.locations[locationId];
                    }
                })


                point.associatedLines.forEach((lId, i) => {
                    const line = layer.lines[lId];
                    if (!line) return;
                    const pointIndex = line.pointIds.indexOf(pointId);
                    if (pointIndex !== -1) line.pointIds.splice(pointIndex, 1);
                });
            });


            // Clear the point selection
            pointIds.length = 0

            lineIds.forEach(id => {
                delete layer.lines[id];
            });
            // Clear the line selection
            lineIds.length = 0

            //clean ploygons selection
            polygonIds.forEach(polygonId => {
                delete layer.polygons[polygonId];
            });
            polygonIds.length = 0;

            // Clear the global selection so no points remain selected
            setFSMState(FSM_STATES.IDLE);
            buildMap();

        },

        undo() {
            const group = map.groups[cacheGroupId];
            if (!group) return;
            const layer = group.layers[cacheLayerId];
            if (!layer) return;

            Object.entries(affectedBranches).forEach(([gId, restoredBranch]) => {
                layer.lines[gId] = restoredBranch;
            });

            Object.entries(affectedPolygons).forEach(([pId, restoredPolygon]) => {
                layer.polygons[pId] = restoredPolygon;
            });

            Object.entries(affectedLocs).forEach(([lId, restoredLocation]) => {
                map.locations[lId] = restoredLocation;
                layer.locationIds.push(lId);
            });

            setFSMState(FSM_STATES.IDLE);
            buildMap();

        },

        redo() {
            this.do();
        }
    };
}


