
import { map } from '../editor.js'; // or wherever your LayoutModel instance lives
import { lineIds, pointIds, setPointId, setLineId, clearSelectionUndoRedo, setPolygonId, polygonIds, treeSelection } from '../selection.js'; // if you  

/**
 * Build a command that restores selection to `afterGuids` on do(),
 * and back to `beforeGuids` on undo().
 *
 * @param {string[]} beforeGuids  – array of point GUIDs that were selected originally
 * @param {string[]} afterGuids   – array of point GUIDs after the click/drag
 */
export function selectCommand(beforePointIds, afterPointIds, beforeLineIds, afterLineIds, beforePolygonIds, afterPolygonIds) { // setPolygonId(polygonId);
    return {
        do() {
            // Clear everything, then re-select exactly the GUIDs in afterGuids
            clearSelectionUndoRedo();

            if (!treeSelection || treeSelection.length === 0) return;
            const sel = treeSelection.at(-1);

            const group = map.groups[sel.groupId];
            if (!group) return;

            const layer = group.layers[sel.layerId];
            if (!layer) return;


            afterPointIds?.forEach(pointId => {
                setPointId(pointId);
            });
            if (afterPointIds?.length == 0) pointIds.length = 0;

            afterLineIds?.forEach(gId => {
                setLineId(gId);
            });
            if (afterLineIds?.length == 0) lineIds.length = 0;

            afterPolygonIds?.forEach(pId => {
                setPolygonId(pId);
            });
            if (afterPolygonIds?.length == 0) polygonIds.length = 0;

        },
        undo() {
            // Clear and then re-select exactly the GUIDs in beforeGuids
            clearSelectionUndoRedo();

            if (!treeSelection || treeSelection.length === 0) return;
            const sel = treeSelection.at(-1);

            const group = map.groups[sel.groupId];
            if (!group) return;

            const layer = group.layers[sel.layerId];
            if (!layer) return;

            beforePointIds?.forEach(pointId => {
                setPointId(pointId);
            });
            if (beforePointIds?.length == 0) pointIds.length = 0;

            beforeLineIds?.forEach(gId => {
                setLineId(gId);
            });
            if (beforeLineIds?.length == 0) lineIds.length = 0;

            beforePolygonIds?.forEach(pId => {
                setLineId(pId);
            });
            if (beforePolygonIds?.length == 0) polygonIds.length = 0;

        },
        redo() {
            this.do();
        }
    };
}
