import { map } from "../editor.js";
import {
  Vec3, StyleModel, LocationType, UnitType, DisplayType,
  floorColor, publicColor, privateColor, greenColor, sandColor, waterColor, innerWallColor, outerWallColor, GroupType
} from "../../shared/models.js";

export const enabledColor = 'rgb(117, 206, 146)';
export const disabledColor = 'rgb(219, 118, 118)';

export const SelectionMode = Object.freeze({
  POINT: 'point',
  LINE: 'line',
  POLYGON: 'polygon'
});




export class PointModel {
  constructor(src = {}) {
    if (src && typeof src === "object" && ("position" in src)) {
      // JSON source path
      this.position = new Vec3(src.position);
      this.groupId = src.groupId;
      this.layerId = src.layerId;
      this.locationId = src.locationId;
      if (src.twins) this.twins = [...src.twins];
    } else {
      // Raw position path
      this.position = new Vec3();
      this.groupId = '';
      this.layerId = '';
    }
  }
  toJSON() {
    return {
      position: this.position,
      groupId: this.groupId,
      layerId: this.layerId,
      locationId: this.locationId,
      twins: this.twins
    }
  }
}


export class LineModel {
  constructor(src = {}) {
    if (src && typeof src === "object" && ("pointIds" in src || "styleId" in src)) {
      // ─ Hydrate from JSON ─
      this.styleId = src.styleId ?? "none";
      // this.points = (src.points ?? []).map(n => new PointModel(n));

      this.pointIds = [...(src.pointIds ?? [])];
    } else {
      // ─ New line ─
      this.styleId = "none";
      // this.points = [];
      this.pointIds = [];
    }
  }

  toJSON() {
    return {
      styleId: this.styleId,
      pointIds: this.pointIds
    };
  }
}


/* 3. layerModel (owns a list of styles) */
export class LayerModel {
  /**
   * @param {object|string} srcOrName
   *    - If it’s an object, treat it as JSON (e.g., from storage).
   *    - If it’s a string, treat it as the name of a new  layer.
   */
  constructor(srcOrName = {}) {
    if (typeof srcOrName === "object" && (srcOrName.styles || typeof srcOrName.name === "string")) {
      const src = /** @type {{ id?: string; name?: string; enabled?: boolean; layerOpacity?: number; backgroundOpacity?: number;styles?: object; locations?: object }} */ (srcOrName);

      this.name = src.name;
      this.enabled = src.enabled !== false;
      this.backgroundOpacity = src.backgroundOpacity ?? 1;
      // this.layerOpacity = src.layerOpacity ?? 1;
      this.backgroundDataURI = src.backgroundDataURI || "";
      this.backgroundWorldWidth = src.backgroundWorldWidth ?? 100;
      this.backgroundWorldHeight = src.backgroundWorldHeight ?? 100;

      this.locationIds = [...srcOrName.locationIds];
      this.polygons = {};
      if (src.polygons && typeof src.polygons === 'object') {
        Object.entries(src.polygons).forEach(([rid, rData]) => {
          this.polygons[rid] = new PolygonModel(rData);
        });
      }



      this.lines = {};
      // ─── Branches ────────────────────────────────────────────
      if (src && typeof src.lines === 'object' && !Array.isArray(src.lines)) {
        this.lines = Object.entries(src.lines).reduce((dict, [key, bData]) => {
          const line = new LineModel(bData);
          dict[key] = line;
          return dict;
        }, {});
      }
      this.lineLayers = {};
      this.polygonsByStyle = {};

    } else {
      // New  layer from name string

      this.name = "layer";
      this.enabled = true;
      this.backgroundOpacity = 1;
      this.backgroundDataURI = "";
      this.backgroundWorldWidth = 100;
      this.backgroundWorldHeight = 100;

      this.polygons = {};
      this.lines = {};
      this.locationIds = [];

      this.lineLayers = {};
      this.polygonsByStyle = {};

    }
  }

  toJSON() {
    return {
      name: this.name,
      enabled: this.enabled,
      opacity: this.opacity,
      lines: this.lines,
      polygons: Object.fromEntries(
        Object.entries(this.polygons).map(([id, polygon]) => [id, polygon.toJSON?.() || polygon])
      ),
      locationIds: this.locationIds
    };
  }
}


export class GroupModel {
  /**
  * @param {object} [src] – optional JSON from Unity.
  *   If provided, can have `.id`, `.name`, `.layout`, `.path`, 
  *   `.lines`, `. layers`, `.locations`, and `.styles`.
  */
  constructor(src = {}) {
    this.name = src.name || "";
    this.meta = src.meta || "";
    this.type = src.type || GroupType.REGULAR;
    this.enabled = src.enabled || true;

    if (src && typeof src.layers === 'object' && !Array.isArray(src.layers)) {
      this.layers = Object.entries(src.layers).reduce((dict, [id, gData]) => {
        const layer = new LayerModel(gData);
        dict[id] = layer;
        return dict;
      }, {});
    } else {
      this.name = "Group";
      this.type = GroupType.REGULAR;
      this.layers = {};
      const layer = new LayerModel({});
      // layer.name = "";
      const initlayerId = generateShortGuid();
      this.layers[initlayerId] = layer;
    }
  }
}


export class MapModel {
  /**
   * @param {object} [src] – optional JSON from Unity.
   *   If provided, can have `.id`, `.name`, `.layout`, `.path`, 
   *   `.lines`, `. layers`, `.locations`, and `.styles`.
   */
  constructor(src = {}) {
    this.id = src.id || generateShortGuid();
    this.name = src.name || "My Map";
    this.meta = src.meta || "";
    this.owner = src.owner || "";
    this.settings = src.settings || new MapSettingsModel();
    this.groups = {};


    // ─── Group ─────────────────────────────────────────────────
    if (src && typeof src.groups === 'object' && !Array.isArray(src.groups)) {
      this.groups = Object.entries(src.groups).reduce((dict, [id, bData]) => {
        const group = new GroupModel(bData);
        dict[id] = group;
        return dict;
      }, {});
    } else {
      const initGroupId = generateShortGuid();
      this.groups[initGroupId] = new GroupModel();
    }

    this.points = {};
    if (src.points && typeof src.points === "object" && !Array.isArray(src.points)) {
      Object.entries(src.points).forEach(([id, data]) => {
        this.points[id] = new PointModel(data);
      });
    }

    this.locations = {};
    if (src.locations && typeof src.locations === "object" && !Array.isArray(src.locations)) {
      Object.entries(src.locations).forEach(([id, data]) => {
        this.locations[id] = new LocationModel(data);
      });
    }

    this.styles = {};
    const defaults = [
      ['none', null, 0],
      ['floor', floorColor, 0],
      ['public', publicColor, 0],
      ['private', privateColor, 0],
      ['innerwall', innerWallColor, 3],
      ['outerwall', outerWallColor, 4],
    ];




    defaults.forEach(([id, color, lineThickness]) => {
      const style = new StyleModel(id, color, lineThickness);
      style.enabled = true;
      this.styles[id] = style;
    });
  }

  toJSON() {
    return {
      id: this.id,
      name: this.name,
      meta: this.meta,
      groups: this.groups,
      points: this.points,
      locations: this.locations,
      settings: this.settings
    }
  }
}


export class MapSettingsModel {
  constructor(src = {}) {
    /* raw JSON has lines:[...] at the root */
    this.unit = src.unit ?? UnitType.Meters;
    this.gridSize = src.gridSize ?? 1;
    //   this.doorWidth = src.doorWidth ?? 1;
    this.rotationSnap = src.rotationSnap ?? 5;
    this.selectionRadius = src.selectionRadius ?? 15;
    this.allowedDomains = src.allowedDomains || [];
  }
  toJSON() {
    return {
      unit: this.unit,
      gridSize: this.gridSize,
      //   doorWidth: this.doorWidth,
      rotationSnap: this.rotationSnap,
      selectionRadius: this.selectionRadius,
      allowedDomains: this.allowedDomains
    };
  }
}

export class LocationModel {
  constructor(src = {}) {
    /* raw JSON has lines:[...] at the root */
    if (src) {
      //  this.id = src.id ?? '';
      this.name = src.name ?? '';
      this.meta = src.meta ?? '';
      this.locationType = src.locationType ?? LocationType.Custom;
      this.displayType = src.displayType ?? DisplayType.None;
      this.polygonIds = [...(src.polygonIds ?? [])];
      this.accessPointIds = [...(src.accessPointIds ?? [])];
      this.groupId = src.groupId;
      this.layerId = src.layerId;
      this.pointId = src.pointId;
      this.iconUrl = src.iconUrl;
    } else {
      this.locationType = LocationType.Custom;
      this.displayType = DisplayType.None;
      this.polygonIds = [];
      this.accessPointIds = [];
      this.groupId = '';
      this.layerId = '';
    }
  }
  toJSON() {
    return {
      name: this.name,
      meta: this.meta,
      locationType: this.locationType,
      displayType: this.displayType,
      polygonIds: this.polygonIds,
      accessPointIds: this.accessPointIds,
      groupId: this.groupId,
      layerId: this.layerId,
      pointId: this.pointId,
    };
  }
}


export class PolygonModel {
  /**
   * @param {object|Array} srcOrNodeIds
   * @param {string} [styleId="floor"]   Ignored when first param is an object
   */
  constructor(srcOrNodeIds = [], styleId = "public") {
    if (
      srcOrNodeIds &&
      typeof srcOrNodeIds === "object" &&
      Array.isArray(srcOrNodeIds.pointIds)
    ) {
      this.pointIds = srcOrNodeIds.pointIds ?? [];         // Array<PointModel|raw>
      this.styleId = srcOrNodeIds.styleId;

    } else {
      this.pointIds = Array.isArray(srcOrNodeIds) ? srcOrNodeIds : [];
      this.styleId = styleId;
    }
  }
  toJSON() {
    return {
      pointIds: this.pointIds,
      styleId: this.styleId,
    };
  }


  /** Signed area in world‑units² (positive = CCW, negative = CW). */
  get area() {
    // Resolve GUIDs → PointModel, skip any missing vertices
    const points = this.pointIds
      .map(id => map.points[id])
      .filter(Boolean);

    const n = points.length;
    if (n < 3) return 0;                    // not a polygon

    let a = 0;
    for (let i = 0; i < n; i++) {
      const p = this._pos(points[i]);
      const q = this._pos(points[(i + 1) % n]);
      a += p.x * q.z - q.x * p.z;
    }
    return a / 2;
  }

  /** Centroid of the polygon (≈ geometric center). */
  get centroid() {
    const points = this.pointIds
      .map(id => map.points[id])
      .filter(Boolean);

    const n = points.length;
    if (n === 0) return { x: 0, z: 0 };

    let cx = 0, cz = 0;
    points.forEach(pt => {
      const p = this._pos(pt);
      cx += p.x;
      cz += p.z;
    });
    return { x: cx / n, z: cz / n };
  }

  /** Convenience: returns [`guid1`, `guid2`, …] (sorted). */
  get fingerprint() {
    // Return sorted list of valid pointIds (no longer relying on PointModel.guid)
    return this.pointIds
      .filter(id => map.points[id])
      .slice()
      .sort();
  }

  /** Toggle visibility. */
  toggle() { this.enabled = !this.enabled; }



  // Internal helper to coerce raw or PointModel to {x,z}
  _pos(point) {
    return { x: point.position.x, z: point.position.z };
  }
}

export function generateShortGuid() {
  const bytes = crypto.getRandomValues(new Uint8Array(4));
  return Array.from(bytes)
    .map(b => b.toString(16).padStart(2, "0"))
    .join("");
}
