
import { refreshViewport, buildMap } from "../draw.js";
import { setIsDirty } from "../selection.js";

const undoStack = [];

export function commit(cmd) {
    console.log("action commited");
    cmd.do();
    undoStack.push(cmd);
    if (undoStack.length > 30) undoStack.shift();  // optional cap
    setIsDirty(true);
    buildMap();
    refreshViewport();
}

function undo() {
    const c = undoStack.pop();
    if (c) {
        c.undo();
        refreshViewport();
    }
}

window.addEventListener('keydown', function (e) {
    const isUndo = (e.ctrlKey || e.metaKey) && !e.shiftKey && e.key.toLowerCase() === 'z';
    if (isUndo) {
        e.preventDefault();
        undo();
    }
});

