import { LocationType, DisplayType } from "../../shared/models.js";
import { map } from "../editor.js";
import { canvasMain, refreshViewport } from "../draw.js";
import { locationId, polygonIds, setPolygonId, treeSelection, setPointId, pointIds } from "../selection.js";
import { BuildNavigationTree } from "./sideMenuUI.js";
import { FSM_STATES, setFSMState } from "./../editing/state.js";





export function initLocationsUI() {

    // helper to build a label+field wrapper
    function makeFieldGroup(labelText, fieldEl) {
        const fg = document.createElement('div');
        fg.className = 'field-group';
        fg.style.marginBottom = '8px';

        const label = document.createElement('label');
        label.textContent = labelText;
        label.htmlFor = fieldEl.id;
        fg.appendChild(label);

        fg.appendChild(fieldEl);
        return fg;
    }


    const container = document.getElementById('locationFieldsContainer');
    container.innerHTML = ''; // clear old

    // 1) Name
    const nameInput = document.createElement('input');
    nameInput.type = 'text';
    nameInput.id = 'locationName';
    nameInput.value = '';
    nameInput.addEventListener('input', e => {
        const location = getCurrentLocation();
        if (!location) return null;
        location.name = e.target.value;
        BuildNavigationTree(); // update the navigation tree
    });
    container.appendChild(makeFieldGroup('Name:', nameInput));

    // 2) Meta Data
    // 2) Meta Data — now a textarea with min 3 lines
    const metaInput = document.createElement('textarea');
    metaInput.id = 'locationMeta';
    metaInput.rows = 3;                             // minimum of 3 lines
    metaInput.style.resize = 'vertical';                   // allow vertical resizing only
    metaInput.value = '';
    metaInput.placeholder = 'e.g. Layer 2, East Wing';
    metaInput.addEventListener('input', e => {
        const location = getCurrentLocation();
        if (!location) return;
        location.meta = e.target.value;
    });
    container.appendChild(makeFieldGroup('Meta Data:', metaInput));

    // 4) Location Type select
    const typeSelect = document.createElement('select');
    typeSelect.id = 'locationType';



    Object.entries(LocationType).forEach(([lbl, val]) => {
        const opt = document.createElement('option');
        opt.value = val;
        opt.textContent = lbl;
        typeSelect.appendChild(opt);
    });
    typeSelect.value = LocationType.Custom;
    typeSelect.addEventListener('change', e => {
        const location = getCurrentLocation();
        if (!location) return null;
        location.locationType = e.target.value;
    });
    container.appendChild(makeFieldGroup('Location Type:', typeSelect));

    // 3) Display Type select
    const dispSelect = document.createElement('select');
    dispSelect.id = 'displayType';

    Object.entries(DisplayType).forEach(([lbl, val]) => {
        const opt = document.createElement('option');
        opt.value = val;
        opt.textContent = lbl;
        dispSelect.appendChild(opt);
    });
    dispSelect.value = DisplayType.None;
    dispSelect.addEventListener('change', e => {
        const location = getCurrentLocation();
        if (!location) return null;
        location.displayType = e.target.value;
    });
    container.appendChild(makeFieldGroup('Display Type:', dispSelect));

    // Surfaces header with title and image-picker button
    const surfacesHeader = document.createElement('div');
    surfacesHeader.className = 'field-group';
    // Ensure grid layout for surfacesHeader
    surfacesHeader.style.display = 'grid';
    surfacesHeader.style.gridTemplateColumns = 'auto 1fr';
    // Label for the polygons section
    const surfacesTitle = document.createElement('label');
    surfacesTitle.textContent = 'Surfaces';
    surfacesTitle.style.marginRight = 'auto';
    surfacesHeader.appendChild(surfacesTitle);
    // Square "Pick Image" button
    const pickSurfaceBtn = document.createElement('button');
    pickSurfaceBtn.type = 'button';
    // pickSurfaceBtn.type = 'surfacePicker';
    pickSurfaceBtn.className = "btn-img";
    pickSurfaceBtn.style.backgroundImage = 'url(/Images/Pick.png)';
    pickSurfaceBtn.style.backgroundRepeat = "no-repeat";
    pickSurfaceBtn.style.backgroundPosition = "center";
    pickSurfaceBtn.style.backgroundSize = "16px 16px";
    pickSurfaceBtn.style.justifySelf = 'end';

    pickSurfaceBtn.onclick = (e) => {
        setFSMState(FSM_STATES.PICK_POLYGON);
        updateLocationsUI();
    }
    surfacesHeader.appendChild(pickSurfaceBtn);
    container.appendChild(surfacesHeader);

    // Container for polygon tags, left-aligned
    const surfacesTags = document.createElement('div');
    surfacesTags.id = 'surfacesTags';
    surfacesTags.className = 'tagContainer';
    surfacesTags.style.display = 'flex';
    surfacesTags.style.flexWrap = 'wrap';
    surfacesTags.style.justifyContent = 'flex-start';
    // renderTags(location.polygonIds, surfacesTags);
    container.appendChild(surfacesTags);



    // Surfaces header with title and image-picker button
    const accessPointsHeader = document.createElement('div');
    accessPointsHeader.className = 'field-group';
    // Ensure grid layout for surfacesHeader
    accessPointsHeader.style.display = 'grid';
    accessPointsHeader.style.gridTemplateColumns = 'auto 1fr';
    // Label for the polygons section
    const accessPointsTitle = document.createElement('label');
    accessPointsTitle.textContent = 'Access Points';
    accessPointsTitle.style.marginRight = 'auto';
    accessPointsHeader.appendChild(accessPointsTitle);


    // Square "Pick Image" button
    const pickAccessPointBtn = document.createElement('button');
    pickAccessPointBtn.type = 'button';
    // pickSurfaceBtn.type = 'surfacePicker';
    pickAccessPointBtn.className = "btn-img";
    pickAccessPointBtn.style.backgroundImage = 'url(/Images/Pick.png)';
    pickAccessPointBtn.style.backgroundRepeat = "no-repeat";
    pickAccessPointBtn.style.backgroundPosition = "center";
    pickAccessPointBtn.style.backgroundSize = "16px 16px";
    pickAccessPointBtn.style.justifySelf = 'end';

    pickAccessPointBtn.onclick = (e) => {
        setFSMState(FSM_STATES.PICK_POINT);
        updateLocationsUI();
    }
    accessPointsHeader.appendChild(pickAccessPointBtn);
    container.appendChild(accessPointsHeader);


    const accessPointsTags = document.createElement('div');
    accessPointsTags.id = 'accessPointsTags';
    accessPointsTags.className = 'tagContainer';
    accessPointsTags.style.display = 'flex';
    accessPointsTags.style.flexWrap = 'wrap';
    accessPointsTags.style.justifyContent = 'flex-start';
    // renderTags(location.polygonIds, surfacesTags);
    container.appendChild(accessPointsTags);
}

export function updateLocationsUI() {
    const location = getCurrentLocation();
    if (!location) return null;

    location.polygonIds.forEach(polygonId => setPolygonId(polygonId));
    refreshViewport();

    console.log("location.name" + location.name);
    // 1) Name
    const nameInput = document.getElementById('locationName');
    nameInput.value = location.name || '';

    // 2) Meta Data
    const metaInput = document.getElementById('locationMeta');
    metaInput.value = location.meta || '';

    // 3) Display Type select
    const dispSelect = document.getElementById('displayType');
    dispSelect.value = location.displayType || DisplayType.None;

    // 4) Location Type select
    const typeSelect = document.getElementById('locationType');
    typeSelect.value = location.locationType || LocationType.Custom;


    renderPolygonTags(location.polygonIds);
    renderAccessPointsTags(location.accessPointIds);
}

function getCurrentLocation() {


    console.log("locationId" + locationId);
    const location = map.locations[locationId];
    return location;
}



function renderPolygonTags(polyIds) {

    const surfacesTags = document.getElementById('surfacesTags');
    surfacesTags.innerHTML = "";
    polygonIds.length = 0; // clear previous selection
    polyIds.forEach((polygonId, i) => {
        setPolygonId(polygonId);

        const pillBtn = document.createElement("span");
        pillBtn.className = "tag-pill";
        // Color pill based on polygon validity
        //  if (validatePolygon(polygonId)) {
        //      pillBtn.style.backgroundColor = 'rgb(133, 204, 175)';
        //      // pillBtn.style.color = 
        //  } else {
        //   pillBtn.style.backgroundColor = 'rgb(217, 122, 122)';
        // pillBtn.style.color =;
        //  }
        pillBtn.textContent = polygonId.length > 3
            ? polygonId.slice(0, 2) + "..."
            : polygonId;
        pillBtn.addEventListener("click", () => {
            setPolygonId(polygonId);
            refreshViewport();
        });

        const delSurfaceBtn = document.createElement("button");
        delSurfaceBtn.type = "button";
        delSurfaceBtn.className = "close-btn";
        delSurfaceBtn.innerHTML = "&times;";
        delSurfaceBtn.addEventListener("click", () => {
            polyIds.splice(i, 1);   // actually remove the element
            renderPolygonTags(polyIds);     // re‑render tags
            refreshViewport();             // redraw without the removed surface
        });

        pillBtn.appendChild(delSurfaceBtn);
        surfacesTags.appendChild(pillBtn);
    });
}


export function renderAccessPointsTags(accesPointIds) {
    const accessPointsTags = document.getElementById('accessPointsTags');
    accessPointsTags.innerHTML = "";
    accesPointIds.forEach((pointId, i) => {
        const pillBtn = document.createElement("span");
        pillBtn.className = "tag-pill";
        pillBtn.textContent = pointId.length > 3
            ? pointId.slice(0, 2) + "..."
            : pointId;
        pillBtn.addEventListener("click", () => {
            pointIds.length = 0;
            setPointId(pointId);
            refreshViewport();
        });
        const delBtn = document.createElement("button");
        delBtn.type = "button";
        delBtn.className = "close-btn";
        delBtn.innerHTML = "&times;";
        delBtn.addEventListener("click", () => {
            //  map.points[pointId].accessPoint = false;
            accesPointIds.splice(i, 1);   // actually remove the element
            renderAccessPointsTags(accesPointIds);
            refreshViewport();
        });
        pillBtn.appendChild(delBtn);
        accessPointsTags.appendChild(pillBtn);
    });
}




 