// Dynamically load Highlight.js and its GitHub theme
(async function loadHighlightJS() {
  if (!window.hljs) {
    const link = document.createElement('link');
    link.rel = 'stylesheet';
    link.href = 'https://cdnjs.cloudflare.com/ajax/libs/highlight.js/11.8.0/styles/github.min.css';
    document.head.appendChild(link);

    await new Promise((resolve, reject) => {
      const script = document.createElement('script');
      script.src = 'https://cdnjs.cloudflare.com/ajax/libs/highlight.js/11.8.0/highlight.min.js';
      script.onload = resolve;
      script.onerror = reject;
      document.head.appendChild(script);
    });
  }
})();

// embedDialog.js
export function showEmbedDialog(mapid) {
  let overlay = document.getElementById('embedDialog');
  if (!overlay) {
    overlay = document.createElement('div');
    overlay.id = 'embedDialog';
    Object.assign(overlay.style, {
      display: 'none',
      position: 'fixed',
      inset: '0',                  // top/right/bottom/left = 0
      background: 'rgba(0,0,0,0.5)',
      alignItems: 'center',
      justifyContent: 'center',
      zIndex: '9999',
      display: 'flex'
    });

    const box = document.createElement('div');
    box.id = 'embedDialogContent';
    Object.assign(box.style, {
      background: '#fff',
      padding: '1.5rem',
      borderRadius: '0.5rem',
      maxWidth: '600px',
      width: '90%',
      maxHeight: '80%',
      overflowY: 'auto',
      boxShadow: '0 4px 12px rgba(0,0,0,0.15)'
    });

    box.innerHTML = `
      <h4 style="margin-top:0">Embed Code</h4>
      <pre>
        <code id="embedCode" class="language-html"></code>
      </pre>
      <div style="display:flex;justify-content:flex-end;gap:0.5rem;margin-top:1rem;">
        <button id="copyEmbedCode" style="
            padding:0.5rem 1rem;
            background:#0d6efd;
            color:#fff;
            border:none;
            border-radius:0.25rem;
            cursor:pointer;
          ">
          Copy Code
        </button>
        <button id="closeEmbedDialog" style="
            padding:0.5rem 1rem;
            background:#6c757d;
            color:#fff;
            border:none;
            border-radius:0.25rem;
            cursor:pointer;
          ">
          Close
        </button>
      </div>
    `;
    overlay.appendChild(box);
    document.body.appendChild(overlay);

    // Close handler
    box.querySelector('#closeEmbedDialog')
       .addEventListener('click', () => overlay.style.display = 'none');

    // Copy handler
    box.querySelector('#copyEmbedCode')
       .addEventListener('click', () => {
         const code = overlay.querySelector('#embedCode').textContent;
         navigator.clipboard.writeText(code).then(() => {
           const btn = overlay.querySelector('#copyEmbedCode');
           btn.textContent = 'Copied!';
           setTimeout(() => btn.textContent = 'Copy Code', 2000);
         });
       });
  }

  // Always update snippet and show dialog
  const snippet = `
<div id="mapbootviewer"></div>
<script type="module">
  import { init } from '/js/editor/mapboot_viewer.js';
  init({
    target: '#mapbootviewer',
    mapid: '${mapid}',
    type: 'viewer'
  });
</script>
`;
  const codeEl = overlay.querySelector('#embedCode');
  codeEl.textContent = snippet;
  if (window.hljs) {
    hljs.highlightElement(codeEl);
  }
  overlay.style.display = 'flex';

}