export function initToolsMenu() {



    const rotateBtn = document.getElementById("rotate");
    const scaleBtn = document.getElementById("scale");
    const centerBtn = document.getElementById("centereditor");
    const addBtn = document.getElementById("addpoint");
    const deleteBtn = document.getElementById("deletenode");
    const fullScreenBtn = document.getElementById("fullscreeneditor");
    const addDoorBtn = document.getElementById("adddoor");
    const previewBtn = document.getElementById("previewmode");
    const debugBtn = document.getElementById("debugmode");
    const snapBtn = document.getElementById("snap");

    // const addLocationBtn = document.getElementById("addlocation");

    const gridBtn = document.getElementById("grid");
    const pointBtn = document.getElementById("point");
    const edgeBtn = document.getElementById("edge");
    const faceBtn = document.getElementById("face");


    centerBtn.addEventListener("click", () => {
        console.log("click center btn ");
        centerView(10);
    });

    // All mode buttons share one highlight group
    modeButtons = [pointBtn, edgeBtn, faceBtn, addBtn, addDoorBtn, rotateBtn, scaleBtn];// panBtn,

    /**
     * Activate a button: set the mode, highlight it, and optionally set selection.
     * @param {HTMLButtonElement} btn    – the clicked button
     * @param {string}            mode   – EditMode value
     * @param {string}           [sel]   – optional SelectionMode value
     */
    function activate(btn, mode, sel) {
        setFSMState(mode);
        // highlight only this button
        modeButtons.forEach(b => b.classList.toggle('active', b === btn));
        // if a selection-mode is provided, set it and refresh
        if (sel) {
            setSelectionMode(sel);
            canvasMain.focus();
            refreshViewport();
        }
    }

    pointBtn.addEventListener("click", () => {
        activate(pointBtn, FSM_STATES.IDLE, SelectionMode.POINT);
        updateSideMenuUI("none");
    });
    edgeBtn.addEventListener("click", () => {
        activate(edgeBtn, FSM_STATES.IDLE, SelectionMode.LINE);
        updateSideMenuUI("none");
    });

    faceBtn.addEventListener("click", () => {
        activate(faceBtn, FSM_STATES.IDLE, SelectionMode.POLYGON);
        updateSideMenuUI("none");
    });

    rotateBtn.addEventListener("click", () => {
        activate(rotateBtn, FSM_STATES.ROTATE);
    });

    scaleBtn.addEventListener("click", () => {
        activate(scaleBtn, FSM_STATES.SCALE);
    });

    addBtn.addEventListener("click", () => {
        activate(addBtn, FSM_STATES.ADD_POINT, null);
    });

    addDoorBtn.addEventListener("click", () => {
        activate(addDoorBtn, FSM_STATES.ADD_DOOR, null);
    });


    //  addLocationBtn.addEventListener("click", () => {
    //      pointIds.forEach(pId => {
    //          addNewLocation(pId)
    //          updateSideMenuUI("location");
    //      })
    //
    //      refreshViewport();
    //  });

    gridBtn.addEventListener("click", () => {
        switchGridVisibility();
        updateGridIcon();
        refreshViewport();
    });


    deleteBtn.addEventListener("click", () => {
        var deleteSelectionCommand = deleteCommand();
        commit(deleteSelectionCommand);
    });


    snapBtn.addEventListener("click", () => {
        switchSnapVisibility();
        updateSnapIcon();
        refreshViewport();
    });

    previewBtn.addEventListener("click", () => {
        switchPreviewVisibility();
        updatePreviewIcon();
        refreshViewport();
    });

    debugBtn.addEventListener("click", () => {
        switchDebugVisibility();
        updateDebugIcon();
        refreshViewport();
    });


    fullScreenBtn.addEventListener("click", () => {
        const root = document.getElementById('wrapper');
        // Detect any native fullscreen
        const isNativeFS = document.fullscreenElement || document.webkitFullscreenElement || document.msFullscreenElement;
        if (isNativeFS) {
            // Exit native fullscreen
            if (document.exitFullscreen) document.exitFullscreen();
            else if (document.webkitExitFullscreen) document.webkitExitFullscreen();
            else if (document.msExitFullscreen) document.msExitFullscreen();
            // exit will trigger fullscreenchange event
            return;
        }
        // Enter native fullscreen if available
        if (root.requestFullscreen) {
            root.requestFullscreen();
        } else if (root.webkitRequestFullscreen) {
            root.webkitRequestFullscreen();
        } else if (root.msRequestFullscreen) {
            root.msRequestFullscreen();
        } else {
            // Fallback for browsers without Fullscreen API
            root.classList.toggle('force-fullscreen');
            fullscreen = !fullscreen;
            updateFullscreenIcon();
            resizeAllCanvases();
        }
    });

    // handle the native fullscreen change event:
    document.addEventListener('fullscreenchange', () => {
        fullscreen = !!document.fullscreenElement;
        updateFullscreenIcon();
        resizeAllCanvases()
    });




    const styletoolbar = document.getElementById("styletoolbar");
    const toolbar = createToolbar();
    // insert cloud buttons above the tree

    // insert our toolbar immediately below the tree
    //toolbar.style.marginLeft = `${navTree.offsetLeft}px`;
    // toolbar.style.marginTop = "4px";
    styletoolbar.appendChild(toolbar);

    activate(pointBtn, FSM_STATES.IDLE, SelectionMode.POINT);
    updateGridIcon();
    updateSnapIcon();
    updatePreviewIcon();
    updateDebugIcon();
}


function updateFullscreenIcon() {
    const fullScreenBtn = document.getElementById("fullscreeneditor");
    const img = fullScreenBtn.querySelector('img');
    if (!img) return;
    img.src = fullscreen ? 'https://cdn.jsdelivr.net/gh/elseforty/IndoorLens/LocationIcons/Minimize.png'
        : 'https://cdn.jsdelivr.net/gh/elseforty/IndoorLens/LocationIcons/Maximize.png';
}




function updateGridIcon() {
    const gridBtn = document.getElementById("grid");
    gridBtn.innerHTML = '';

    let icon = null;
    if (!isGrid) icon = "/Images/HideGrid.png";
    else icon = "/Images/ShowGrid.png";

    gridBtn.style.backgroundImage = `url("${icon}")`;
    // optional sizing tweaks:
    gridBtn.style.backgroundSize = "contain";
    gridBtn.style.backgroundRepeat = "no-repeat";
    gridBtn.style.backgroundPosition = "center";
    gridBtn.style.backgroundSize = "16px 16px";
}

