function createToolbar() {
    // 1) Parent container
    const toolbarDiv = document.createElement("div");
    // lay out toolbar items in one horizontal row
    toolbarDiv.style.display = "flex";
    toolbarDiv.style.alignItems = "center";
    toolbarDiv.style.gap = "2px";
    toolbarDiv.style.whiteSpace = "nowrap";
    toolbarDiv.id = "toolbar";

    /**
     * Build a compound style‐button:
     *  ┌──────────────┐
     *  │ 24×24 top    │ ← apply style
     *  ├──────────────┤
     *  │ 24×10 bottom │ ← show / hide toggle
     *  └──────────────┘
     *
     * Clicking the top region   ⇒ assign styleId to selected polygons / lines.
     * Clicking the bottom region⇒ toggle visibility of everything with that styleId.
     */
    function makeBtn(id, title, color) {


        const style = map.styles[id];


        /* outer wrapper – fixed 24×34 */
        const wrapper = document.createElement("div");
        Object.assign(wrapper.style, {
            width: "24px",
            height: "30px",
            display: "flex",
            flexDirection: "column",
            alignItems: "stretch",
            border: "1px solid #333",
            boxSizing: "border-box",
            cursor: "pointer"
        });
        wrapper.title = title;

        /* --- top region (style applicator) --------------------------- */
        const top = document.createElement("div");
        Object.assign(top.style, {
            height: "24px",
            background: id === "none" ? "transparent" : color,
            backgroundImage: id === "none" ? 'url("/Images/None.png")' : "none",
            backgroundSize: "16px 16px",
            backgroundPosition: "center",
            backgroundRepeat: "no-repeat"
        });

        /* --- bottom region (show / hide toggle) ---------------------- */
        const bottom = document.createElement("div");
        Object.assign(bottom.style, {
            height: "6px",
            background: style.enabled ? enabledColor : disabledColor,
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            // fontSize: "8px",
            userSelect: "none"
        });

        /* click‑top: apply style */
        top.addEventListener("click", e => {
            e.stopPropagation();
            console.log("style id is ", id);
            if (!treeSelection || treeSelection.length === 0) return;
            const sel = treeSelection.at(-1);

            const group = map.groups[sel.groupId];
            if (!group) return;

            const layer = group.layers[sel.layerId];
            if (!layer) return;

            if (["innerwall", "outerwall"].includes(id)) {
                lineIds.forEach(lineId => {
                    const ln = layer.lines[lineId];
                    if (ln) {
                        ln.styleId = id;
                    }
                });
                lineIds.length = 0;
            } else if (["floor", "public", "private"].includes(id)) {
                updatePolygonsStyle(id) ;
            }
            else if (id === "none") {
                lineIds.forEach(lineId => {
                    const ln = layer.lines[lineId];
                    if (ln) {
                        ln.styleId = id;
                    }
                });
                lineIds.length = 0;
            }

            buildMap();
            refreshViewport();
        });

        /* click‑bottom: toggle visibility */
        bottom.addEventListener("click", e => {
            e.stopPropagation();

            /* toggle lines */
            Object.entries(map.styles).forEach(([styleId, style]) => {
                if (styleId === id) style.enabled = !style.enabled;
            });

            bottom.style.background = style.enabled ? enabledColor : disabledColor;
            refreshViewport();
        });

        wrapper.appendChild(top);
        wrapper.appendChild(bottom);
        return wrapper;
    }

    // 3) Label + Wall buttons


    toolbarDiv.appendChild(document.createTextNode("Wall:"));
    toolbarDiv.appendChild(makeBtn("innerwall", "Inner Wall", innerWallColor));
    toolbarDiv.appendChild(makeBtn("outerwall", "Outer Wall", outerWallColor));
    toolbarDiv.appendChild(makeBtn("none", "Clear Wall Style", null));

    // spacer between Wall and Surface groups
    const groupSpacer = document.createElement("div");
    groupSpacer.style.width = "16px";
    toolbarDiv.appendChild(groupSpacer);

    // 4) Label + Surface buttons
    toolbarDiv.appendChild(document.createTextNode("Surface:"));
    toolbarDiv.appendChild(makeBtn("floor", "Floor Surface Color", floorColor));
    toolbarDiv.appendChild(makeBtn("public", "Public Surface Color", publicColor));
    toolbarDiv.appendChild(makeBtn("private", "Private Surface Color", privateColor));

    return toolbarDiv;
}


export function addNewLocation(pointId) {
    const point = map.points[pointId];
    if (!point || point.locationId) return;
    const loc = new LocationModel();
    loc.groupId = point.groupId;
    loc.layerId = point.layerId;
    loc.pointId = pointId;
    loc.name = `Location Name`;
    const newLocId = generateShortGuid();
    point.locationId = newLocId;


    const group = map.groups[point.groupId];
    if (!group) return;
    const layer = group.layers[point.layerId];
    if (!layer) return;

    layer.locationIds.push(newLocId);
    map.locations[newLocId] = loc;
    setLocationId(newLocId);
}


export function updatePolygonsStyle(styleId) {
    if (!treeSelection || treeSelection.length === 0) return;
    const sel = treeSelection.at(-1);

    const group = map.groups[sel.groupId];
    if (!group) return;

    const layer = group.layers[sel.layerId];
    if (!layer) return;
    polygonIds.forEach(pid => {
        const poly = layer.polygons[pid];
        if (poly) {
            poly.styleId = styleId;
        }
    });
    tryGeneratePolygon(lineIds, styleId);
    polygonIds.length = 0;
}