import { Vec3 } from '../../shared/models.js';
import { PointModel, LineModel, generateShortGuid } from '../models/models.js';
import { map } from '../editor.js';
import { lineIds, pointIds, setPointId, setLineId, treeSelection } from '../selection.js';
import { refreshViewport, buildMap, getSnapPointId } from "../draw.js";
import { mousePos } from "../editing/state.js";
import { cloneBranch } from './utils.js';
import { FSM_STATES, setFSMState } from './../editing/state.js';

// Constants for better maintainability
const EPSILON = 1e-6;
const SNAP_TOLERANCE_MULTIPLIER = 1.0;

/**
 * Configuration object for geometric operations
 */
const CONFIG = {
    EPSILON,
    SNAP_TOLERANCE_MULTIPLIER,
    DEFAULT_Y_COORDINATE: 0
};

/**
 * Utility functions for geometric operations
 */
const GeometryUtils = {
    /**
     * Compare two floating point numbers with epsilon tolerance
     * @param {number} a - First number
     * @param {number} b - Second number  
     * @param {number} eps - Epsilon tolerance
     * @returns {boolean} True if numbers are equal within tolerance
     */
    floatEquals(a, b, eps = CONFIG.EPSILON) {
        return Math.abs(a - b) < eps;
    },

    /**
     * Check if value is in the middle range (not at endpoints)
     * @param {number} value - Value to check
     * @param {number} eps - Epsilon tolerance
     * @returns {boolean} True if value is in middle range
     */
    isInMiddleRange(value, eps = CONFIG.EPSILON) {
        return value > eps && value < (1 - eps);
    },

    /**
     * Validate array index bounds
     * @param {Array} array - Array to check
     * @param {number} index - Index to validate
     * @returns {boolean} True if index is valid
     */
    isValidIndex(array, index) {
        return Array.isArray(array) && index >= 0 && index < array.length;
    },

    /**
     * Calculate distance between two points
     * @param {Object} p1 - First point {x, z}
     * @param {Object} p2 - Second point {x, z}
     * @returns {number} Distance between points
     */
    distance(p1, p2) {
        return Math.hypot(p2.x - p1.x, p2.z - p1.z);
    },

    /**
     * Check if two bounding boxes overlap
     * @param {Object} box1 - First bounding box {minX, maxX, minZ, maxZ}
     * @param {Object} box2 - Second bounding box {minX, maxX, minZ, maxZ}
     * @returns {boolean} True if boxes overlap
     */
    boundingBoxesOverlap(box1, box2) {
        return !(box1.maxX < box2.minX || box2.maxX < box1.minX ||
                box1.maxZ < box2.minZ || box2.maxZ < box1.minZ);
    }
};

// Holds a reference to the command's delete-list while `do()` is running
let currentLinesToDeleteRef = null;

/**
 * Error handling utilities
 */
const ErrorHandler = {
    /**
     * Log warning with context
     * @param {string} message - Warning message
     * @param {Object} context - Additional context
     */
    warn(message, context = {}) {
        console.warn(`[GeometryEditor] ${message}`, context);
    },

    /**
     * Log error with context
     * @param {string} message - Error message
     * @param {Error} error - Error object
     * @param {Object} context - Additional context
     */
    error(message, error, context = {}) {
        console.error(`[GeometryEditor] ${message}`, error, context);
    },

    /**
     * Safe execution wrapper
     * @param {Function} fn - Function to execute
     * @param {string} operation - Operation name for logging
     * @param {*} fallback - Fallback value on error
     * @returns {*} Function result or fallback
     */
    safeExecute(fn, operation, fallback = null) {
        try {
            return fn();
        } catch (error) {
            this.error(`Failed to execute ${operation}`, error);
            return fallback;
        }
    }
};

/**
 * Point management utilities
 */
const PointManager = {
    /**
     * Create a new point with proper initialization
     * @param {Object} position - Point position {x, y, z}
     * @param {string} groupId - Group ID
     * @param {string} layerId - Layer ID
     * @returns {Object} Created point with ID
     */
    createPoint(position, groupId, layerId) {
        const point = new PointModel();
        point.groupId = groupId;
        point.layerId = layerId;
        point.position = new Vec3(position);
        point.associatedLines = [];

        const pointId = generateShortGuid();
        map.points[pointId] = point;

        return { point, pointId };
    },

    /**
     * Add line association to point
     * @param {string} pointId - Point ID
     * @param {string} lineId - Line ID
     */
    addLineToPoint(pointId, lineId) {
        const point = map.points[pointId];
        if (!point) {
            ErrorHandler.warn('Point not found for line association', { pointId, lineId });
            return;
        }

        if (!Array.isArray(point.associatedLines)) {
            point.associatedLines = [];
        }

        if (!point.associatedLines.includes(lineId)) {
            point.associatedLines.push(lineId);
        }
    },

    /**
     * Remove line association from point
     * @param {string} pointId - Point ID
     * @param {string} lineId - Line ID
     */
    removeLineFromPoint(pointId, lineId) {
        const point = map.points[pointId];
        if (!point || !Array.isArray(point.associatedLines)) return;

        point.associatedLines = point.associatedLines.filter(id => id !== lineId);
    },

    /**
     * Get point by ID with validation
     * @param {string} pointId - Point ID
     * @returns {Object|null} Point object or null
     */
    getPoint(pointId) {
        const point = map.points[pointId];
        if (!point) {
            ErrorHandler.warn('Point not found', { pointId });
            return null;
        }
        return point;
    }
};

/**
 * Line management utilities
 */
const LineManager = {
    /**
     * Create a new line with proper initialization
     * @param {string} groupId - Group ID
     * @param {string} layerId - Layer ID
     * @param {string} styleId - Style ID (optional)
     * @returns {Object} Created line with ID
     */
    createLine(groupId, layerId, styleId = null) {
        const line = new LineModel(styleId);
        if (styleId) {
            line.styleId = styleId;
        }

        const lineId = generateShortGuid();
        const context = SelectionManager.getSelectionContext();
        if (context && context.layer) {
            context.layer.lines[lineId] = line;
        }

        return { line, lineId };
    },

    /**
     * Safe branch cloning with error handling
     * @param {string} lineId - Line ID to clone
     * @param {Object} layer - Layer object
     * @returns {Object|null} Cloned branch or null
     */
    safeBranchClone(lineId, layer) {
        return ErrorHandler.safeExecute(() => {
            if (layer.lines[lineId]) {
                return cloneBranch(layer.lines[lineId]);
            }
            return null;
        }, 'branch cloning');
    },

    /**
     * Get line by ID with validation
     * @param {string} lineId - Line ID
     * @param {Object} layer - Layer object
     * @returns {Object|null} Line object or null
     */
    getLine(lineId, layer) {
        const line = layer.lines[lineId];
        if (!line) {
            ErrorHandler.warn('Line not found', { lineId });
            return null;
        }
        return line;
    }
};

/**
 * Selection management utilities
 */
const SelectionManager = {
    /**
     * Get current selection context with validation
     * @returns {Object|null} Selection context or null
     */
    getSelectionContext() {
        if (!treeSelection || treeSelection.length === 0) {
            ErrorHandler.warn('No selection context available');
            return null;
        }

        const selection = treeSelection.at(-1);
        const group = map.groups[selection.groupId];
        if (!group) {
            ErrorHandler.warn('Group not found in selection', { groupId: selection.groupId });
            return null;
        }

        const layer = group.layers[selection.layerId];
        if (!layer) {
            ErrorHandler.warn('Layer not found in selection', { layerId: selection.layerId });
            return null;
        }

        return { selection, group, layer };
    },

    /**
     * Update point selection
     * @param {string} newPointId - New point ID to select
     */
    updateSelection(newPointId) {
        pointIds.length = 0;
        setPointId(newPointId);
    },

    /**
     * Check if a point is currently selected
     * @returns {boolean} True if point is selected
     */
    hasPointSelection() {
        return pointIds && pointIds.length === 1;
    },

    /**
     * Get selected point ID
     * @returns {string|null} Selected point ID or null
     */
    getSelectedPointId() {
        return this.hasPointSelection() ? pointIds[0] : null;
    }
};

/**
 * Intersection handling utilities
 */
const IntersectionHandler = {
    /**
     * Process a single intersection hit
     * @param {Object} context - Selection context
     * @param {string} newPointId - New point ID
     * @param {Object} hit - Intersection hit data
     * @param {Array} linesToDelete - Array to track lines for deletion
     */
    processSingleHit(context, newPointId, hit, linesToDelete) {
        return ErrorHandler.safeExecute(() => {
            if (!hit || !hit.lineId || !newPointId) {
                ErrorHandler.warn('Invalid parameters for intersection processing');
                return;
            }

            const line = LineManager.getLine(hit.lineId, context.layer);
            if (!line || !GeometryUtils.isValidIndex(line.pointIds, hit.segmentIndex)) {
                ErrorHandler.warn('Invalid line or segment index', { lineId: hit.lineId, segmentIndex: hit.segmentIndex });
                return;
            }

            // Handle intersection based on position
            if (GeometryUtils.floatEquals(hit.t, 0) || GeometryUtils.floatEquals(hit.t, 1)) {
                // At segment endpoints - connect to existing point
                console.log('Intersection at segment endpoint');
                this.breakBranchAtNode(newPointId);
            } else if (GeometryUtils.isInMiddleRange(hit.t)) {
                // In middle of segment - split the line
                console.log('Intersection in middle of segment');
                const segmentIndex = hit.segmentIndex;
                line.pointIds.splice(segmentIndex + 1, 0, newPointId);
                this.breakBranchAtNode(newPointId);
            }

            if (!linesToDelete.includes(hit.lineId)) {
                linesToDelete.push(hit.lineId);
            }
        }, 'single hit processing');
    },

    /**
     * Process multiple intersection hits
     * @param {Object} context - Selection context
     * @param {Object} mainBranch - Main branch object
     * @param {Array} hits - Array of intersection hits
     * @param {Array} linesToDelete - Array to track lines for deletion
     */
    processMultipleHits(context, mainBranch, hits, linesToDelete) {
        return ErrorHandler.safeExecute(() => {
            if (!Array.isArray(hits) || !Array.isArray(linesToDelete)) {
                ErrorHandler.warn('Invalid parameters for multiple hits processing');
                return;
            }

            hits.forEach(hit => {
                if (hit && hit.lineId) {
                    this.processSingleHit(context, hit.newPointId, hit, linesToDelete);
                }
            });
        }, 'multiple hits processing');
    },

    /**
     * Break branch at intersection node
     * @param {string} pointId - Point ID where to break
     */
    breakBranchAtNode(pointId) {
        const point = PointManager.getPoint(pointId);
        if (!point || !Array.isArray(point.associatedLines)) return;

        const group = map.groups[point.groupId];
        if (!group) return;

        const layer = group.layers[point.layerId];
        if (!layer) return;

        // Copy array to avoid modification during iteration
        const lineIds = [...point.associatedLines];

        lineIds.forEach(initialLineId => {
            const line = layer.lines[initialLineId];
            if (!line) return;

            const pointIndex = line.pointIds.indexOf(pointId);
            if (pointIndex <= 0 || pointIndex >= line.pointIds.length - 1) return; // Skip endpoints

            // Create new line (inherit style)
            const newLineId = generateShortGuid();
            const newLine = new LineModel(line.styleId);
            newLine.styleId = line.styleId;
            layer.lines[newLineId] = newLine;


               // 👉 Tell the caller’s undo logic to delete this line later
   if (currentLinesToDeleteRef && !currentLinesToDeleteRef.includes(newLineId)) {
       currentLinesToDeleteRef.push(newLineId);
   }

            // Split the point arrays
            const leftPoints = line.pointIds.slice(0, pointIndex + 1); // includes split point
            const rightPoints = line.pointIds.slice(pointIndex);        // includes split point

            line.pointIds = leftPoints;
            newLine.pointIds = rightPoints;


            // Update associations for points in right segment
            rightPoints.forEach(pId => {
                const pt = PointManager.getPoint(pId);
                if (!pt) return;

                // Remove old association (except for split point)
                if (pId !== pointId) {
                    PointManager.removeLineFromPoint(pId, initialLineId);
                }

                // Add new association
                PointManager.addLineToPoint(pId, newLineId);
            });

            // Ensure split point references both halves
            PointManager.addLineToPoint(pointId, newLineId);
        });
    }
};

/**
 * Geometric calculation utilities
 */
const GeometricCalculator = {
    /**
     * Find if point lies on any line segment
     * @param {Object} point - Point to test {x, z}
     * @param {number} eps - Epsilon tolerance
     * @returns {Object|null} Hit information or null
     */
    findPointOnAnySegment(point, eps = CONFIG.EPSILON) {
        if (!point || !treeSelection) {
            ErrorHandler.warn('Invalid parameters for point-on-segment test');
            return null;
        }

        const { x: pointX, z: pointZ } = point;

        return ErrorHandler.safeExecute(() => {
            for (const selection of treeSelection) {
                const group = map.groups[selection.groupId];
                if (!group) continue;

                const layer = group.layers[selection.layerId];
                if (!layer) continue;

                for (const [lineId, line] of Object.entries(layer.lines)) {
                    if (!line || !line.pointIds) continue;

                    const points = line.pointIds.map(pid => PointManager.getPoint(pid)).filter(Boolean);
                    if (points.length < 2) continue;

                    for (let i = 0; i < points.length - 1; i++) {
                        const segmentHit = this.testPointOnSegment(
                            { x: pointX, z: pointZ },
                            points[i].position,
                            points[i + 1].position,
                            eps
                        );

                        if (segmentHit) {
                            return {
                                lineId,
                                segmentIndex: i,
                                t: segmentHit.t,
                                x: pointX,
                                z: pointZ
                            };
                        }
                    }
                }
            }
            return null;
        }, 'point-on-segment calculation');
    },

    /**
     * Test if point lies on a specific line segment
     * @param {Object} testPoint - Point to test {x, z}
     * @param {Object} segmentStart - Segment start point
     * @param {Object} segmentEnd - Segment end point
     * @param {number} eps - Epsilon tolerance
     * @returns {Object|null} Hit information or null
     */
    testPointOnSegment(testPoint, segmentStart, segmentEnd, eps) {
        const { x: px, z: pz } = testPoint;
        const { x: sx, z: sz } = segmentStart;
        const { x: ex, z: ez } = segmentEnd;

        // Bounding box quick reject
        const minX = Math.min(sx, ex) - eps;
        const maxX = Math.max(sx, ex) + eps;
        const minZ = Math.min(sz, ez) - eps;
        const maxZ = Math.max(sz, ez) + eps;

        if (px < minX || px > maxX || pz < minZ || pz > maxZ) {
            return null;
        }

        // Distance test
        const segmentDx = ex - sx;
        const segmentDz = ez - sz;
        const pointDx = px - sx;
        const pointDz = pz - sz;

        const cross = Math.abs(segmentDx * pointDz - segmentDz * pointDx);
        const segmentLength = Math.hypot(segmentDx, segmentDz);

        if (segmentLength < eps) return null; // Degenerate segment

        if (cross / segmentLength <= eps) {
            // Compute parameter t
            const dot = pointDx * segmentDx + pointDz * segmentDz;
            const t = dot / (segmentLength * segmentLength);
            const clampedT = Math.max(0, Math.min(1, t));

            return { t: clampedT };
        }

        return null;
    },

    /**
     * Find intersections between a line segment and existing geometry
     * @param {string} excludeLineId - Line ID to exclude from search
     * @param {Object} startPoint - Segment start point {x, z}
     * @param {Object} endPoint - Segment end point {x, z}
     * @returns {Array} Array of intersection objects
     */
    findIntersections(excludeLineId, startPoint, endPoint) {
        if (!startPoint || !endPoint || !treeSelection) {
            ErrorHandler.warn('Invalid parameters for intersection calculation');
            return [];
        }

        const intersections = [];
        const segmentDx = endPoint.x - startPoint.x;
        const segmentDz = endPoint.z - startPoint.z;

        return ErrorHandler.safeExecute(() => {
            treeSelection.forEach(selection => {
                const group = map.groups[selection.groupId];
                if (!group) return;

                const layer = group.layers[selection.layerId];
                if (!layer) return;

                Object.entries(layer.lines).forEach(([lineId, line]) => {
                    if (lineId === excludeLineId || !line || !line.pointIds) return;

                    const points = line.pointIds.map(pid => PointManager.getPoint(pid)).filter(Boolean);
                    if (points.length < 2) return;

                    for (let i = 0; i < points.length - 1; i++) {
                        const intersection = this.calculateSegmentIntersection(
                            startPoint, endPoint,
                            points[i].position, points[i + 1].position
                        );

                        if (intersection) {
                            intersections.push({
                                lineId,
                                x: intersection.x,
                                z: intersection.z,
                                segmentIndex: i,
                                t: intersection.t,
                                u: intersection.u
                            });
                        }
                    }
                });
            });

            return intersections;
        }, 'intersection calculation', []);
    },

    /**
     * Calculate intersection between two line segments
     * @param {Object} seg1Start - First segment start point
     * @param {Object} seg1End - First segment end point
     * @param {Object} seg2Start - Second segment start point
     * @param {Object} seg2End - Second segment end point
     * @returns {Object|null} Intersection data or null
     */
    calculateSegmentIntersection(seg1Start, seg1End, seg2Start, seg2End) {
        const dx1 = seg1End.x - seg1Start.x;
        const dz1 = seg1End.z - seg1Start.z;
        const dx2 = seg2End.x - seg2Start.x;
        const dz2 = seg2End.z - seg2Start.z;

        // Fast bounding box overlap test
        const box1 = {
            minX: Math.min(seg1Start.x, seg1End.x),
            maxX: Math.max(seg1Start.x, seg1End.x),
            minZ: Math.min(seg1Start.z, seg1End.z),
            maxZ: Math.max(seg1Start.z, seg1End.z)
        };

        const box2 = {
            minX: Math.min(seg2Start.x, seg2End.x),
            maxX: Math.max(seg2Start.x, seg2End.x),
            minZ: Math.min(seg2Start.z, seg2End.z),
            maxZ: Math.max(seg2Start.z, seg2End.z)
        };

        if (!GeometryUtils.boundingBoxesOverlap(box1, box2)) {
            return null;
        }

        // Calculate intersection
        const denominator = dx1 * (-dz2) - dz1 * (-dx2);
        if (Math.abs(denominator) < CONFIG.EPSILON) return null; // Parallel lines

        const startDx = seg2Start.x - seg1Start.x;
        const startDz = seg2Start.z - seg1Start.z;
        const t = (startDx * -dz2 - startDz * -dx2) / denominator;
        const u = (dx1 * startDz - dz1 * startDx) / denominator;

        // Check if intersection is within both segments
        if (t >= 0 && t <= 1 && u >= 0 && u <= 1) {
            const x = seg1Start.x + t * dx1;
            const z = seg1Start.z + t * dz1;

            return { x, z, t, u };
        }

        return null;
    }
};

/**
 * Command execution strategies
 */
const CommandStrategies = {
    /**
     * Handle case when a point is selected
     * @param {Object} context - Selection context
     * @param {string} selectedPointId - Selected point ID
     * @param {Object} newPoint - New point data
     * @param {string} newPointId - New point ID
     * @param {Array} linesToDelete - Lines to delete array
     * @param {Object} affectedBranches - Affected branches object
     * @param {Array} createdPoints - Created points array
     */
    handlePointSelection(context, selectedPointId, newPoint, newPointId, linesToDelete, affectedBranches, createdPoints) {
        const selectedNode = PointManager.getPoint(selectedPointId);
        if (!selectedNode || !selectedNode.associatedLines || selectedNode.associatedLines.length === 0) {
            ErrorHandler.warn('Selected point has no associated lines');
            return;
        }

        const lineId = selectedNode.associatedLines[0];
        const targetLine = LineManager.getLine(lineId, context.layer);
        if (!targetLine) {
            ErrorHandler.warn('Target line not found', { lineId });
            return;
        }

        // Clone branch for undo functionality
        if (!affectedBranches[lineId]) {
            affectedBranches[lineId] = LineManager.safeBranchClone(lineId, context.layer);
        }

        if (selectedNode.associatedLines.length === 1) {
            this.handleNonSharedPoint(context, selectedPointId, selectedNode, newPoint, newPointId, 
                                    lineId, targetLine, linesToDelete, affectedBranches, createdPoints);
        } else {
            this.handleSharedPoint(context, selectedPointId, selectedNode, newPoint, newPointId, 
                                 linesToDelete, affectedBranches, createdPoints);
        }
    },

    /**
     * Handle non-shared point selection
     */
    handleNonSharedPoint(context, selectedPointId, selectedNode, newPoint, newPointId, 
                        lineId, targetLine, linesToDelete, affectedBranches, createdPoints) {
        console.log("Selected point is not shared – creating fresh 2-point line");

        // Create new line
        const { line: newLine, lineId: newLineId } = LineManager.createLine(
            context.selection.groupId, context.selection.layerId
        );

        newLine.pointIds.push(selectedPointId, newPointId);
        PointManager.addLineToPoint(selectedPointId, newLineId);
        PointManager.addLineToPoint(newPointId, newLineId);

        // Check for endpoint intersections
        this.handleEndpointIntersection(newPoint.position, newPointId, newLineId, context, affectedBranches, linesToDelete);

        // Track for undo
        setLineId(newLineId);
        linesToDelete.push(newLineId);

        // Clean up single-point line
        if (targetLine.pointIds.length === 1) {
            this.cleanupSinglePointLine(lineId, selectedNode, context, linesToDelete);
        }

        // Handle segment intersections
        this.handleSegmentIntersections(selectedNode.position, newPoint.position, newLineId, 
                                      newLine, context, affectedBranches, linesToDelete, createdPoints);
    },

    /**
     * Handle shared point selection
     */
    handleSharedPoint(context, selectedPointId, selectedNode, newPoint, newPointId, 
                     linesToDelete, affectedBranches, createdPoints) {
        console.log("Selected point is shared");

        // Create new line
        const { line: newLine, lineId: newLineId } = LineManager.createLine(
            context.selection.groupId, context.selection.layerId
        );

        newLine.pointIds.push(selectedPointId, newPointId);
        PointManager.addLineToPoint(selectedPointId, newLineId);
        PointManager.addLineToPoint(newPointId, newLineId);

        // Check for endpoint intersections
        this.handleEndpointIntersection(newPoint.position, newPointId, newLineId, context, affectedBranches, linesToDelete);

        setLineId(newLineId);
        linesToDelete.push(newLineId);

        // Handle segment intersections
        this.handleSegmentIntersections(selectedNode.position, newPoint.position, newLineId, 
                                      newLine, context, affectedBranches, linesToDelete, createdPoints);
    },

    /**
     * Handle case when no point is selected
     */
    handleNoSelection(context, newPoint, newPointId, linesToDelete, affectedBranches) {
        console.log("No point selected");

        const hit = GeometricCalculator.findPointOnAnySegment(newPoint.position);

        if (hit) {
            console.log("Point intersects existing line");
            const affectedBranch = LineManager.getLine(hit.lineId, context.layer);
            if (!linesToDelete.includes(hit.lineId) && !affectedBranches[hit.lineId]) {
                affectedBranches[hit.lineId] = LineManager.safeBranchClone(hit.lineId, context.layer);
            }
            IntersectionHandler.processSingleHit(context, newPointId, hit, linesToDelete);
        } else {
            console.log("No intersection - creating new line");
            const { line, lineId: newBranchId } = LineManager.createLine(
                context.selection.groupId, context.selection.layerId
            );
            setLineId(newBranchId);

            linesToDelete.push(newBranchId);
            line.pointIds.push(newPointId);
            PointManager.addLineToPoint(newPointId, newBranchId);
        }
    },

    /**
     * Handle endpoint intersections
     */
    handleEndpointIntersection(position, pointId, lineId, context, affectedBranches, linesToDelete) {
        const endpointHit = GeometricCalculator.findPointOnAnySegment(position);
        if (endpointHit && endpointHit.lineId !== lineId) {
            endpointHit.newPointId = pointId;
            PointManager.addLineToPoint(pointId, endpointHit.lineId);

            if (!linesToDelete.includes(endpointHit.lineId) && !affectedBranches[endpointHit.lineId]) {
                affectedBranches[endpointHit.lineId] = LineManager.safeBranchClone(endpointHit.lineId, context.layer);
            }
            IntersectionHandler.processSingleHit(context, pointId, endpointHit, linesToDelete);
        }
    },

    /**
     * Handle segment intersections
     */
    handleSegmentIntersections(startPos, endPos, lineId, line, context, affectedBranches, linesToDelete, createdPoints) {
        const hits = GeometricCalculator.findIntersections(
            lineId,
            { x: startPos.x, z: startPos.z },
            { x: endPos.x, z: endPos.z }
        ).sort((a, b) => a.t - b.t);

        // Process intersections on the new line
        let insertOffset = 1;
        hits.forEach(hit => {
            if (!GeometryUtils.isInMiddleRange(hit.t)) {
                // Endpoint intersection - use existing point
                hit.newPointId = GeometryUtils.floatEquals(hit.t, 0) ? 
                    line.pointIds[0] : line.pointIds[line.pointIds.length - 1];
                return;
            }

            // Middle intersection - create new point
            const { point: intersectionPoint, pointId: intersectionId } = PointManager.createPoint(
                { x: hit.x, y: CONFIG.DEFAULT_Y_COORDINATE, z: hit.z },
                context.selection.groupId,
                context.selection.layerId
            );

            createdPoints.push(intersectionId);
            PointManager.addLineToPoint(intersectionId, lineId);
            PointManager.addLineToPoint(intersectionId, hit.lineId);

            hit.newPointId = intersectionId;
            line.pointIds.splice(insertOffset, 0, intersectionId);
            insertOffset += 1;
        });

        // Update affected branches
        hits.forEach(hit => {
            if (!linesToDelete.includes(hit.lineId) && !affectedBranches[hit.lineId]) {
                affectedBranches[hit.lineId] = LineManager.safeBranchClone(hit.lineId, context.layer);
            }
        });

        // Process intersections on existing lines
        hits.forEach(hit => { hit.t = hit.u; }); // Re-base parameter
        IntersectionHandler.processMultipleHits(context, line, hits, linesToDelete);
    },

    /**
     * Clean up single-point line
     */
    cleanupSinglePointLine(lineId, selectedNode, context, linesToDelete) {
        if (!linesToDelete.includes(lineId)) {
            linesToDelete.push(lineId);
        }
        if (context.layer.lines[lineId]) {
            delete context.layer.lines[lineId];
        }
        selectedNode.associatedLines = selectedNode.associatedLines.filter(id => id !== lineId);
    }
};

/**
 * Main command factory function
 * @returns {Object} Command object with do() and undo() methods
 */
export function addCommand() {
    let linesToDelete = [];
    let affectedBranches = {};
    let createdPoints = [];

    return {
        /**
         * Execute the add command
         */
        
        do() {
            return ErrorHandler.safeExecute(() => {
                // Reset state

                linesToDelete.length = 0;
                affectedBranches = {};
                createdPoints.length = 0;
 currentLinesToDeleteRef = linesToDelete;   // make it globally reachable


                const context = SelectionManager.getSelectionContext();
                if (!context) {
                    ErrorHandler.warn('No valid selection context');
                    return;
                }

                // Calculate snap tolerance
                const snapTolerance = Math.abs(map.settings.gridSize) > 0 ?
                    Math.abs(map.settings.gridSize) * CONFIG.SNAP_TOLERANCE_MULTIPLIER : 1;

                // Get or create point
                const snapPointId = getSnapPointId(mousePos);
                let point, newPointId;

                if (!snapPointId) {
                    console.log('Creating new point');
                    const result = PointManager.createPoint(
                        mousePos, 
                        context.selection.groupId, 
                        context.selection.layerId
                    );
                    point = result.point;
                    newPointId = result.pointId;
                    createdPoints.push(newPointId);
                } else {
                    console.log('Using snap point');
                    point = PointManager.getPoint(snapPointId);
                    newPointId = snapPointId;

                    if (!point) {
                        ErrorHandler.error('Snap point not found in map');
                        return;
                    }
                }

                // Execute appropriate strategy based on selection state
                if (SelectionManager.hasPointSelection()) {
                    const selectedPointId = SelectionManager.getSelectedPointId();
                    CommandStrategies.handlePointSelection(
                        context, selectedPointId, point, newPointId,
                        linesToDelete, affectedBranches, createdPoints
                    );
                } else {
                    CommandStrategies.handleNoSelection(
                        context, point, newPointId, linesToDelete, affectedBranches
                    );
                }

                // Update selection and rebuild map
                SelectionManager.updateSelection(newPointId);
                buildMap();
                setFSMState(FSM_STATES.ADD_POINT);

            }, 'add command execution', () => {
                // Fallback: attempt to restore state
                this.undo();
            });
        },

        /**
         * Undo the add command
         */
        undo() {
            return ErrorHandler.safeExecute(() => {
                currentLinesToDeleteRef = null;
                const context = SelectionManager.getSelectionContext();
                if (!context) return;

                // Remove created lines
                linesToDelete.forEach(lineId => {
                    if (context.layer.lines[lineId]) {
                        delete context.layer.lines[lineId];
                    }
                });

                // Restore affected lines
                Object.keys(affectedBranches).forEach(lineId => {
                    if (affectedBranches[lineId]) {
                        context.layer.lines[lineId] = affectedBranches[lineId];
                    }
                });

                // Remove created points only if no line still references them
                createdPoints.forEach(pointId => {
                    let stillUsed = false;
                    Object.values(map.groups).forEach(g => {
                        if (stillUsed) return;
                        Object.values(g.layers).forEach(l => {
                            if (stillUsed) return;
                            Object.values(l.lines).forEach(line => {
                                if (line && line.pointIds && line.pointIds.includes(pointId)) {
                                    stillUsed = true;
                                }
                            });
                        });
                    });
                    if (!stillUsed && map.points[pointId]) {
                        delete map.points[pointId];
                    }
                });

                // Clear state
                linesToDelete.length = 0;
                affectedBranches = {};
                createdPoints.length = 0;

                buildMap();
            }, 'add command undo');
        }
    };
}

/**
 * Enhanced pointOnAnySegment with better error handling
 * @param {Object} point - Point to test {x, z}
 * @param {number} eps - Epsilon tolerance
 * @returns {Object|null} Hit information or null
 */
export function pointOnAnySegment(point, eps = CONFIG.EPSILON) {
    return GeometricCalculator.findPointOnAnySegment(point, eps);
}

/**
 * Enhanced findIntersections with better error handling and bounds checking
 * @param {string} excludeLineId - Line ID to exclude from search
 * @param {Object} startPoint - Segment start point {x, z}
 * @param {Object} endPoint - Segment end point {x, z}
 * @returns {Array} Array of intersection objects
 */
export function findIntersections(excludeLineId, startPoint, endPoint) {
    return GeometricCalculator.findIntersections(excludeLineId, startPoint, endPoint);
}

/**
 * Split every line that passes through the point with the given pointId at that point
 * @param {string} pointId - Point ID where to break branches
 * @returns {boolean} True if something was split, false otherwise
 */
export function breakBranchAtNode(pointId) {
    IntersectionHandler.breakBranchAtNode(pointId);
    return true; // Return true for compatibility
}


