import { map, isSnap } from "./../editor.js";
import { canvasMain, canvasSelection, pickColorMap, navigation } from "./../../editor/draw.js";
import { treeSelection } from "./../selection.js";

/**
 * Hit testing utilities
 * @typedef {import('./types.js').PointId} PointId
 * @typedef {import('./types.js').HitTestResult} HitTestResult
 * @typedef {import('./types.js').WorldRect} WorldRect
 * @typedef {import('./types.js').ClosestPointResult} ClosestPointResult
 * @typedef {import('./types.js').ProjectionResult} ProjectionResult
 */

export const hitTest = {
    /**
     * Find the closest node to the given position within tolerance.
     * @param {{x: number, z: number}} pos - Position to test
     * @param {number} tolPx - Tolerance in pixels
     * @returns {HitTestResult} - Closest node ID and distance
     */
    snapToNode(pos) {

        const transform = navigation.getTransform();
        const pickPx = map.settings.selectionRadius > 0 ? map.settings.selectionRadius : 8;
        const tol = pickPx / transform.scale;

        let closestId = null;
        let minDist = tol;

        const sel = treeSelection.at(-1);

        if (sel) {
            const group = map.groups[sel.groupId];
            if (!group) return;
            const layer = group.layers[sel.layerId];
            if (!layer) return;

            // Iterate all lines & their pointIds
            Object.values(layer.lines).forEach(line => {
                line.pointIds.forEach(pid => {
                    const p = map.points[pid];
                    if (!p) return;
                    const dx = p.position.x - pos.x;
                    const dz = p.position.z - pos.z;
                    const dist = Math.hypot(dx, dz);
                    if (dist < minDist) {
                        minDist = dist;
                        closestId = pid;
                    }
                });
            });
        }

        return closestId;
    },
        pickPoint(pos) {

        const transform = navigation.getTransform();
        const pickPx = map.settings.selectionRadius > 0 ? map.settings.selectionRadius : 8;
        const tol = pickPx / transform.scale;

        let closestId = null;
        let minDist = tol;

        treeSelection.forEach(sel=>{
            const group = map.groups[sel.groupId];
            if (!group) return;
            const layer = group.layers[sel.layerId];
            if (!layer) return;

            // Iterate all lines & their pointIds
            Object.values(layer.lines).forEach(line => {
                line.pointIds.forEach(pid => {
                    const p = map.points[pid];
                    if (!p) return;
                    const dx = p.position.x - pos.x;
                    const dz = p.position.z - pos.z;
                    const dist = Math.hypot(dx, dz);
                    if (dist < minDist) {
                        minDist = dist;
                        closestId = pid;
                    }
                });
            });
         });

        return closestId;
    },

    snapToLocation(pos) {
        const transform = navigation.getTransform();
        const pickPx = map.settings.selectionRadius > 0 ? map.settings.selectionRadius : 8;
        const tol = pickPx / transform.scale;

        let closestId = null;
        let minDist = tol;


        treeSelection.forEach(sel => {
            const group = map.groups[sel.groupId];
            if (!group) return;
            const layer = group.layers[sel.layerId];
            if (!layer) return;

            // Iterate all lines & their pointIds
            Object.values(layer.locationIds).forEach(locationId => {
                const location = map.locations[locationId];
                if (!location) return;
                const p = location.center;
                if (!p) return;
                const dx = p.x - pos.x;
                const dz = p.y - pos.z;
                const dist = Math.hypot(dx, dz);
                if (dist < minDist) {
                    minDist = dist;
                    closestId = locationId;
                }

            });
        });

        return closestId;
    },


    /**
     * Find all lines whose points lie within the given rectangle.
     * @param {WorldRect} rectWorld - Rectangle in world coordinates
     * @returns {string[]} - Array of line IDs
     */
    pickLinesRect(rectWorld) {
        const { x0, z0, x1, z1 } = rectWorld;
        const lineIds = [];

        const sel = treeSelection.at(-1);
        if (sel) {
            const group = map.groups[sel.groupId];
            if (!group) return;
            const layer = group.layers[sel.layerId];
            if (!layer) return;

            Object.entries(layer.lines).forEach(([lineId, line]) => {
                const allInside = line.pointIds.every(guid => {
                    const n = map.points[guid];
                    return n.position.x >= x0 && n.position.x <= x1 &&
                        n.position.z >= z0 && n.position.z <= z1;
                });
                if (allInside) lineIds.push(lineId);
            });
        };

        return lineIds;
    },

    getSelectedPolygon(e) {
        // Convert raw mouse coordinates to canvas pixel coordinates
        const rect = canvasMain.getBoundingClientRect();
        const x = e.clientX - rect.left;
        const y = e.clientY - rect.top;
        const scaleX = canvasMain.width / rect.width;
        const scaleY = canvasMain.height / rect.height;
        const px = Math.floor(x * scaleX);
        const py = Math.floor(y * scaleY);

        // Ensure canvasSelection and pickColorMap are imported at top of file:
        // import { canvasSelection, pickColorMap } from "../../editor/draw.js";

        const ctx = canvasSelection.getContext("2d");
        const data = ctx.getImageData(px, py, 1, 1).data;  // [r, g, b, a]
        const pickColor = `rgb(${data[0]},${data[1]},${data[2]})`;

        // Lookup polygon by color, return null if none
        return pickColorMap[pickColor] || null;
    },

    /**
     * Find the closest branch (line) to the given position.
     * @param {{x: number, z: number}} pos - Position to test
     * @returns {string | null} - Closest line ID or null
     */
    getClosestBranch(pos) {
        // console.log('getClosestBranch called with pos:', pos);
        //  const layer = map.groups[groupId]?.layers[layerId];
        //  if (!layer) {
        //      //  console.log('No layer found, returning null');
        //      return null;
        //  }
        const transform = navigation.getTransform();
        const pickPx = map.settings.selectionRadius > 0 ? map.settings.selectionRadius : 8;
        const tolWorld = pickPx / transform.scale;
        const tolSq = tolWorld * tolWorld;
        //  console.log('Tolerance squared:', tolSq);

        let bestId = null;
        let bestDistSq = tolSq;
        let bestSegs = Infinity;
        treeSelection.forEach(sel => {
            const group = map.groups[sel.groupId];
            if (!group) return;
            const layer = group.layers[sel.layerId];
            if (!layer) return;
            Object.entries(layer.lines).forEach(([id, line]) => {
                const pts = line.pointIds.map(nid => map.points[nid]);
                if (pts.length < 2) return;

                for (let i = 0; i < pts.length - 1; i++) {
                    const a = pts[i].position;
                    const b = pts[i + 1].position;
                    const distSq = this.pointToSegmentDistanceSq(pos.x, pos.z, a.x, a.z, b.x, b.z);

                    if (
                        distSq < bestDistSq - 1e-9 ||
                        (Math.abs(distSq - bestDistSq) < 1e-9 && pts.length < bestSegs)
                    ) {
                        bestDistSq = distSq;
                        bestId = id;
                        bestSegs = pts.length;
                    }
                }
            });
        });

        // console.log('getClosestBranch returning:', bestId);
        return bestId;
    },

    /**
     * Find the closest point on the given branches (lines).
     * @param {{x: number, z: number}} pos - Position to test
     * @param {string[]} lineIds - Array of line IDs to test
     * @param {number} Dlimit - Distance limit from line ends (default 0)
     * @returns {ClosestPointResult | null}
     */
    getClosestPointOnBranches(pos, lineIds, Dlimit = 0) {
        let best = {
            distance: Infinity,
            x: 0, z: 0,
            segmentIndex: -1,
            t: 0,
            lineId: ''
        };

        treeSelection.forEach(sel => {
            const group = map.groups[sel.groupId];
            if (!group) return;
            const layer = group.layers[sel.layerId];
            if (!layer) return;

            lineIds.forEach(lineId => {
                if (!lineId) return;
                const line = layer.lines[lineId];

                const points = line.pointIds.map(id => map.points[id]);
                if (points.length <= 1) return;

                for (let i = 0; i < points.length - 1; i++) {
                    const a = points[i].position;
                    const b = points[i + 1].position;

                    const proj = this.projectPointToSegment(pos.x, pos.z, a.x, a.z, b.x, b.z);

                    if (Dlimit > 0) {
                        const segLen = Math.hypot(b.x - a.x, b.z - a.z);
                        const tol = segLen > 0 ? Dlimit / segLen : 0;
                        proj.t = Math.min(1 - tol, Math.max(tol, proj.t));
                        proj.x = a.x + (b.x - a.x) * proj.t;
                        proj.z = a.z + (b.z - a.z) * proj.t;
                    }

                    const dx = pos.x - proj.x;
                    const dz = pos.z - proj.z;
                    const dist = Math.hypot(dx, dz);

                    if (dist < best.distance) {
                        best = {
                            distance: dist,
                            x: proj.x,
                            z: proj.z,
                            segmentIndex: i,
                            t: proj.t,
                            lineId
                        };
                    }
                }
            });
        });

        return best;
    },

    /**
     * Calculate squared distance from point to line segment.
     * @param {number} px - Point x coordinate
     * @param {number} pz - Point z coordinate
     * @param {number} x1 - Segment start x
     * @param {number} z1 - Segment start z
     * @param {number} x2 - Segment end x
     * @param {number} z2 - Segment end z
     * @returns {number} - Squared distance
     */
    pointToSegmentDistanceSq(px, pz, x1, z1, x2, z2) {
        const vx = x2 - x1, vz = z2 - z1;
        const wx = px - x1, wz = pz - z1;
        const c1 = vx * wx + vz * wz;
        const c2 = vx * vx + vz * vz;
        let t = c2 > 0 ? c1 / c2 : 0;
        t = Math.max(0, Math.min(1, t));
        const projx = x1 + t * vx;
        const projz = z1 + t * vz;
        const dx = px - projx;
        const dz = pz - projz;
        return dx * dx + dz * dz;
    },

    /**
     * Project a point onto a line segment.
     * @param {number} px - Point x coordinate
     * @param {number} pz - Point z coordinate
     * @param {number} x1 - Segment start x
     * @param {number} z1 - Segment start z
     * @param {number} x2 - Segment end x
     * @param {number} z2 - Segment end z
     * @returns {ProjectionResult} - Projected point and parameter t
     */
    projectPointToSegment(px, pz, x1, z1, x2, z2) {
        const vx = x2 - x1, vz = z2 - z1;
        const wx = px - x1, wz = pz - z1;
        const c1 = vx * wx + vz * wz;
        const c2 = vx * vx + vz * vz;
        let t = c2 > 0 ? c1 / c2 : 0;
        t = Math.max(0, Math.min(1, t));

        return {
            x: x1 + t * vx,
            z: z1 + t * vz,
            t
        };
    }
};

export function computeCentroid(points) {
    const sum = { x: 0, y: 0, z: 0 };

    points.forEach(point => {
        sum.x += point.position.x;
        sum.y += point.position.y;
        sum.z += point.position.z;
    });

    const count = points.length;
    return {
        x: sum.x / count,
        y: sum.y / count,
        z: sum.z / count
    };
}


export function snap(v) {
    if (!isSnap) return v;
    // Use custom gridSize if defined and positive, otherwise default SNAP_TRANSLATE
    const step = (map.settings.gridSize > 0)
        ? Math.abs(map.settings.gridSize)
        : 1;
    return Math.round(v / step) * step;
}
