

import { PointModel, LineModel, generateShortGuid } from '../models/models.js';   // assumes PointModel is already imported somewhere
import { Vec3 } from '../../shared/models.js';   // assumes PointModel is already imported somewhere
import { map } from '../editor.js';
import { buildMap, getSnapPointId } from '../draw.js';
import { cloneBranch } from './utils.js';
import { FSM_STATES, setFSMState } from './../editing/state.js';
import { forceEdgeSelection } from '../UI/toolsMenu.js';
import { clearSelectionUndoRedo, treeSelection } from '../selection.js';


/**
 * Factory for an "Add Door" command.
 * Instead of inserting a single point, this will:
 *  • Insert two points at position1 and position2 on the same segment
 *  • Split the original line in two at those points
 *  • Create a new "door" line whose only endpoints are those two points
 *
  * @param {object} splineTransform – { segmentIndex, x, z } telling which segment
 * @param {{ x:number, z:number }} position1 – world-coords for the first door point
 * @param {{ x:number, z:number }} position2 – world-coords for the second door point
 * @returns {{ do():void, undo():void }}
 */
export function addDoorCommand(splineTransform, position1, position2) {
    // Store a snapshot of the original line point models for restoration on undo
    let linesToDelete = [];
    let affectedBranches = {};

    return {
        do() {
            if (!position1 || !position2) return;
            if (!treeSelection || treeSelection.length === 0) return;
            const sel = treeSelection.at(-1);

            const group = map.groups[sel.groupId];
            if (!group) return;

            const layer = group.layers[sel.layerId];
            if (!layer) return;

            const line = layer.lines[splineTransform.lineId];
            if (!line) return;

            affectedBranches[splineTransform.lineId] = cloneBranch(line);
            //  linesToDelete.push(splineTransform.lineId);


            // 2) Create Node A and Node B at the provided positions, with explicit IDs

            let newPointIdB = getSnapPointId( position1 );
            
            if (!newPointIdB) {
                const nodeB = new PointModel();
                nodeB.groupId = sel.groupId;
                nodeB.layerId = sel.layerId;
                nodeB.position = new Vec3({ x: position1.x, y: 0, z: position1.z });
                  newPointIdB = generateShortGuid();
                map.points[newPointIdB] = nodeB;
            }

            const nodeCenter = new PointModel();
            nodeCenter.groupId = sel.groupId;
            nodeCenter.layerId = sel.layerId;
            nodeCenter.position = new Vec3({ x: splineTransform.x, y: 0, z: splineTransform.z });

            const newPointIdCenter = generateShortGuid();
            map.points[newPointIdCenter] = nodeCenter;

            let newPointIdA = getSnapPointId( position2 );
            if (!newPointIdA) {
                const nodeA = new PointModel();
                nodeA.groupId = sel.groupId;
                nodeA.layerId = sel.layerId;
                nodeA.position = new Vec3({ x: position2.x, y: 0, z: position2.z });
                newPointIdA = generateShortGuid();
                map.points[newPointIdA] = nodeA;
            }

            // 3) Insert both points into line.points at the correct indices.
            const i = splineTransform.segmentIndex + 1;
            line.pointIds.splice(i, 0, newPointIdA, newPointIdB);

            // 4) Split into raw halves (arrays of PointModel)
            const rawFirstNodeIds = line.pointIds.slice(0, i + 1);
            const rawSecondNodeIds = line.pointIds.slice(i + 1);

            // 5) Build the first half: keep the original PointModel instances so their
            //    associatedLines arrays (and any external references) remain intact.
            line.pointIds = rawFirstNodeIds.slice();


            // Make sure every point in the first-half line knows about this line

            // 6) Create line for secondHalf with existing points (if ≥ 2)
            if (rawSecondNodeIds.length >= 2) {
                const newBranch = new LineModel();
                newBranch.styleId = line.styleId;
                const secondHalfBranchKey = generateShortGuid();

                // Use the existing PointModel instances for the second half (includes nodeB)
                newBranch.pointIds = rawSecondNodeIds.slice();
                layer.lines[secondHalfBranchKey] = newBranch;

                linesToDelete.push(secondHalfBranchKey);
            }

            // 7) Create the "door" line that connects nodeA ↔ nodeB
            const doorBranch = new LineModel();
            doorBranch.styleId = 'none';

            const doorBranchKey = generateShortGuid();

            doorBranch.isDoor = true;
            doorBranch.pointIds = [newPointIdA, newPointIdCenter, newPointIdB];
            layer.lines[doorBranchKey] = doorBranch;

            linesToDelete.push(doorBranchKey);


            // 8) Select the new door points so UI can show them
            // setPointId(nodeA);
            // setPointId(nodeB);

            // 9) Re-run any spatial processing (e.g. polygon extraction)
         //   purgeBrokenPolygonsOnLayer(layer);//remove broken plolygons affected by the door adding, the issue is that access point are not added to the polygon if door added after polygon, this is why polygon is dismissed forcing used to create a new one.

            buildMap();

            clearSelectionUndoRedo();
            forceEdgeSelection();
            setFSMState(FSM_STATES.IDLE);

        },

        undo() {
            // 1) Remove door line entirely
            if (!treeSelection || treeSelection.length === 0) return;
            const sel = treeSelection.at(-1);

            const group = map.groups[sel.groupId];
            if (!group) return;

            const layer = group.layers[sel.layerId];
            if (!layer) return;

            Object.entries(affectedBranches).forEach(([gId, line]) => {
                layer.lines[gId] = line;
            });

            linesToDelete.forEach(lineId => {
                delete layer.lines[lineId];
            })

            buildMap();

            clearSelectionUndoRedo();
            setFSMState(FSM_STATES.IDLE);

        },

        redo() {
            //  clearSelectionUndoRedo();
            this.do();
        }
    };
}




/**
 * Build an adjacency map of point‑id → Set<neighbour‑id>
 * from layer.lines.  O(E) where E = total segments.
 */
function buildNeighboursMap(branchesMap = {}) {
    const neighbours = new Map();
    for (const line of Object.values(branchesMap)) {
        const pts = line?.pointIds;
        if (!Array.isArray(pts) || pts.length < 2) continue;
        for (let i = 0; i < pts.length - 1; i++) {
            const a = pts[i], b = pts[i + 1];
            if (!neighbours.has(a)) neighbours.set(a, new Set());
            if (!neighbours.has(b)) neighbours.set(b, new Set());
            neighbours.get(a).add(b);
            neighbours.get(b).add(a);
        }
    }
    return neighbours;
}

/**
 * Validate that a PolygonModel’s point sequence still forms a closed ring.
 * A ring is valid when:
 *   • At least three unique vertices
 *   • First and last id are identical (explicit closure)
 *   • Every consecutive pair of ids is connected by a segment
 *     present in `branchesMap` (layer.lines).
 *
 * @param {PolygonModel} polygon
 * @param {object} branchesMap   Map of lineId → { pointIds:[…] }
 * @param {Map|null} neighbourMap Optional precomputed adjacency map
 * @returns {boolean}
 */
export function polygonFormsClosedRing(
    polygon,
    branchesMap = {},
    neighbourMap = null
) {
    const ids = polygon?.pointIds;
    if (!Array.isArray(ids) || ids.length < 4) return false;   // need 3+ distinct + repeat

    // 1️⃣ explicit closure
    if (ids[0] !== ids[ids.length - 1]) return false;

    // 2️⃣ no duplicate vertices inside (besides closure)
    const innerIds = ids.slice(0, -1);
    const unique = new Set(innerIds);
    if (unique.size !== innerIds.length) return false;

    // 3️⃣ adjacency check
    const neighbours = neighbourMap ?? buildNeighboursMap(branchesMap);
    for (let i = 0; i < ids.length - 1; i++) {
        const a = ids[i], b = ids[i + 1];
        if (!(neighbours.get(a)?.has(b))) return false;
    }
    return true;
}




// Helper for cleaning up broken polygons
/**
 * Remove polygons from layer.polygons that do not form valid closed rings.
 * @param {object} layer
 * @returns {string[]} removed polygon IDs
 */
export function purgeBrokenPolygonsOnLayer(layer) {
    const removed = [];
    const branchesMap = layer.lines ?? {};
    const neigh = buildNeighboursMap(branchesMap);

    for (const [polyId, polygon] of Object.entries(layer.polygons)) {
        if (!polygonFormsClosedRing(polygon, branchesMap, neigh)) {
            delete layer.polygons[polyId];
            removed.push(polyId);
        }
    }
    return removed;
}

