import { getMap } from "./cloud.js";
import { initSideMenuUI } from "./UI/sideMenuUI.js";
import { initCanvas, refreshViewport, buildMap } from "./draw.js";

import { initToolsMenu } from "./UI/toolsMenu.js";       // UI buttons,  layer +/- logic
import { initMap, setMap, map } from "./editor.js";       // UI buttons,  layer +/- logic
import { initEditing } from "./editing/editing.js";       // UI buttons,  layer +/- logic

export class widget {
  constructor(root, opts) {
    this.root = root;          // <div> host element
    this.opts = opts;
    // injectIndoorLensCSS().then(() => {

    // build all DOM elements inside the host and get references
    const refs = buildIndoorLensDOM(this.root);

    this.loadingOverlay = refs.loadingOverlay;
    this.deadOverlay = refs.deadOverlay;

    const mapid = opts.mapid;

    console.log("Loading map with id:", mapid);
    if (mapid) {
      const loadingOverlay = document.getElementById("loadingOverlay");
      const deadOverlay = document.getElementById("deadOverlay");
      loadingOverlay?.classList.remove("hidden");
      deadOverlay?.classList.add("hidden");

      getMap({
        mapid,
        onSuccess: (mapJson) => {
          setMap(mapJson);
          console.log("Successfully loaded, map id:", mapid);
          loadingOverlay?.classList.add("hidden");
        },
        onError: e => {
          console.log("Could not load map, a new map is created", mapid);
          console.log("why ", e);
          initMap(mapid);
        }
      })
        .finally(() => {

          deadOverlay?.classList.add("hidden");



          // Wait one frame so the flex layout settles *and* we know the real dimensions
          requestAnimationFrame(() => {
            // Now that layout is correct, init canvases and editors
            initCanvas(
              refs.canvasBackground,
              refs.canvasSelection,
              refs.canvasMain,
              refs.canvasOverlay,
              refs.canvasGrid
            );


            initEditing(refs.canvasMain, refreshViewport);

            // Build static side panels while overlay is still up
            initSideMenuUI();
            initToolsMenu();

            buildMap();
            refreshViewport();
            loadingOverlay?.classList.add("hidden");
          });
        });
    }
    //  });

  }
}

function injectIndoorLensCSS() {
  return new Promise((resolve, reject) => {
    // Skip if the sheet is already present
    if (document.getElementById('indoorlens-css')) return resolve();

    const link = document.createElement('link');
    link.id = 'indoorlens-css';
    link.rel = 'stylesheet';
    link.href = '/dist/indoorlens-editor.css';
    link.onload = () => resolve();
    link.onerror = reject;
    document.head.appendChild(link);
  });
}




