import { map } from "./editor.js";
import {  PolygonModel } from "./models/models.js";
import { treeSelection} from "./selection.js";
 

// -----------------------------
// Geometry helpers
// -----------------------------

// 2‑D point‑in‑polygon (XZ‑plane) using the even‑odd rule
export function isPointInPolygon(pt, poly) {
    const x = pt.position ? pt.position.x : pt.x;
    const y = pt.position ? pt.position.z : pt.z;

    let inside = false;
    for (let i = 0, j = poly.length - 1; i < poly.length; j = i++) {
        const pi = poly[i].position ? { x: poly[i].position.x, y: poly[i].position.z }
            : { x: poly[i].x, y: poly[i].z };
        const pj = poly[j].position ? { x: poly[j].position.x, y: poly[j].position.z }
            : { x: poly[j].x, y: poly[j].z };

        const intersect = (pi.y > y) !== (pj.y > y) &&
            x < (pj.x - pi.x) * (y - pi.y) / (pj.y - pi.y) + pi.x;
        if (intersect) inside = !inside;
    }
    return inside;
}

// --------------------------------------------------
// Point-to-segment distance² (XZ plane)
function dist2ToSegment(p, a, b) {
    const px = p.x, pz = p.z;
    const ax = a.x, az = a.z;
    const bx = b.x, bz = b.z;

    const vx = bx - ax;
    const vz = bz - az;
    const wx = px - ax;
    const wz = pz - az;

    const c1 = vx * wx + vz * wz;
    if (c1 <= 0) return (px - ax) ** 2 + (pz - az) ** 2;

    const c2 = vx * vx + vz * vz;
    if (c2 <= c1) return (px - bx) ** 2 + (pz - bz) ** 2;

    const t = c1 / c2;
    const projX = ax + t * vx;
    const projZ = az + t * vz;
    return (px - projX) ** 2 + (pz - projZ) ** 2;
}

// --------------------------------------------------
// “Inside OR on edge” test
const EPS2 = 1e-6;                     // 1 mm in world units (adjust if needed)
export function isPointInOrOnPolygon(pt, poly) {
    if (isPointInPolygon(pt, poly)) return true;   // strictly inside

    // otherwise, check if it sits on any edge
    for (let i = 0; i < poly.length; i++) {
        const a = poly[i].position ?? poly[i];
        const b = poly[(i + 1) % poly.length].position ?? poly[(i + 1) % poly.length];
        if (dist2ToSegment(
            pt.position ?? pt,
            { x: a.x ?? a.position.x, z: a.z ?? a.position.z },
            { x: b.x ?? b.position.x, z: b.z ?? b.position.z }
        ) < EPS2) {
            return true;          // lies on the border
        }
    }
    return false;
}



// --------------------------------------------------
// Strict‑containment test:
//  • every vertex of `inner` is in/on `outer`
//  • at least one vertex lies strictly inside
// --------------------------------------------------
export function polygonStrictlyContainsPolygon(outer, inner) {
    let anyStrict = false;
    for (const v of inner) {
        if (!isPointInOrOnPolygon(v, outer)) return false;
        if (isPointInPolygon(v, outer)) anyStrict = true;
    }
    return anyStrict;
}


/**
 * Add a polygon if it doesn’t already exist.
 * @param {string[]} pointIds – GUIDs of the polygon’s perimeter points, in any order.
 * @returns {string|null}    – Room GUID (existing or newly created), or null if
 *                              the current group / layer is missing.
 */
export function tryGeneratePolygon(lineIds, styleId) {
   // const newRoomId =null;
    if (!treeSelection || treeSelection.length === 0) return;
    const sel = treeSelection.at(-1);

    const group = map.groups[sel.groupId];
    if (!group) return;

    const layer = group.layers[sel.layerId];
    if (!layer) return;
 
    let pointIds = stitchBranchesIntoRing(lineIds, layer.lines);
    if (!pointIds) {
        console.error("Cannot form a closed loop from selected lines:", lineIds);
        return;
    }

    const canonicalKey = pointIds.slice().sort().join("|");
    const eventualRoomId = generatePolygonIdFromCanonicalKey(canonicalKey);

    layer.polygons ??= {};                       // ensure dict exists
    layer.polygons[eventualRoomId] = new PolygonModel(pointIds, styleId, true);
 
}



/**
 * Stitches the selected line branches into a single closed ring.
 * Works even when the user picks lines in random order or when some
 * line point arrays are reversed.
 *
 * @param {string[]} lineIds
 * @param {object}   lines              layer.lines dictionary
 * @returns {string[]|null}             ordered pointIds ending with the
 *                                      same id that starts the array,
 *                                      or null if the lines do not form
 *                                      ONE simple, closed loop.
 */
function stitchBranchesIntoRing(lineIds, lines) {
    if (!Array.isArray(lineIds) || lineIds.length === 0) return null;

    // ===== Fast path: single line – always make a ring if it has ≥ 3 points =====
    if (lineIds.length === 1) {
        const pts = lines[lineIds[0]]?.pointIds;
        if (pts && pts.length >= 3) {
            const ring = pts.slice();                 // copy
            if (ring[0] !== ring[ring.length - 1]) {
                ring.push(ring[0]);                   // close the loop
            }
            return ring;                              // no further processing needed
        }
        // fall through if fewer than 3 points
    }

    // 1) Build an undirected adjacency list (pointId -> [neighbourIds])
    const graph = new Map();
    for (const lid of lineIds) {
        const pts = lines[lid]?.pointIds;
        if (!pts || pts.length < 2) return null;

        for (let i = 0; i < pts.length - 1; i++) {
            const a = pts[i], b = pts[i + 1];
            if (!graph.has(a)) graph.set(a, []);
            if (!graph.has(b)) graph.set(b, []);
            graph.get(a).push(b);
            graph.get(b).push(a);
        }
    }

    // 2) Every vertex in a simple closed loop must have degree 2
    for (const [pid, neighbours] of graph) {
        if (neighbours.length !== 2) return null;
    }

    // 3) Walk the ring once to produce an ordered list of pointIds
    const start = graph.keys().next().value;
    const ring = [start];

    let prev = null;
    let current = start;

    while (true) {
        const neighbours = graph.get(current);
        // pick the neighbour that is NOT the vertex we just came from
        const next = neighbours[0] === prev ? neighbours[1] : neighbours[0];

        if (next === start) {          // completed the loop
            ring.push(start);
            break;
        }
        if (ring.includes(next)) {     // encountered a visited vertex too early -> self‑intersection
            return null;
        }
        ring.push(next);
        prev = current;
        current = next;

        // Safety guard to avoid infinite loops
        if (ring.length > graph.size + 1) return null;
    }

    return ring;
}

 

/**
 * FNV-1a 32-bit hash, output in base-36.
 * @param {string} str – your long polygon key (e.g. "11845c97|37032f50|…")
 * @returns {string}   – a short, “random-looking” string like "k9f3zt"
 */
function generatePolygonIdFromCanonicalKey(str) {
    let hash = 0x811c9dc5;            // FNV offset basis (2166136261)
    for (let i = 0; i < str.length; i++) {
        hash ^= str.charCodeAt(i);
        // FNV prime = 16777619
        hash = (hash * 0x01000193) >>> 0;
    }
    // toString(36) gives 0–9a–z and is much shorter than hex
    return hash.toString(36);
}

 


