import { map } from "../editor.js";
import { BuildNavigationTree } from "./sideMenuUI.js";
import { treeSelection, setLocationId } from "../selection.js";
import { showInfo } from "../dialogBox.js";   // safe‑optional

export function initGroupUI() {

  const deleteGroup = document.getElementById('deleteGroup');
  deleteGroup.addEventListener("click", (evt) => {
    const groupId = currentGroupId();
    if (!groupId) return null;

    showInfo(
      "Are you sure you want to delete this group?",
      () => {
        removeGroup(groupId);
        BuildNavigationTree();
        updateGroupUI();
      },
      null
    );


  });

  const groupName = document.getElementById('groupName');
  groupName.style.marginBottom = '8px';
  groupName.addEventListener("input", (evt) => {
    const group = currentGroup()
    if (!group) return null;
    group.name = evt.target.value;
    BuildNavigationTree();
    updateGroupUI();
  });

  const groupType = document.getElementById('groupType');
  groupType.addEventListener("change", (evt) => {
    const group = currentGroup()
    if (!group) return null;
    group.type = evt.target.value;
    BuildNavigationTree();
    updateGroupUI();
  });

  const groupMetaInput = document.getElementById('groupMetaInput');
  groupMetaInput.style.marginBottom = '8px';
  groupMetaInput.addEventListener("input", (evt) => {
    const group = currentGroup()
    if (!group) return null;
    group.meta = evt.target.value;

    updateGroupUI();
  });


}

export function updateGroupUI() {
  const group = currentGroup()
  if (!group) return null;
  const groupName = document.getElementById('groupName');
  groupName.value = group.name;

  const groupMetaInput = document.getElementById('groupMetaInput');
  groupMetaInput.value = group.meta;

  const groupType = document.getElementById('groupType');
  groupType.value = group.type;

}



function currentGroup() {
  if (!treeSelection || treeSelection.length === 0) return null;
  const sel = treeSelection.at(-1);

  const group = map.groups[sel.groupId];
  if (!group) return null;
  return group
}

function currentGroupId() {
  if (!treeSelection || treeSelection.length === 0) return null;
  const sel = treeSelection.at(-1);


  if (sel === -1) return null;
  return sel.groupId;
}







function removeGroup(gId) {
  // Remove the group
  if (gId && map.groups[gId]) {
    delete map.groups[gId];
  }
  // Remove any points belonging to the deleted group
  Object.keys(map.points).forEach(pId => {
    if (map.points[pId].groupId === gId) {
      delete map.points[pId];
    }
  });

  // Remove any location belonging to the deleted group
  Object.keys(map.locations).forEach(lId => {
    if (map.locations[lId].groupId === gId) {
      delete map.locations[lId];
    }
  });

  setLocationId(null);
  treeSelection.length = 0;
}



