/** give the module a canva  let circlePos = null;s *after* it exists in the DOM */
export function initCanvas(_canvasBackground, _canvasSelection, _canvasMain, _canvasOverlay, _canvasGrid) {
  canvasBackground = _canvasBackground;
  canvasSelection = _canvasSelection;
  canvasMain = _canvasMain;
  canvasOverlay = _canvasOverlay;
  canvasGrid = _canvasGrid;

  bctx = canvasBackground.getContext("2d");
  sctx = canvasSelection.getContext("2d");
  ctx = canvasMain.getContext("2d");
  octx = canvasOverlay.getContext("2d");
  gctx = canvasGrid.getContext("2d");


  if (document.readyState === 'complete') {
    resizeAllCanvases();
  } else {
    window.addEventListener('load', resizeAllCanvases, { once: true });
  }

  // Clear any previous overlay content
  bctx.clearRect(0, 0, canvasBackground.width, canvasBackground.height);
  sctx.clearRect(0, 0, canvasSelection.width, canvasSelection.height);
  gctx.clearRect(0, 0, canvasGrid.width, canvasGrid.height);
  octx.clearRect(0, 0, canvasOverlay.width, canvasOverlay.height);


  window.addEventListener("resize", resizeAllCanvases);

  const selecetdImg = new Image();
  selecetdImg.src = "/Images/Pin.png";                 // absolute path avoids relative issues
  iconCache.selectedLocation = selecetdImg;
  const standbyImg = new Image();
  standbyImg.src = "/Images/StandbyLocation.png";
  iconCache.standbyLocation = standbyImg;
  const keyImg = new Image();
  keyImg.src = "/Images/Key.png";
  iconCache.accessPoint = keyImg;
  const portalImg = new Image();
  portalImg.src = "/Images/Portal.png";
  iconCache.portal = portalImg;

  /* --------------------------------------------------------------
     Spawn map centred in the canvas on first load
  ----------------------------------------------------------------*/
  const dpr = window.devicePixelRatio || 1;
  const rect = canvasMain.getBoundingClientRect();

  // Buffer‑pixel half‑size of the canvas
  const screenCX = (rect.width * dpr) / 2;
  const screenCY = (rect.height * dpr) / 2;

  // World‑space centre of the current map


  // Translate so that world‑centre appears at screen‑centre
  const initialPan = {
    dx: screenCX,
    dy: screenCY
  };

  // Create the navigation with that initial pan
  navigation = createNavigator(canvasMain, {
    initialPan,
    onUpdate: refreshViewportPure
  });
}


// Your canvas drawing function
function refreshViewportPure(viewTransform) {
  // Sync legacy globals for code that still reads them
  scale = viewTransform.scale;
  pan.x = viewTransform.dx;
  pan.y = viewTransform.dy;
  // Clear canvas

  backgroundCanvasNav();
  rgbSelectionCanvasNav();
  mainCanvasNav();
  gridCanvasNav();

  //overlayCanvasNav();
}

export function refreshViewport() {
  // Clear canvas
  if (!navigation) return;
  const viewTransform = navigation.getTransform();
  if (!viewTransform) return;
  // Sync legacy globals for code that still reads them
  scale = viewTransform.scale;
  pan.x = viewTransform.dx;
  pan.y = viewTransform.dy;

  backgroundCanvasNav();
  rgbSelectionCanvasNav();
  mainCanvasNav();
  gridCanvasNav();
  //overlayCanvasNav();
}
export function resetViewport() {
  const container = canvasMain.parentElement;
  const rect = container.getBoundingClientRect();
  const newW = rect.width * (window.devicePixelRatio || 1);
  const newH = rect.height * (window.devicePixelRatio || 1);

  canvasMain.width = newW;
  canvasMain.height = newH;

  requestAnimationFrame(() => {
    // … your existing scale/dx/dy math and navigation.setTransform() …
    centerView(10);
  });

}

