export function buildMap() {
  // Clear all points' associated lines in one pass
  for (const point of Object.values(map.points)) {
    if (point) point.associatedLines = [];
  }

  // Predefine style categories
  const lineStyleIds = ['none', 'innerwall', 'outerwall'];
  const polyStyleIds = ['floor', 'public', 'private'];
  // grid = new Map();
  // Initialize layers for every layer
  for (const group of Object.values(map.groups)) {
    if (!group) continue;
    for (const layer of Object.values(group.layers)) {
      if (!layer) continue;
      layer.linesByStyle = {};
      for (const style of lineStyleIds) {
        layer.linesByStyle[style] = [];
      }
      layer.polygonsByStyle = {};
      for (const style of polyStyleIds) {
        layer.polygonsByStyle[style] = [];
      }
    }
  }

  // Populate layers and build associations
  for (const [groupId, group] of Object.entries(map.groups)) {
    if (!group) continue;
    for (const [layerId, layer] of Object.entries(group.layers)) {
      if (!layer) continue;

      // Lines
      for (const [lineId, line] of Object.entries(layer.lines || {})) {
        if (!line) continue;
        for (const pid of line.pointIds) {
          const pt = map.points[pid];
          pt.groupId = groupId;
          pt.layerId = layerId;
          //  if (!pt.layerIds.includes(layerId)) pt.layerIds.push(layerId);
          if (pt && !pt.isLocation) {
            pt.associatedLines.push(lineId);
            // if (group.enabled && layer.enabled) {
            //   gridInsert(pid);
            // }
          }
        }
        const styleLineIds = layer.linesByStyle[line.styleId];
        if (styleLineIds) styleLineIds.push(lineId);
      }

      // Polygons
      for (const [polyId, poly] of Object.entries(layer.polygons || {})) {
        const stylePolygonIds = layer.polygonsByStyle[poly.styleId];
        if (stylePolygonIds) stylePolygonIds.push(polyId);
      }
    }

    //  console.log("rebuilt map "+ JSON.stringify(map));
  }
}

function refreshGrid() {
  grid = new Map();
  treeSelection.forEach(sel => {
    if (!sel.groupId) return;
    const group = map.groups[sel.groupId];
    if (!sel.layerId) return;
    const layer = group.layers[sel.layerId];
    Object.entries(layer.lines ?? {}).forEach(([lineId, line]) => {
      if (!line) return;
      line.pointIds.forEach(pid => {
        const point = map.points[pid];
        if (!point) return;
        gridInsert(pid);
      });
    });
  });

}

