/**
 * Ray‑casting point‑in‑polygon test on the X‑Z plane.
 * Returns true when the given world‑space point lies inside the polygon.
 *
 * @param {{x:number, z:number}} point  – mousePos in world units
 * @param {Array} poly                  – array of PointModel or {x,z} verts
 */
export function isPointInPoly(point, poly) {
  if (!point) return false;
  const px = point.x;
  const py = point.z ?? point.y;

  let inside = false;
  for (let i = 0, j = poly.length - 1; i < poly.length; j = i++) {
    const pi = poly[i].position ?? poly[i];
    const pj = poly[j].position ?? poly[j];

    const xi = pi.x, yi = pi.z ?? pi.y;
    const xj = pj.x, yj = pj.z ?? pj.y;

    const intersect =
      (yi > py) !== (yj > py) &&
      px < ((xj - xi) * (py - yi)) / (yj - yi) + xi;
    if (intersect) inside = !inside;
  }
  return inside;
}

/* ---------- main draw loop ---------- */
export function drawMap() {
  if (!map.groups) return;          // nothing to draw yet
  if (isPreview) renderLayer();
  else {

    ctx.save();
    //group entourage shape drawing
    Object.entries(map.groups).forEach(([gId, group]) => {
      // if(treeSelection.some(sel=>(sel.groupId!==gId)))return;
      Object.entries(group.layers).forEach(([layId, layer]) => {
        const style = map.styles?.["floor"];
        if (style && !style.enabled) return;

        const stylePolygonIds = layer.polygonsByStyle?.["floor"];
        if (!stylePolygonIds) return;

        stylePolygonIds.forEach(polyId => {
          const polygon = layer.polygons[polyId];
          ctx.fillStyle = groupColor;
          ctx.beginPath();
          polygon?.pointIds.forEach((pId, i) => {
            const point = map.points[pId];
            if (!point) return;
            i === 0 ? ctx.moveTo(point.position.x, point.position.z) : ctx.lineTo(point.position.x, point.position.z);
          });
          ctx.closePath();
          ctx.fill();
        });
      });
    });
    ctx.restore();



    /*-----------Draw selection--------*/

    treeSelection.forEach((sel, i) => {
      const group = map.groups[sel.groupId];
      if (!group) return;
      const layer = group.layers[sel.layerId];
      if (!layer) return;

      if (i === (treeSelection.length - 1)) {
        drawGizmos(layer, true);
        drawLocations(layer);
      } else {
        drawGizmos(layer, false);
      }


    });
  }
}


function renderLayer() {

  ctx.save();
  //group entourage shape drawing
  Object.entries(map.groups).forEach(([gId, group]) => {
    // if(treeSelection.some(sel=>(sel.groupId!==gId)))return;
    Object.entries(group.layers).forEach(([layId, layer]) => {
      const stylePolygonIds = layer.polygonsByStyle?.["floor"];
      if (!stylePolygonIds) return;

      stylePolygonIds.forEach(polyId => {
        const polygon = layer.polygons[polyId];
        ctx.fillStyle = groupColor;
        ctx.beginPath();
        polygon.pointIds.forEach((pId, i) => {
          const point = map.points[pId];
          i === 0 ? ctx.moveTo(point.position.x, point.position.z) : ctx.lineTo(point.position.x, point.position.z);
        });
        ctx.closePath();
        ctx.fill();
      });
    });
  });
  ctx.restore();

  if (!treeSelection || treeSelection.length === 0) return;
  const sel = treeSelection.at(-1);

  const group = map.groups[sel.groupId];
  if (!group) return;

  const layer = group.layers[sel.layerId];
  if (!layer) return;

  styleOrder.forEach(styleId => {
    if (styleId === 'none') return;
    const stylePolygonIds = layer.polygonsByStyle?.[styleId];
    if (!stylePolygonIds) return;

    const style = map.styles?.[styleId];
    // restore real fillStyle below
    if (style && !style.enabled) return;
    ctx.fillStyle = style.color;

    stylePolygonIds.forEach(polygonId => {
      const polygon = layer.polygons[polygonId];
      if (!polygon) return;


      ctx.beginPath();
      polygon.pointIds.map(g => map.points[g]).forEach((p, i) => {
        const { x, z } = p.position;
        i === 0 ? ctx.moveTo(x, z) : ctx.lineTo(x, z);
      });
      ctx.closePath();
      ctx.fill();
    });
  });

  //ctx.globalAlpha = (currentSelectionMode === SelectionMode.LINE) ? ctx.globalAlpha : 0.3;

  styleOrder.forEach(styleId => {
    if (styleId === 'none') return;
    const styleLineIds = layer.linesByStyle?.[styleId];
    if (!styleLineIds) return;

    //  Object.entries(layer.linesByStyle ?? {}).forEach(([styleId, styleLineIds]) => {
    styleLineIds.forEach(lineId => {
      const line = layer.lines[lineId];
      if (!line) return;

      const style = map.styles?.[styleId];


      if (style && !style.enabled) return;

      const points = line.pointIds.map(id => map.points[id]);
      if (!points) return;
      //---- Segments Drawing---- 
      if (points.length >= 2) {

        ctx.lineWidth = style.lineThickness / scale;
        ctx.strokeStyle = style.color;
        ctx.beginPath();
        ctx.moveTo(points[0].position.x, points[0].position.z);
        points.slice(1).forEach(p => ctx.lineTo(p.position.x, p.position.z));

        ctx.stroke();
      }
    });
  })

}


function drawLocations(layer) {

  if (currentSelectionMode !== SelectionMode.POINT) return;
  ctx.globalAlpha = 1;

  layer.locationIds.forEach(locId => {
    const location = map.locations[locId];
    if (!location) return;

    const locationPoint = map.points[location.pointId];
    if (!locationPoint) return;
    /* choose which icon to draw */
    const icon = (locId === locationId) ? iconCache.selectedLocation : iconCache.standbyLocation;
    if (!icon || !icon.complete) return;   // skip until loaded

    // world → screen transform
    const size = (locId === locationId) ? 30 : 24;
    ctx.save();
    ctx.translate(locationPoint.position.x, locationPoint.position.z);  // move to icon position

    ctx.scale(1 / scale, 1 / scale);

    ctx.drawImage(icon, -size / 2, -size / 2, size, size);
    ctx.restore();

    // LABELS -------------------------------------------------

    const label = location.name ?? location.locationType;
    ctx.save();
    ctx.translate(locationPoint.position.x, locationPoint.position.z);  // move to icon position
    ctx.scale(1 / scale, 1 / scale);

    ctx.font = "20px Arial";
    ctx.textAlign = "center";
    ctx.textBaseline = "middle";
    ctx.fillStyle = "#000";
    ctx.fillText(label, 0, -30);
    ctx.restore();

    if (locId === locationId) {
      location.accessPointIds.forEach(pid => {
        const point = map.points[pid];
        if (!point) return;


        ctx.save();
        ctx.translate(point.position.x + 1, point.position.z);  // move to icon position
        ctx.scale(1 / scale, 1 / scale);
        ctx.drawImage(iconCache.accessPoint, -size / 2, -size / 2, size, size);
        ctx.restore();

        //  if (point.twins && point.twins.length !== 0) {

        //    ctx.save();
        //    ctx.translate(point.position.x, point.position.z );  // move to icon position
        //    ctx.scale(1 / scale, 1 / scale);
        //    ctx.drawImage(iconCache.portal, -size / 2, -size / 2, size, size);
        //    ctx.restore();

        //  }else{

        ctx.fillStyle = pathColor;
        const dotRadius = 6 / scale;
        ctx.beginPath();
        ctx.arc(point.position.x, point.position.z, dotRadius, 0, Math.PI * 2);
        ctx.fill();
        // }



      });
    }

  });
}



function drawGizmos(layer, highlight) {

  ctx.save();

  // const isLineMode = (currentSelectionMode === SelectionMode.LINE);
  const isPointMode = (currentSelectionMode === SelectionMode.POINT);
  // const isPolygonMode = (currentSelectionMode === SelectionMode.POLYGON);

  /*---------Polygons----------*/
  if (!highlight) ctx.globalAlpha = 0.3;
  else ctx.globalAlpha = 1;

  let polygonColor;
  styleOrder.forEach(styleId => {
    const stylePolygonIds = layer.polygonsByStyle?.[styleId];
    if (!stylePolygonIds) return;

    const style = map.styles?.[styleId];

    if (style && !style.enabled) return;
    stylePolygonIds.forEach(polygonId => {
      const polygon = layer.polygons[polygonId];
      if (!polygon) return;

      // ── Choose polygon fill colour ─────────────────────────────────────
      polygonColor = style.color;
      ctx.fillStyle = polygonColor;

      ctx.beginPath();
      polygon.pointIds.map(g => map.points[g]).forEach((p, i) => {
        if (!p) return;
        const { x, z } = p.position;
        i === 0 ? ctx.moveTo(x, z) : ctx.lineTo(x, z);
      });
      // ctx.closePath();
      ctx.fill();
    });
  });

  /*---------Lines----------*/

  let lineColor = standbyLineColor;
  Object.entries(layer.lines ?? {}).forEach(([lineId, line]) => {
    if (!line) return;
    preProcessBranch(layer, lineId, line.pointIds);
    //---- Segments Drawing----
    const points = line.pointIds.map(id => map.points[id]);
    if (points.length >= 2) {

      ctx.strokeStyle = lineColor;
      if (isPointMode && line.styleId === "none") {
        ctx.strokeStyle = pathColor;
      }
      ctx.lineWidth = 3 / scale;
      ctx.beginPath();
      // draw each segment separately
      for (let i = 0; i < points.length - 1; i++) {
        const a = points[i].position;
        const b = points[i + 1].position;

        ctx.moveTo(a.x, a.z);
        ctx.lineTo(b.x, b.z);
        ctx.stroke();
      }
    }


    /*---------Points----------*/
    let pointColor;
    // if (!highlight) ctx.globalAlpha *= 0.5;
    pointColor = standbyPointColor;

    line.pointIds.forEach(pid => {
      const point = map.points[pid];
      if (!point) return;

      const lineCount = Array.isArray(point.associatedLines)
        ? point.associatedLines.length
        : 0;
      const dotRadius =   4  / scale;

      // ── Choose point fill colour ──────────────────────────────────────
      ctx.fillStyle =   (lineCount > 1 )? sharedPointColor : pointColor;
      ctx.beginPath();
      //  ctx.setLineDash([]);
         ctx.arc(point.position.x, point.position.z, dotRadius, 0, Math.PI * 2);
       // ctx.closePath();
      ctx.fill();

      //Bridge Icons
      if (highlight && point.twins && point.twins.length !== 0) {
        ctx.save();
        ctx.translate(point.position.x, point.position.z - 1);  // move to icon position
        ctx.scale(1 / scale, 1 / scale);
        ctx.drawImage(iconCache.portal, -size / 2, -size / 2, size, size);
        ctx.restore();


        ctx.strokeStyle = twinLinkColor;
        ctx.lineWidth = 3 / scale;
        ctx.beginPath();
       
        ctx.setLineDash([0.2, 0.2]);
        // draw each segment separately
        for (let i = 0; i < point.twins.length; i++) {
          const twinPointId = point.twins[i];
          const twinPoint = map.points[twinPointId];
          if (!twinPoint) continue;
          const a = point.position;
          const b = twinPoint.position;

          ctx.moveTo(a.x, a.z);
          ctx.lineTo(b.x, b.z);
          ctx.stroke();
        }
      }
      ctx.setLineDash([]);
      if (isDebug && !point.locationId) { // ---- label just above the dot ----

        const cx = point.position.x;
        const cy = point.position.z;

        ctx.save();
        ctx.translate(cx, cy);  // move to icon position
        ctx.scale(1 / scale, 1 / scale);
        ctx.beginPath();
        ctx.font = "20px Arial";
        ctx.textAlign = "center";
        ctx.textBaseline = "middle";
        ctx.fillStyle = "#000";
        ctx.fillText(lineCount.toString(), cx / scale, (cy / scale) - 15);
        ctx.restore();
      }
    });
  });

  if (highlight) drawSelection(layer);
  /* --- restore the context saved at the very top of drawGizmos() --- */
  ctx.restore();
}

function drawSelection(layer) {
  const isLineMode = (currentSelectionMode === SelectionMode.LINE);
  const isPointMode = (currentSelectionMode === SelectionMode.POINT);
  const isPolygonMode = (currentSelectionMode === SelectionMode.POLYGON);

  ctx.save();
  polygonIds.forEach(polygonId => {
    const polygon = layer.polygons[polygonId];
    if (!polygon) return;

    ctx.globalAlpha = 0.4;
    let color;
    if (isPolygonMode) {
      color = polygonSelectedColor;
    }
    else if (isLineMode) {
      color = polygonSelectedColor;
    }
    else if (isPointMode) {
      if (locationId) {
        const selectedLocation = map.locations[locationId];
        if (selectedLocation && selectedLocation.polygonIds.includes(polygonId)) {
          color = selectedColor;
          ctx.globalAlpha = 0.2;
        }
      }
    }
    ctx.fillStyle = color;
    ctx.beginPath();
    polygon.pointIds.map(g => map.points[g]).forEach((p, i) => {
      if (!p) return;
      const { x, z } = p.position;
      i === 0 ? ctx.moveTo(x, z) : ctx.lineTo(x, z);
    });

    ctx.fill();
  });
  ctx.restore();

  /* --------- Highlight selected lines (single pass) --------- */
  if (lineIds.length && isLineMode) {
    ctx.save();
    ctx.lineWidth = 6 / scale;
    ctx.strokeStyle = selectedColor;

    lineIds.forEach(lId => {
      const selLine = layer.lines[lId];
      if (!selLine) return;
      let color;
      if (isLineMode) {
        ctx.globalAlpha = 1;
        color = selectableColor;                          // regular, selectable node
      }
      else if (isPolygonMode) {
        ctx.globalAlpha = 0.4;
        color = standbyColor;                          // regular, selectable node
        //return;
      }
      else if (isPointMode) {
        color = standbyColor;
        ctx.globalAlpha = 0.4;
      }

      ctx.strokeStyle = color;
      ctx.beginPath();
      const firstPt = map.points[selLine.pointIds[0]];
      if (!firstPt) return;
      ctx.moveTo(firstPt.position.x, firstPt.position.z);

      for (let i = 1; i < selLine.pointIds.length; i++) {
        const p = map.points[selLine.pointIds[i]];
        if (!p) continue;
        ctx.lineTo(p.position.x, p.position.z);
      }
      ctx.stroke();
    });
    ctx.restore();
  }

  /* --------- Highlight selected points (single pass) --------- */
  if (pointIds.length && isPointMode) {
    ctx.save();
    ctx.strokeStyle = selectedColor;
    ctx.lineWidth = 2 / scale;
    const highlightRadius = 10 / scale;
    let color;
    pointIds.forEach(pid => {
      const pt = map.points[pid];
      if (!pt) return;


      if (isPointMode) {
        ctx.globalAlpha = 1;
        color = selectableColor;                          // regular, selectable node
      }
      else if (isLineMode) {
        ctx.globalAlpha = 0.4;
        if (lineCount > 1) {
          color = sharedPointColor;                         // node shared by ≥2 lines
        } else {
          color = standbyColor;
        }
      } else if (isPolygonMode) {
        return;
      }

      ctx.fillStyle = color;
      ctx.beginPath();
      ctx.arc(pt.position.x, pt.position.z, highlightRadius, 0, Math.PI * 2);
      ctx.stroke();

      //  if (isDebug) {
      //    const cx = pt.position.x;
      //    const cy = pt.position.z;
      //
      //    ctx.save();
      //    ctx.translate(cx, cy);  // move to icon position
      //    ctx.scale(1 / scale, 1 / scale);
      //    ctx.beginPath();
      //    ctx.font = "20px Arial";
      //    ctx.textAlign = "center";
      //    ctx.textBaseline = "middle";
      //    ctx.fillStyle = "#000";
      //    ctx.fillText(pid, cx / scale, (cy / scale) - 15);
      //    ctx.restore();
      //  }
    });
    ctx.restore();
  }

}



export function drawRGBSelection() {
  if (!treeSelection || treeSelection.length === 0) return;
  const sel = treeSelection.at(-1);

  const group = map.groups[sel.groupId];
  if (!group) return;

  const layer = group.layers[sel.layerId];
  if (!layer) return;

  // Reset pick map and start index at 1 to avoid rgb(0,0,0)
  pickColorMap = {};
  nextPickIndex = 1;

  //--- Rooms color selection
  styleOrder.forEach(styleId => {
    const stylePolygonIds = layer.polygonsByStyle?.[styleId];
    if (!stylePolygonIds) return;
    stylePolygonIds.forEach(polygonId => {
      const polygon = layer.polygons[polygonId];
      if (!polygon) return;
      // generate a unique pick‐color for this polygon
      const colorNum = nextPickIndex++;
      const r = (colorNum >> 16) & 0xFF;
      const g = (colorNum >> 8) & 0xFF;
      const b = colorNum & 0xFF;
      const pickColor = `rgb(${r},${g},${b})`;

      pickColorMap[pickColor] = polygonId;

      // --- pick‐color pass: draw the same polygon in pickColor ---
      sctx.beginPath();
      polygon.pointIds.map(g => map.points[g])?.forEach((p, i) => {
        if (!p) return;
        const { x, z } = p.position;
        if (i === 0) sctx.moveTo(x, z);
        else sctx.lineTo(x, z);
      });

      sctx.closePath();
      sctx.fillStyle = pickColor;
      sctx.fill();
    });
  });

}

function drawBackground() {
  if (!treeSelection || treeSelection.length === 0) return;
  const sel = treeSelection.at(-1);

  const group = map.groups[sel.groupId];
  if (!group) return;

  const layer = group.layers[sel.layerId];
  if (!layer) return;

  bctx.globalAlpha = layer.backgroundOpacity;

  if (
    backgroundImage &&
    backgroundImage.complete &&
    backgroundImage.naturalWidth > 0 &&
    layer &&
    layer.backgroundWorldWidth &&
    layer.backgroundWorldHeight
  ) {
    const worldW = layer.backgroundWorldWidth;
    const worldH = layer.backgroundWorldHeight;

    // Since we've already translated to (pivotX, pivotY) → world(0,0) in buffer‐pixels,
    // drawing at (−worldW/2, −worldH/2) places the image's center at world(0,0).
    bctx.drawImage(
      backgroundImage,
      -worldW / 2,
      -worldH / 2,
      worldW,
      worldH
    );
  }


}


