export function drawGrid() {
  // const { scale, dx, dy } = viewTransform;

  /* ---------- canvas / dpi ---------- */
  const dpr = window.devicePixelRatio || 1;
  const cwBuf = canvasGrid.width;   // buffer-px width  (already ×dpr)
  const chBuf = canvasGrid.height;  // buffer-px height

  /* ---------- clear layer ---------- */
  gctx.setTransform(1, 0, 0, 1, 0, 0);
  gctx.clearRect(0, 0, cwBuf, chBuf);

  /* ---------- figure out world bounds now visible ----------
     screenX = worldX * scale + dx     (all in *buffer* pixels)
     → worldX = (screenX − dx) / scale
  ----------------------------------------------------------- */
  const invS = 1 / scale;
  const minX = Math.floor((-pan.x) * invS);
  const maxX = Math.ceil((cwBuf - pan.x) * invS);
  const minY = Math.floor((-pan.y) * invS);
  const maxY = Math.ceil((chBuf - pan.y) * invS);

  /* ---------- apply the pan/zoom so we can draw in world units ---------- */
  gctx.setTransform(scale, 0, 0, scale, pan.x, pan.y);

  /* ---------- grid style & fade ---------- */
  const FADE_START = 4, FADE_END = 6;
  let fade = (scale - FADE_START) / (FADE_END - FADE_START);
  fade = Math.max(0, Math.min(0.3, fade));

  const invScale = 1 / scale;      // keep strokes 1 px on-screen
  gctx.lineWidth = 1 * invScale;
  gctx.strokeStyle = `rgba(140,140,140,${fade})`;

  /* ---------- draw light grid every gridSize world-units ---------- */
  const step = Math.abs(map.settings.gridSize) || 1;
  const startX = Math.floor(minX / step) * step;
  const startY = Math.floor(minY / step) * step;

  gctx.beginPath();
  for (let x = startX; x <= maxX; x += step) {
    gctx.moveTo(x, minY);
    gctx.lineTo(x, maxY);
  }
  for (let y = startY; y <= maxY; y += step) {
    gctx.moveTo(minX, y);
    gctx.lineTo(maxX, y);
  }
  gctx.stroke();

  /* ---------- draw bold centre axes ---------- */
  gctx.lineWidth = 2 * invScale;
  gctx.strokeStyle = "rgba(0,0,0,0.5)";
  gctx.beginPath();
  gctx.moveTo(0, minY); gctx.lineTo(0, maxY);  // Y-axis
  gctx.moveTo(minX, 0); gctx.lineTo(maxX, 0);  // X-axis
  gctx.stroke();
}


