export function drawPointEdit() {

  if (pointIds.length === 0) return;
  if (!treeSelection || treeSelection.length === 0) return;
  const sel = treeSelection.at(-1);

  const group = map.groups[sel.groupId];
  if (!group) return;

  const layer = group.layers[sel.layerId];
  if (!layer) return;

  octx.setTransform(1, 0, 0, 1, 0, 0);
  octx.clearRect(0, 0, canvasOverlay.width, canvasOverlay.height);
  octx.save();
  octx.setTransform(scale, 0, 0, scale, pan.x, pan.y);


  const point = map.points[pointIds[0]];
  const dotRadius = 6 / scale;
  octx.fillStyle = selectedColor;
  octx.beginPath();
  octx.lineWidth = 2 / scale; // keep 2px on screen
  octx.strokeStyle = selectedColor;
  octx.moveTo(point.position.x, point.position.z);

  const snapPointId = getSnapPointId(mousePos);
  if (snapPointId) octx.lineTo(map.points[snapPointId].position.x, map.points[snapPointId].position.z);
  else octx.lineTo(mousePos.x, mousePos.z);

  octx.stroke();
  octx.restore();


}

export function drawDoorEdit() {

  if (getCurrentState() === FSM_STATES.ADD_DOOR && splineTransform) {

    if (!treeSelection || treeSelection.length === 0) return;
    const sel = treeSelection.at(-1);

    const group = map.groups[sel.groupId];
    if (!group) return;

    const layer = group.layers[sel.layerId];
    if (!layer) return;

    const line = layer.lines[splineTransform.lineId];
    if (!line) return;

    octx.setTransform(1, 0, 0, 1, 0, 0);
    octx.clearRect(0, 0, canvasOverlay.width, canvasOverlay.height);
    octx.save();
    octx.setTransform(scale, 0, 0, scale, pan.x, pan.y);


    const points = line.pointIds.map(pointId => map.points[pointId]);
    if (points.length > 1) {
      // 1) grab your three positions as simple {x, y, z} objects
      const centerVert = { x: splineTransform.x, z: splineTransform.z };               // e.g. {x:…, y:…, z:…}
      const vert1 = points[splineTransform.segmentIndex].position;
      const vert2 = points[splineTransform.segmentIndex + 1].position;


      // 2) build the segment‐direction vector (in XZ)
      let nx = vert2.x - vert1.x;
      let nz = vert2.z - vert1.z;



      // 4) normalize that normal vector (so its length = 1)
      const len = Math.hypot(nx, nz);
      if (len !== 0) {
        nx /= len;
        nz /= len;
      } else {
        // (degenerate segment: vert1===vert2.  You can just skip or pick a default normal)
        nx = 0;
        nz = 0;
      }


      // 3) segment length
      const d = map.settings.gridSize;
      const segLen = Math.hypot(nx * len, nz * len);   // or simply len from earlier
      if (segLen < (d * 2)) {
        doorNode1Position = null;
        doorNode2Position = null;
        return;
      }
      // 4) choose your desired offset d, but clamp to segLen/2 − ε

      // 5) scale by your desired "offset" distance (call it d)
      // <-- how far you want each point from centerVert

      const offsetX = nx * d;
      const offsetZ = nz * d;

      // 6) build the two offset‐points (above and below the segment)
      doorNode1Position = {
        x: centerVert.x + offsetX,
        y: centerVert.y,        // keep same Y
        z: centerVert.z + offsetZ
      };

      doorNode2Position = {
        x: centerVert.x - offsetX,
        y: centerVert.y,
        z: centerVert.z - offsetZ
      };


      const dotRadius = 6 / scale;
      octx.fillStyle = selectedColor;
      octx.beginPath();
      octx.arc(doorNode1Position.x, doorNode1Position.z, dotRadius, 0, Math.PI * 2);
      octx.arc(doorNode2Position.x, doorNode2Position.z, dotRadius, 0, Math.PI * 2);
      octx.fill();
    }

  }
  octx.restore();
}


/* ------------------------------------------------------------------------
 * Zoom‑and‑pan the new Navigator so a set of world‑vertices fills the view.
 * (No rotation support –‑ the new nav is 2‑D only.)
 *
 * @param {Array<{x:number,y?:number,z?:number}>} verts   – vertices in world units
 * @param {object} [options]
 * @param {number} [options.padding=250]        – screen padding in CSS‑px
 * @param {number} [options.duration=500]       – ms animation duration
 * @param {number} [options.minScale=0.5]       – clamp
 * @param {number} [options.maxScale=100]       – clamp
 * --------------------------------------------------------------------- */
export function focusOnVertices(
  verts,
  {
    padding = 250,
    duration = 500,
    minScale = 0.5,
    maxScale = 100
  } = {}
) {
  if (!navigation || !verts?.length) return;

  /* 0. device pixel ratio + canvas extents */
  const dpr = window.devicePixelRatio || 1;
  const bufW = canvasMain.width;
  const bufH = canvasMain.height;

  /* 1. compute axis‑aligned bbox on X‑Z plane (Y==Z for 2‑D maps) */
  let minX = Infinity, minY = Infinity;
  let maxX = -Infinity, maxY = -Infinity;

  verts.forEach(v => {
    const vx = v.x;
    const vy = (v.z ?? v.y);      // allow either {x,y} or {x,z}
    if (vx == null || vy == null) return;
    if (vx < minX) minX = vx;
    if (vy < minY) minY = vy;
    if (vx > maxX) maxX = vx;
    if (vy > maxY) maxY = vy;
  });

  const bboxW = Math.max(maxX - minX, 1e-6);
  const bboxH = Math.max(maxY - minY, 1e-6);

  /* 2. choose target scale (buffer‑px / world‑unit) */
  const availW = bufW - padding * dpr * 2;
  const availH = bufH - padding * dpr * 2;
  let targetScale = Math.min(availW / bboxW, availH / bboxH);
  targetScale = Math.max(minScale, Math.min(maxScale, targetScale));

  /* 3. target pan so bbox centre → screen centre */
  const cx = (minX + maxX) / 2;
  const cy = (minY + maxY) / 2;
  // Centre the bbox on‑screen: screen = world * scale + pan
  // → pan = screenCentreBufPx − scale * worldCentre
  const panX_target = (bufW / 2) - targetScale * cx;
  const panY_target = (bufH / 2) - targetScale * cy;

  /* 4. animate via Navigator */
  const { scale: startScale, dx: startPanX, dy: startPanY } = navigation.getTransform();
  const t0 = performance.now();
  const ease = t => (t < 0.5 ? 2 * t * t : -1 + (4 - 2 * t) * t);
  const lerp = (a, b, t) => a + (b - a) * t;

  function step(now) {
    const traw = Math.min((now - t0) / duration, 1);
    const t = ease(traw);

    navigation.setTransform({
      scale: lerp(startScale, targetScale, t),
      dx: lerp(startPanX, panX_target, t),
      dy: lerp(startPanY, panY_target, t)
    });

    if (traw < 1) requestAnimationFrame(step);
  }
  requestAnimationFrame(step);
}

export function drawSelectionRect(rectSelectState) {
  octx.setTransform(1, 0, 0, 1, 0, 0);
  octx.clearRect(0, 0, canvasOverlay.width, canvasOverlay.height);
  octx.save();
  const dpr = window.devicePixelRatio || 1;
  octx.setTransform(dpr, 0, 0, dpr, 0, 0);   // scale so CSS‑pixel coords match

  octx.strokeStyle = "#00e069";
  octx.lineWidth = 2 / dpr;                  // 1 CSS px
  octx.setLineDash([4, 4]);
  octx.strokeRect(
    Math.min(rectSelectState.x0, rectSelectState.x1),
    Math.min(rectSelectState.y0, rectSelectState.y1),
    Math.abs(rectSelectState.x1 - rectSelectState.x0),
    Math.abs(rectSelectState.y1 - rectSelectState.y0)
  );
  octx.setLineDash([]);
  octx.restore();
}

/**
 * Remove "orphan" lines exactly as in the C# PreProcessing:
 * - 0 points  → delete line
 * - 1 point   → if that point was shared, remove the line key, then delete
 * - 2 points  → if both ends are the same shared point, remove that line key, then delete
 *
 * @param {LineModel} line
 * @param {object} map.layout  – your root data (with .lines dictionary)
 */




export function preProcessBranch(layer, lineId, pointIds) {
  if (getCurrentState() === FSM_STATES.ADD_POINT) return;

  // safety cleanup for accidetly deleted points
  const line = layer.lines[lineId];
  // Remove any non-existent points from the line and delete them from the map
  line.pointIds = line.pointIds.filter(ptId => {
    if (!map.points[ptId]) {
      delete map.points[ptId];
      return false;
    }
    return true;
  });

  // Clean up twin relationships for remaining points
// line.pointIds.forEach(ptId => {
//   const point = map.points[ptId];
//   if (!Array.isArray(point.twins)) return;
//   point.twins = point.twins.filter(twinId => {
//     // Keep only twins that still exist in the map
//     if (!map.points[twinId]) return false;
//     // Also remove the back-reference from the twin
//     const twinPoint = map.points[twinId];
//     const backIdx = twinPoint.twins.indexOf(ptId);
//     if (backIdx !== -1) twinPoint.twins.splice(backIdx, 1);
//     return true;
//   });
// });

  // 0 points → delete outright
  if (pointIds.length === 0) {

    delete layer.lines[lineId];
    console.log(`Orphan line ${lineId} deleted`);
    return;
  }

  // 1 point → clean up any shared association, then delete
  if (pointIds.length === 1) {
    const pointId = pointIds[0];
    const point = map.points[pointId];
    if (point.associatedLines && point.associatedLines.length === 1 && !point.locationId) {
      addNewLocation(pointId);
      updateSideMenuUI('location');
    }

    //  delete layer.lines[id];
    //  console.log(`Orphan line ${id} deleted`);
    //return;
  }




  // 2 points → if they're actually the same shared point, clean up then delete
  if (pointIds.length === 2 && pointIds[0] === pointIds[1]) {
    delete layer.lines[lineId];
    console.log(`Orphan line ${lineId} deleted`);
    return;
  }

}


export function resizeAllCanvases() {
  const container = canvasMain.parentElement;
  const rect = container.getBoundingClientRect();
  const newW = rect.width * (window.devicePixelRatio || 1);
  const newH = rect.height * (window.devicePixelRatio || 1);

  [canvasBackground, canvasSelection, canvasMain, canvasGrid, canvasOverlay].forEach(cv => {
    cv.width = newW;
    cv.height = newH;
    cv.style.width = rect.width + 'px';
    cv.style.height = rect.height + 'px';
  });
  console.log("resizeAllCanvases");
  refreshViewport();
}




/** Convert world X/Z to integer cell coordinates */
function cellKey(x, z) {
  const ix = Math.floor(x / map.settings.gridSize);
  const iz = Math.floor(z / map.settings.gridSize);
  return `${ix},${iz}`;
}

/** Add a point to the grid */
function gridInsert(pointId) {
  const point = map.points[pointId];
  const key = cellKey(point.position.x, point.position.z);
  if (!grid.has(key)) grid.set(key, []);
  grid.get(key).push(pointId);
}



function findSnapTargetFast(pos) {
  const cx = Math.floor(pos.x / map.settings.gridSize);
  const cz = Math.floor(pos.z / map.settings.gridSize);

  let bestId = null;
  const SNAP_EPS = Math.abs(map.settings.gridSize) > 0 ? Math.abs(map.settings.gridSize) : 1; // your snap radius
  const SNAP_EPS2 = SNAP_EPS * SNAP_EPS;
  let bestD2 = SNAP_EPS2;

  // search cell (cx,cz) and its 8 neighbors
  for (let ix = cx - 1; ix <= cx + 1; ix++) {
    for (let iz = cz - 1; iz <= cz + 1; iz++) {
      const arr = grid.get(`${ix},${iz}`);
      if (!arr) continue;

      for (const pointId of arr) {
        const n = map.points[pointId];
        if (!n) continue;
        const dx = n.position.x - pos.x;
        const dz = n.position.z - pos.z;
        const d2 = dx * dx + dz * dz;
        if (d2 < bestD2) {
          bestD2 = d2;
          bestId = pointId;
        }
      }
    }
  }
  return bestId;   // null if nothing in radius
}

// --- helper: reset view so world origin sits in the canvas centre -----------
export function centerView(targetScale = 6) {
  console.log("reset center view editor");


  // 1. Work in *buffer* pixels (canvas.width / height already include DPR)
  const bufW = canvasMain.width;      // e.g. 2560 on a 1280-CSS-px retina screen
  const bufH = canvasMain.height;


  // 2. World point you want at screen-centre.
  //    For the global origin (0, 0) use cx = 0, cy = 0.
  //    For an arbitrary world point replace cx / cy.
  const cx = 0;
  const cy = 0;

  // 3. Pan so that (cx,cy) → middle of the canvas
  const dx = bufW / 2 - targetScale * cx;
  const dy = bufH / 2 - targetScale * cy;

  // 4. Commit it
  navigation.setTransform({
    scale: targetScale,
    dx,       // = panX buffer-pixels
    dy,       // = panY buffer-pixels
    rotation: 0
  });

  // redraw (if your onUpdate handler doesn’t already do it)
  refreshViewport();
}
