function buildNavTree() {

  const root = document.getElementById("navTree");
  root.innerHTML = "";                                // clear

  /* ─────── Root header + “Add group” button ─────── */
  const header = document.createElement("div");
  header.className = "point";
  header.style.display = "flex";
  header.style.alignItems = "center";

  // make root selectable as "map"
  header.dataset.key = "map";
  header.dataset.level = "map";

  // 1) Label span
  const label = document.createElement("span");
  label.className = "label";
  label.textContent = map.name || map.id;
  label.style.color="#2aa14eff";
  label.style.fontSize="14px";
  label.style.paddingLeft="20px";
  label.style.cursor = "pointer";
  label.addEventListener("click", e => {
    e.stopPropagation();
    updateSideMenuUI("map");
  });
  header.appendChild(label);


  header.querySelector(".label").style.cursor = "pointer";
  header.querySelector(".label").addEventListener("click", (e) => {
    e.stopPropagation();
    // selectNode(header);
    updateSideMenuUI("map");
  });

  root.appendChild(header);

  const groupsLabel = document.createElement("div");
  groupsLabel.className = "point";
  groupsLabel.style.display = "flex";
  groupsLabel.style.alignItems = "center";
  groupsLabel.style.gap = "4px";
  groupsLabel.style.backgroundColor = "rgba(200, 200, 200, 1)"; // light gray
  root.appendChild(groupsLabel);
  // add-group button next to Groups label
  const addGroupBtn = document.createElement("button");
  addGroupBtn.className = "btn-img";
  addGroupBtn.title = "Add group";
  addGroupBtn.style.marginLeft = "auto";
  addGroupBtn.style.paddingRight = "5px";
  addGroupBtn.style.backgroundImage = "url(/Images/AddGroup.png)";
  addGroupBtn.style.backgroundRepeat = "no-repeat";
  addGroupBtn.style.backgroundPosition = "center";
  addGroupBtn.style.backgroundSize = "20px 20px";
  addGroupBtn.addEventListener("click", e => {
    e.stopPropagation();
    // create and add a new group
    const group = new GroupModel();
    //  group.name = "Name here";
    const newGroupId = generateShortGuid();
    map.groups[newGroupId] = group;
    // setGroupId(newGroupId);
    // setLayerId();

    treeSelection.length = 0;
    setTreeSelection(newGroupId, Object.keys(group.layers)[0]);
    // refresh group UI and tree
    updateGroupUI?.();
    buildNavTree(map, root);
  });
  // const groupsText = document.createElement("span");
  //  groupsText.textContent = "Groups";
  //groupsLabel.appendChild(groupsText);
  header.appendChild(addGroupBtn);

  /* ─────── UL for the expandable tree ─────── */
  const ul = document.createElement("ul");
  root.appendChild(ul);
  Object.entries(map.groups).forEach(([gId, b]) => {
    const ulB = addNode(
      ul,
      gId,
      null,
      null,
      b,
      "group"
    );

    const layersUL = document.createElement("ul");
    ulB.appendChild(layersUL);

    Object.entries(b.layers).forEach(([layId, f]) => {
      const ulG = addNode(
        layersUL,
        gId,
        layId,
        null,
        f,
        "layer"
      );
    });
  });

  updateSelectedRows();
}


/* Expand the given row *and all ancestors* so the row is visible. */
function expandRow(root, key) {
  const nodeRow = root.querySelector(`.point[data-key="${key}"]`);
  if (!nodeRow) return;

  function openRow(row) {
    const toggle = row.querySelector(".toggle");
    if (toggle && toggle.textContent === "▸") {
      toggle.textContent = "▾";
    }
    // reveal its direct child <ul>
    row.parentElement.querySelectorAll(":scope > ul").forEach(
      ul => (ul.style.display = "")
    );
  }

  // open this row …
  openRow(nodeRow);

  // … then walk ancestors to the root
  let li = nodeRow.parentElement;         // <li> hosting this .point
  while (li) {
    const parentLi = li.parentElement?.closest("li");
    if (!parentLi) break;
    const parentRow = parentLi.querySelector(":scope > .point");
    if (parentRow) openRow(parentRow);
    li = parentLi;
  }
}

