function getRowById(id, level, root = document.getElementById("navTree")) {
  // Always escape user-supplied strings in a CSS selector
  const escapedId = CSS.escape(id);
  const selector = level
    ? `.point[data-level="${level}"][data-key="${escapedId}"]`
    : `.point[data-key="${escapedId}"]`;

  return root.querySelector(selector);
}

export function updateSelectedRows() {
  const root = document.getElementById("navTree");
  // Remove any previous selections
  clearSelectedRows(root);

  treeSelection.forEach(sel => {
    // Always select the group row by key only
    const groupRow = getRowById(sel.groupId) || getRowById(sel.groupId, "group");
    if (groupRow) {
      const label = groupRow.querySelector('.label');
      if (label) label.classList.add('selected');
    }

    // If there's a valid layer selected, highlight it
    if (sel.layerId) {
      const layerRow = getRowById(sel.layerId, "layer");
      if (layerRow) {
        const label = layerRow.querySelector('.label');
        if (label) label.classList.add('selected');
      }
    }
  });

  refreshViewport();
}




