import { map } from "../editor.js";
import { showInfo } from "../dialogBox.js";   // safe‑optional
import { refreshViewport } from "../draw.js";
import { BuildNavigationTree } from "./sideMenuUI.js";
import { treeSelection, setLocationId } from "../selection.js";

export let backgroundImage;



export function initLayersUI() {
    backgroundImage = new Image();


    const deleteLayer = document.getElementById('deleteLayer');
    deleteLayer.addEventListener("click", (evt) => {
        const selectionData = currentGroupLayerId();
        if (!selectionData) return null;

        showInfo(
            "Are you sure you want to delete this layer?",
            () => {
                removeLayer(selectionData.groupId, selectionData.layerId);
                BuildNavigationTree();
                updateLayersUI();
            },
            null
        );
    });

    // Grab references if you haven’t already:
    const layerName = document.getElementById("layerName");
    layerName.style.marginBottom = '8px';
    const bgWidthInput = document.getElementById("bgWidthInput");
    bgWidthInput.style.marginBottom = '8px';
    const bgHeightInput = document.getElementById("bgHeightInput");
    bgHeightInput.style.marginBottom = '8px';
    const bgOpacityInput = document.getElementById("bgOpacityInput");
    bgOpacityInput.style.marginBottom = '8px';
    const bgOpacityValue = document.getElementById("bgOpacityValue");
    /* always fetch the latest selected  layer */

    document
        .querySelector(".side-menu")          // parent container
        .addEventListener("click", (e) => {
            // const assignBtn     = e.target.closest(".btn-assign");
            const bgAssignBtn = e.target.closest(".btn-bg-assign");
            if (!bgAssignBtn) return;     // clicked elsewhere
            const actionsEl = e.target.closest(".field-group");

            /* ---------- “Assign background” ---------- */
            if (bgAssignBtn) {
                const fileInput = actionsEl.querySelector(".bg-file-input");
                fileInput.click();                      // open chooser

                fileInput.onchange = () => {
                    const file = fileInput.files[0];
                    if (!file) return;

                    const reader = new FileReader();
                    reader.onload = () => {
                        const layer = currentLayer();
                        if (!layer) return;
                        layer.backgroundDataURI = reader.result;
                        layer.backgroundFileName = file.name;
                        // default opacity
                        if (typeof layer.backgroundOpacity !== "number") {
                            layer.backgroundOpacity = 1.0;
                            document.getElementById("bgOpacityInput").value = "1";
                            // document.getElementById("bgOpacityValue").textContent = "1.00";
                        }
                        updateLayersUI();
                    };
                    reader.readAsDataURL(file);
                };
            }
        });


    layerName.addEventListener("input", (evt) => {
        const layer = currentLayer();
        if (!layer) return;

        layer.name = evt.target.value;
        BuildNavigationTree();
    });

    // ---------------------------------------------------
    // 3) Handle “Width (world units)” change
    bgWidthInput.addEventListener("input", (evt) => {
        const layer = currentLayer();
        if (!layer) return;
        const val = parseFloat(evt.target.value);
        if (!isNaN(val) && val >= 0) {
            layer.backgroundWorldWidth = val;
            refreshViewport();
        }
    });

    // ---------------------------------------------------
    // 4) Handle “Height (world units)” change
    bgHeightInput.addEventListener("input", (evt) => {
        const layer = currentLayer();
        if (!layer) return;
        const val = parseFloat(evt.target.value);
        if (!isNaN(val) && val >= 0) {
            layer.backgroundWorldHeight = val;
            refreshViewport();
        }
    });

    // ---------------------------------------------------
    // 5) Handle “Opacity” slider change
    bgOpacityInput.addEventListener("input", (evt) => {
        const layer = currentLayer();
        if (!layer) return;
        const val = parseFloat(evt.target.value);
        if (!isNaN(val) && val >= 0 && val <= 1) {
            layer.backgroundOpacity = val;

            refreshViewport();
        }
    });

    updateLayersUI();
}


export function updateLayersUI() {
    const layerName = document.getElementById("layerName");
    const bgWidthInput = document.getElementById("bgWidthInput");
    const bgHeightInput = document.getElementById("bgHeightInput");
    const bgOpacityInput = document.getElementById("bgOpacityInput");
    // const layerOpacityInput = document.getElementById("layerOpacityInput");
    //  const bgOpacityValue = document.getElementById("bgOpacityValue");
    //  const bgPreview = document.getElementById("bgPreview");
    const bgFileName = document.getElementById("bgFileName");
    //  const worldUnitLabel = document.getElementById("worldunitlabel");

    const layer = currentLayer();
    if (!layer) return;

    layerName.value = layer.name;

    // worldUnitLabel.textContent = "Size (world units in " + map.settings.unit + ") :"

    /* ----- number fields ----- */
    bgWidthInput.value = layer.backgroundWorldWidth ?? "";
    bgHeightInput.value = layer.backgroundWorldHeight ?? "";

    // const op = typeof  layer.backgroundOpacity === "number" ?  layer.backgroundOpacity : 1;
    bgOpacityInput.value = layer.backgroundOpacity;
    //  layerOpacityInput.value = layer.layerOpacity;
    // bgOpacityValue.textContent = layer.backgroundOpacity.toFixed(2);
    // fl.textContent = layer.backgroundOpacity.toFixed(2);

    /* ----- preview image ----- */
    //  if (bgPreview) {
    //   if (layer.backgroundFileName) {
    // bgPreview.src = layer.backgroundDataURI;
    bgFileName.value = layer.backgroundFileName || "";
    //  bgPreview.classList.remove("hidden");
    //  } else {
    //  bgPreview.src = "";
    // bgFileName.textContent = "";
    // bgPreview.classList.add("hidden");
    // }
    //  }
    //renderLayersList();
    updateBackground();   // keep the canvas in sync
}



export function updateBackground() {
    const layer = currentLayer();
    if (!layer) return;

    const backgroundURI = layer.backgroundDataURI;

    // console.log("backgroundURI"+backgroundURI);

    if (!backgroundImage) backgroundImage = new Image();
    if (backgroundURI) {
        backgroundImage.src = backgroundURI;
        backgroundImage.onload = () => {
            // Once the image is ready, force a redraw so the background appears immediately
            refreshViewport();
        };
    } else {
        backgroundImage = null;
        refreshViewport();
    }
}

function currentLayer() {
    if (!treeSelection || treeSelection.length === 0) return null;
    const sel = treeSelection.at(-1);

    const group = map.groups[sel.groupId];
    if (!group) return null;

    const layer = group.layers[sel.layerId];
    if (!layer) return null;
    return layer
}

function currentGroupLayerId() {
    if (!treeSelection || treeSelection.length === 0) return null;
    const sel = treeSelection.at(-1);
    if (sel === -1) return null;

    return { groupId: sel.groupId, layerId: sel.layerId }
}

function removeLayer(gId, layId) {
    // Remove the layer from its group
    if (gId && map.groups[gId]) {
        delete map.groups[gId].layers[layId];
    }

    // Remove any points belonging to the deleted layer
    Object.keys(map.points).forEach(pId => {
        if (map.points[pId].layerId === layId) {
            delete map.points[pId];
        }
    });

    // Remove any location belonging to the deleted layer
    Object.keys(map.locations).forEach(lId => {
        if (map.locations[lId].layerId === layId) {
            delete map.locations[lId];
        }
    });

    // Clear selection if it was this layer
    // if (layerId === layId) {
    // setLayerId?.(null);
    setLocationId(null);
    treeSelection.length = 0;
    //  }
    // Rebuild the tree, show group settings, and re-open the group
    //  const navRoot = parentUL.closest("#navTree");
    //  buildNavTree(map, navRoot);
    // updateSideMenuUI("group");

}