import { SelectionMode } from "../models/models.js";
import {
    pointIds, lineIds, polygonIds, setLineId, setPointId, currentSelectionMode,
    setPolygonId, setLocationId, locationId, treeSelection
} from "../selection.js";
import { octx, canvasOverlay, doorNode1Position, doorNode2Position, navigation, drawDoorEdit, drawPointEdit, drawSelectionRect } from "../draw.js";
import { map, isSnap } from "../editor.js";
import { hitTest } from "./hitTest.js";
import { refreshViewport, canvasMain } from "./editing.js";


//import { NAV_STATES, getNavState } from "../navigation/state.js";
//import { scale, pan } from "../navigation/navigation.js";

import { STATES } from "../navigation.js";

import { computeCentroid, snap } from "./hitTest.js";
import { commit } from "../undoRedo/commit.js";
import { addDoorCommand } from "../undoRedo/addDoor.js";
import { addCommand } from "../undoRedo/addPoint.js";
import { selectCommand } from "../undoRedo/select.js";
import { moveCommand } from "../undoRedo/move.js";
import { updateSideMenuUI } from "./../UI/sideMenuUI.js";
import { deselectEverything, forcePointSelection, forcePolygonSelection, forceEdgeSelection, addNewLocation } from "../UI/toolsMenu.js";
import { updateLocationsUI } from "../UI/locationsUI.js";
import { updatePointUI } from "../UI/pointUI.js";



/**
 * Finite State Machine for handling user interactions
 * @typedef {import('./types.js').PointId} PointId
 * @typedef {import('./types.js').DragState} DragState
 * @typedef {import('./types.js').RectSelectState} RectSelectState
 */

// FSM States
export const FSM_STATES = {
    IDLE: 'Idle',
    RECT_SELECTING: 'RectSelecting',
    READY_TO_DRAG: 'ReadyToDrag',
    DRAGGING: 'Dragging',
    ROTATE: 'Rotate',
    SCALE: 'Scale',
    ADD_POINT: 'AddNode',
    ADD_DOOR: 'AddDoor',
    PICK_POLYGON: 'PickPolygon',
    PICK_POINT: 'PickPoint',
};

// FSM Events
export const FSM_EVENTS = {
    POINTER_DOWN: 'POINTER_DOWN',
    POINTER_MOVE: 'POINTER_MOVE',
    POINTER_UP: 'POINTER_UP',
    KEY_DOWN: 'KEY_DOWN',
    KEY_UP: 'KEY_UP',
    DOUBLE_CLICK: 'DOUBLE_CLICK',
};

let currentState = FSM_STATES.IDLE;

// Global state for drag operations
const drag = {
    active: false,
    startScrX: 0, startScrY: 0,
    startWorld: { x: 0, z: 0 },
    pointStarts: new Map(),   // guid → {x,z}
    pointIds: []                // the actual PointModel refs
};

const DRAG_THRESHOLD = 3;
export let mousePos;
export let mousePosRaw;
export let splineTransform;


let rectSelectState = { active: false, x0: 0, y0: 0, x1: 0, y1: 0 };
let startSelectWorld;

let startMouse, startPositions, startAngle, startDist, startSize;
let startVector;
export let pivot;

// Helper to clear drag state
function clearDragState() {
    drag.active = false;
    drag.pointStarts.clear();
    drag.pointIds.length = 0;
}

// Helper for canvas refresh
let _raf = null;
function requestCanvasRefresh() {
    if (_raf) return;
    _raf = requestAnimationFrame(() => {
        _raf = null;
        refreshViewport();
    });
}



/**
 * Initialize pivot, start positions, and drag state for rotate/scale.
 * Returns the appropriate next FSM state.
 */
function initTransformState(e, STATE) {
    startMouse = { ...mousePos };
    startPositions = pointIds.map(pid => ({ ...map.points[pid].position }));
    const rawPivot = computeCentroid(pointIds.map(id => map.points[id]));
    pivot = {
        x: snap(rawPivot.x),
        y: snap(rawPivot.y),
        z: snap(rawPivot.z)
    };
    pointIds.forEach(pid => {
        const p = map.points[pid];
        p.position.x += pivot.x - rawPivot.x;
        p.position.z += pivot.z - rawPivot.z;
    });
    const v0x = startMouse.x - pivot.x;
    const v0z = startMouse.z - pivot.z;
    startAngle = Math.atan2(v0z, v0x);
    startDist = Math.hypot(v0x, v0z);
    startVector = { x: v0x, z: v0z };
    const distances = startPositions.map(pos =>
        Math.hypot(pos.x - pivot.x, pos.z - pivot.z)
    );
    startSize = distances.length ? Math.max(...distances) : 0;

    drag.pointIds = [...pointIds];
    drag.pointStarts.clear();
    drag.pointIds.forEach(id => drag.pointStarts.set(id, { x: map.points[id].position.x, z: map.points[id].position.z }));
    drag.startScrX = e.clientX;
    drag.startScrY = e.clientY;
    drag.startWorld = { ...mousePos };
    drag.active = true;
    canvasMain.setPointerCapture(e.pointerId);
    refreshViewport();

    return STATE;
}




let handlers = {};
export function registerNavHandlers(table) {
    console.log("Editing FSM handlers registered for states:", Object.keys(table));
    handlers = table || {};
}

// Handlers for FSM transitions
registerNavHandlers({
    [FSM_STATES.IDLE]: {

        [FSM_EVENTS.POINTER_DOWN]: (e) => {
            if (e.button === 2) e.preventDefault();
            clearDragState();
            /* If navigation FSM has already decided we're in pan‑drag mode
               (or the user is holding Alt/Meta, which we map to pan),
               do NOT start rectangle selection here. */
            //   console.log("navigation state", navigation.getState());


            // ─────────── Modifier logic ───────────
            // Holding Cmd (⌘ / Meta) or Ctrl means “multi‑select” — unless
            // the Navigation FSM has already switched to PANNING, in which
            // case we let the nav layer have exclusive control.
            const multi = e.metaKey || e.ctrlKey;
            const navState = navigation.getState();

            if (navState === STATES.PANNING && multi) {
                // User is dragging the canvas while the meta key is held.
                // Defer to navigation; do NOT start any selection logic.
                return FSM_STATES.IDLE;
            }

            const before = {
                points: [...pointIds],
                lines: [...lineIds],
                polygons: [...polygonIds]
            };

            const pointId = hitTest.snapToNode(mousePosRaw);
            const lineId = hitTest.getClosestBranch(mousePosRaw);
            const polygonId = hitTest.getSelectedPolygon(e);

            switch (currentSelectionMode) {
                case SelectionMode.POINT: {
                    if (!pointId) break;
                    const idx = pointIds.indexOf(pointId);
                    if (multi) {
                        // Toggle selection
                        if (idx >= 0) pointIds.splice(idx, 1);
                        else setPointId(pointId);
                    } else {
                        // If multiple points already selected and clicked one of them, keep the group
                        if (!(pointIds.length > 1 && idx >= 0)) {
                            // Otherwise, single-select this point
                            pointIds.length = 0;
                            pointIds.push(pointId);
                            const pt = map.points[pointId];
                            if (pt.locationId) {
                                setLocationId(pt.locationId);
                                updateSideMenuUI('location');
                            } else {
                                setLocationId(null);
                                updateSideMenuUI('point');
                            }
                                             if( pt.twins)         console.log('twings :', pt.twins);
                        }
                    }

                    // Initialize drag state so you can move the selected point(s)
                    drag.pointIds = [...pointIds];
                    drag.pointStarts.clear();
                    drag.pointIds.forEach(pid => {
                        const pos = map.points[pid].position;
                        drag.pointStarts.set(pid, { x: pos.x, z: pos.z });
                    });
                    drag.startScrX = e.clientX;
                    drag.startScrY = e.clientY;
                    drag.startWorld = { ...mousePos };
                    canvasMain.setPointerCapture(e.pointerId);

                    return FSM_STATES.READY_TO_DRAG;
                }

                case SelectionMode.LINE: {
                    if (!lineId) break;

                    const idx = lineIds.indexOf(lineId);

                    if (multi) {
                        // Toggle behaviour when Shift/Ctrl/Meta pressed
                        if (idx >= 0) lineIds.splice(idx, 1);
                        else setLineId(lineId);
                    } else {
                        // Single click → select entire ring the edge belongs to
                        lineIds.length = 0;

                        // const group = map.groups[groupId];
                        // if (!group) return;
                        // const layer = group.layers[layerId];
                        // if (!layer) return;
                        //    lineIds.push(lineId);
                        setLineId(lineId);
                    }
                    break;
                }

                case SelectionMode.POLYGON: {
                    //  console.log("polygonId" + polygonId);
                    if (!polygonId) return;
                    const idx = polygonIds.indexOf(polygonId);
                    if (multi) {
                        // Toggle behaviour when Shift/Ctrl/Meta pressed
                        if (idx >= 0) polygonIds.splice(idx, 1);
                        else setPolygonId(polygonId);
                    } else {
                        // Single click → select entire ring the edge belongs to
                        polygonIds.length = 0;
                        //polygonIds.splice(0, polygonIds.length, polygonId);
                        // polygonIds.push(polygonId);
                        setPolygonId(polygonId);
                    }
                    console.log("polygonIds.length", polygonIds.length);
                    break;
                }
            }

            const after = {
                points: [...pointIds],
                lines: [...lineIds],
                polygons: [...polygonIds]
            };
            if (
                before.points.join(",") !== after.points.join(",") ||
                before.lines.join(",") !== after.lines.join(",") ||
                before.polygons.join(",") !== after.polygons.join(",")
            ) {
                commit(selectCommand(
                    before.points, after.points,
                    before.lines, after.lines,
                    before.polygons, after.polygons
                ));
            }

            if (!multi &&                                   // ⌘ / Ctrl held → no rect select
                currentSelectionMode !== SelectionMode.POLYGON &&
                !pointId && !lineId) {
                const overlayRect = canvasOverlay.getBoundingClientRect();

                // clear any previous rectangle right away
                const octx = canvasOverlay.getContext('2d');
                octx.clearRect(0, 0, canvasOverlay.width, canvasOverlay.height);

                rectSelectState.active = true;
                canvasMain.setPointerCapture(e.pointerId);

                // pixel position of the click
                rectSelectState.x0 = e.clientX - overlayRect.left;
                rectSelectState.y0 = e.clientY - overlayRect.top;

                //  🔑 reset the far corner so it starts as a zero-size box
                rectSelectState.x1 = rectSelectState.x0;
                rectSelectState.y1 = rectSelectState.y0;

                // store world coords for filtering
                startSelectWorld = { x: mousePos.x, z: mousePos.z };

                return FSM_STATES.RECT_SELECTING;
            }
            refreshViewport();
            return FSM_STATES.IDLE;


        },
        [FSM_EVENTS.DOUBLE_CLICK]: (e) => {

            if (e.button === 0) {
                pointIds.length = 0;
                lineIds.length = 0;
                polygonIds.length = 0;
            }

            refreshViewport();
            return FSM_STATES.IDLE;
        },
        // Ignore pointer move/up when idle
        [FSM_EVENTS.POINTER_MOVE]: () => { FSM_STATES.IDLE },
        [FSM_EVENTS.POINTER_UP]: () => FSM_STATES.IDLE,
    },
    [FSM_STATES.RECT_SELECTING]: {
        [FSM_EVENTS.POINTER_MOVE]: (e) => {
            const overlayRect = canvasOverlay.getBoundingClientRect();
            rectSelectState.x1 = e.clientX - overlayRect.left;
            rectSelectState.y1 = e.clientY - overlayRect.top;
            drawSelectionRect(rectSelectState);
            return FSM_STATES.RECT_SELECTING;
        },
        [FSM_EVENTS.POINTER_UP]: (e) => {
            const octx = canvasOverlay.getContext('2d');
            octx.clearRect(0, 0, canvasOverlay.width, canvasOverlay.height);
            // No need to reset transform since we always use save/restore for rect drawing now

            canvasMain.releasePointerCapture(e.pointerId);
            rectSelectState.active = false;
            const sWorld = startSelectWorld !== undefined
                ? startSelectWorld
                : screenToWorld(rectSelectState.x0, rectSelectState.y0);
            // World‐space rectangle bounds (no rotation)
            const wx0 = Math.min(startSelectWorld.x, mousePos.x);
            const wz0 = Math.min(startSelectWorld.z, mousePos.z);
            const wx1 = Math.max(startSelectWorld.x, mousePos.x);
            const wz1 = Math.max(startSelectWorld.z, mousePos.z);

            const beforePoints = [...pointIds];
            const beforeLines = [...lineIds];
            const beforePolygons = [...polygonIds];

            const newPointIdsSet = new Set();
            const newLineIdsSet = new Set();
            const newPolygonIdsSet = new Set();

            switch (currentSelectionMode) {
                case SelectionMode.POINT: {


                    const sel = treeSelection.at(-1);
                    if (sel) {
                        const group = map.groups[sel.groupId];
                        if (!group) return;
                        const layer = group.layers[sel.layerId];
                        if (!layer) return;
                        Object.entries(layer.lines).forEach(([lineId, line]) => {
                            line.pointIds.forEach(pid => {
                                if (newPointIdsSet.has(pid)) return;
                                const p = map.points[pid].position;
                                if (p.x >= wx0 && p.x <= wx1 && p.z >= wz0 && p.z <= wz1) {
                                    newPointIdsSet.add(pid);
                                }
                            });
                        });
                    };

                    if (newPointIdsSet.length == 1) {
                        const pt = map.points[newPointIdsSet[0]];
                        if (pt.locationId) {
                            setLocationId(pt.locationId);
                            updateSideMenuUI('location');
                        } else {
                            setLocationId(null);
                            updateSideMenuUI('point');
                        }
                    }

                    break;
                }

                case SelectionMode.LINE:
                    hitTest.pickLinesRect({ x0: wx0, z0: wz0, x1: wx1, z1: wz1 }).forEach(lineId => {
                        if (newLineIdsSet.has(lineId)) return;
                        newLineIdsSet.add(lineId);
                    });
                    break;

                case SelectionMode.POLYGON:
                    break;
            }

            const newPointIds = Array.from(newPointIdsSet);
            const newLineIds = Array.from(newLineIdsSet);
            const newPolygonIds = Array.from(newPolygonIdsSet);

            if (
                JSON.stringify(beforePoints) !== JSON.stringify(newPointIds) ||
                JSON.stringify(beforeLines) !== JSON.stringify(newLineIds) ||
                JSON.stringify(beforePolygons) !== JSON.stringify(newPolygonIds)
            ) {
                commit(selectCommand(
                    beforePoints, newPointIds,
                    beforeLines, newLineIds,
                    beforePolygons, newPolygonIds
                ));
            }
            refreshViewport();
            return FSM_STATES.IDLE;
        }
    },
    [FSM_STATES.READY_TO_DRAG]: {
        /* If the user moves far enough, switch to full DRAGGING */
        [FSM_EVENTS.POINTER_MOVE]: (e) => {
            const dx = e.clientX - drag.startScrX;
            const dy = e.clientY - drag.startScrY;
            if (Math.abs(dx) > DRAG_THRESHOLD || Math.abs(dy) > DRAG_THRESHOLD) {
                /* Promote to DRAGGING (reuse the logic already inside that state) */
                return FSM_STATES.DRAGGING;
            }
            return FSM_STATES.READY_TO_DRAG;
        },
        /* If they release without moving much, treat it like a simple click */
        [FSM_EVENTS.POINTER_UP]: (e) => {
            canvasMain.releasePointerCapture(e.pointerId);
            clearDragState();
            refreshViewport();
            return FSM_STATES.IDLE;
        },
        /* Ignore other events */
        [FSM_EVENTS.POINTER_DOWN]: () => FSM_STATES.READY_TO_DRAG,
        [FSM_EVENTS.KEY_DOWN]: () => FSM_STATES.READY_TO_DRAG,
        [FSM_EVENTS.KEY_UP]: () => FSM_STATES.READY_TO_DRAG
    },
    [FSM_STATES.DRAGGING]: {
        [FSM_EVENTS.POINTER_MOVE]: (e) => {
            const worldDX = mousePos.x - drag.startWorld.x;
            const worldDZ = mousePos.z - drag.startWorld.z;

            drag.pointIds.forEach(id => {
                const s = drag.pointStarts.get(id);
                map.points[id].position.x = snap(s.x + worldDX);
                map.points[id].position.z = snap(s.z + worldDZ);
            });
            requestCanvasRefresh();
            return FSM_STATES.DRAGGING;
        },
        [FSM_EVENTS.POINTER_UP]: (e) => {
            const before = drag.pointIds.map(id => ({ pointId: id, x: drag.pointStarts.get(id).x, z: drag.pointStarts.get(id).z }));
            const after = drag.pointIds.map(id => ({
                pointId: id,
                x: map.points[id].position.x,
                z: map.points[id].position.z
            }));
            const changed = before.some((b, i) => b.x !== after[i].x || b.z !== after[i].z);
            if (changed) {
                const moveCmd = moveCommand(before, after);
                commit(moveCmd);
            }
            canvasMain.releasePointerCapture(e.pointerId);
            clearDragState();
            refreshViewport();
            return FSM_STATES.IDLE;
        }
    },
    [FSM_STATES.ROTATE]: {
        [FSM_EVENTS.POINTER_DOWN]: (e) => initTransformState(e, FSM_STATES.ROTATE),

        [FSM_EVENTS.POINTER_MOVE]: (e) => {
            if (!pivot) {
                // Ensure pivot is set; bail until POINTER_DOWN sets it
                return FSM_STATES.ROTATE;
            }
            const v = { x: mousePos.x - pivot.x, z: mousePos.z - pivot.z };
            const rawAngle = Math.atan2(v.z, v.x) - startAngle;
            const snapRotation = map.settings.rotationSnap > 0 ? map.settings.rotationSnap : 5;
            const snappedDeg = Math.round(rawAngle * (180 / Math.PI) / snapRotation) * snapRotation;
            const angle = snappedDeg * (Math.PI / 180);

            const cosA = Math.cos(angle);
            const sinA = Math.sin(angle);
            drag.pointIds.forEach((id, i) => {
                const sx = startPositions[i].x - pivot.x;
                const sz = startPositions[i].z - pivot.z;
                map.points[id].position.x = pivot.x + (sx * cosA - sz * sinA);
                map.points[id].position.z = pivot.z + (sx * sinA + sz * cosA);
            });
            requestCanvasRefresh();
            return FSM_STATES.ROTATE;
        },
        [FSM_EVENTS.POINTER_UP]: (e) => {
            clearDragState();
            refreshViewport();
            return FSM_STATES.ROTATE;
        }
    },
    [FSM_STATES.SCALE]: {
        [FSM_EVENTS.POINTER_DOWN]: (e) => initTransformState(e, FSM_STATES.SCALE),

        [FSM_EVENTS.POINTER_MOVE]: (e) => {

            const v = { x: mousePos.x - pivot.x, z: mousePos.z - pivot.z };
            let factor = 1;
            if (startDist > 0) {
                const dot = v.x * startVector.x + v.z * startVector.z;
                factor = dot / (startDist * startDist);
            }

            drag.pointIds.forEach((id, i) => {
                const sx = startPositions[i].x - pivot.x;
                const sy = startPositions[i].y - pivot.y;
                const sz = startPositions[i].z - pivot.z;

                map.points[id].position.x = snap(pivot.x + sx * factor);
                map.points[id].position.y = snap(pivot.y + sy * factor);
                map.points[id].position.z = snap(pivot.z + sz * factor);
            });
            requestCanvasRefresh();
            return FSM_STATES.SCALE;
        },
        [FSM_EVENTS.POINTER_UP]: (e) => {
            clearDragState();
            refreshViewport();
            return FSM_STATES.SCALE;
        }
    },
    [FSM_STATES.ADD_POINT]: {
        [FSM_EVENTS.POINTER_DOWN]: (e) => {
            if (e.button !== 0) return FSM_STATES.ADD_POINT;      // ignore right/middle

            const cmd = addCommand();
            commit(cmd);
            //  refreshViewport();
            /* stay in ADD mode, but return to Idle state awaiting next click */
            return FSM_STATES.ADD_POINT;
        },

        [FSM_EVENTS.POINTER_MOVE]: (e) => {
            //  const group = map.groups[groupId];
            //  if (!group) return FSM_STATES.ADD_POINT;
            //  const layer = group.layers[layerId];
            //  if (!layer) return FSM_STATES.ADD_POINT;

            //  if (lineIds.length) {
            //      splineTransform = hitTest.getClosestPointOnBranches(
            //          mousePos,
            //          lineIds
            //      );
            //      refreshViewport();
            //  }
            drawPointEdit();
            return FSM_STATES.ADD_POINT;
        },
        [FSM_EVENTS.POINTER_UP]: (e) => {
            // This state is for adding; pointerup means the action is complete.
            // The actual add command is committed on pointerdown in IDLE state.
            return FSM_STATES.ADD_POINT;
        }
    },
    [FSM_STATES.ADD_DOOR]: {
        [FSM_EVENTS.POINTER_MOVE]: (e) => {
            //  const group = map.groups[groupId];
            //  if (!group) return FSM_STATES.ADD_DOOR;
            //  const layer = group.layers[layerId];
            //  if (!layer) return FSM_STATES.ADD_DOOR;
            if (lineIds) {
                const doorWidth = map.settings.gridSize;
                splineTransform = hitTest.getClosestPointOnBranches(mousePos, lineIds, doorWidth);
                //   console.log("update splineTransform" + splineTransform);
                refreshViewport();
            }
            drawDoorEdit();
            return FSM_STATES.ADD_DOOR;
        },
        [FSM_EVENTS.POINTER_UP]: (e) => {
            // This state is for adding, pointerup means the action is complete.

            const addDoorCmd = addDoorCommand(splineTransform, doorNode1Position, doorNode2Position);
            commit(addDoorCmd);
            return FSM_STATES.IDLE;
        }
    },
    [FSM_STATES.PICK_POLYGON]: {
        [FSM_EVENTS.POINTER_DOWN]: (e) => {
            //  const group = map.groups[groupId];
            //  if (!group) return null;
            //
            //  const layer = group.layers[layerId];
            //  if (!layer) return null;

            const location = map.locations[locationId];
            if (!location) return null;

            const polygonId = hitTest.getSelectedPolygon(e);
            if (!polygonId) return;

            if (!location.polygonIds.includes(polygonId)) {
                // add it
                location.polygonIds.push(polygonId);
            } else {
                // remove it
                const idx = location.polygonIds.indexOf(polygonId);
                if (idx !== -1) {
                    location.polygonIds.splice(idx, 1);
                }
            }
            updateLocationsUI();
            return FSM_STATES.PICK_POLYGON;
        }
    },

    [FSM_STATES.PICK_POINT]: {
        [FSM_EVENTS.POINTER_DOWN]: (e) => {
            if (pointIds.length === 1) {

                const selectedPointId = pointIds[0];
                const selectedPoint = map.points[selectedPointId];
                const pointId = hitTest.pickPoint(mousePosRaw);
                if (!pointId) return;

                if (selectedPoint.locationId) {
                    const location = map.locations[locationId];
                    if (!location) return null;
                    if (!location.accessPointIds.includes(pointId)) {
                        // add it
                        location.accessPointIds.push(pointId);
                    } else {
                        // remove it
                        const idx = location.accessPointIds.indexOf(pointId);
                        if (idx !== -1) {
                            location.accessPointIds.splice(idx, 1);
                        }
                    }
                    updateLocationsUI();
                } else {

                    const twinPoint = map.points[pointId];
                    // Ensure twin arrays exist
                    selectedPoint.twins = selectedPoint.twins || [];
                    twinPoint.twins   = twinPoint.twins   || [];

                    // Add missing relationship entries
                    if (!selectedPoint.twins.includes(pointId)) {
                      selectedPoint.twins.push(pointId);
                    }
                    if (!twinPoint.twins.includes(selectedPointId)) {
                      twinPoint.twins.push(selectedPointId);
                    }

                    // Debug logs to verify both sides
                   
                    updatePointUI();
                }
            }
            return FSM_STATES.PICK_POINT;
        }
    }
});
//console.log("Editing FSM fully initialized. States:", Object.keys(handlers));

export function dispatch(event, e) {
    const handler = handlers[currentState]?.[event];
    // console.log(`dispatch: state=${currentState}, event=${event}, handler=${handler}`);
    if (handler) {
        const nextState = handler(e);
        if (nextState && nextState !== currentState) {
            //   console.log(`FSM Transition: ${currentState} -> ${nextState} on ${event}`);
            currentState = nextState;
        }
    }
}

export function getCurrentState() {
    return currentState;
}

export function setMousePos(newMousePos, newMousePosRaw) {

    mousePos = {
        x: snap(newMousePos.x),
        z: snap(newMousePos.z)
    };
    mousePosRaw = newMousePosRaw;   // keep the unsnapped copy
}
export function getRectSelectState() {
    return rectSelectState;
}

/**
 * Forcefully set the current FSM state.
 * @param {string} state - One of FSM_STATES.
 */
export function setFSMState(state) {
    if (Object.values(FSM_STATES).includes(state)) {
        currentState = state;
        console.log(`Current state: ${state}`);

        switch (currentState) {
            case FSM_STATES.ROTATE:
                canvasMain.style.cursor = "alias";
                break;

            case FSM_STATES.SCALE:
                canvasMain.style.cursor = "nwse-resize";
                break;

            case FSM_STATES.ADD_DOOR:
                break;

            case FSM_STATES.ADD_POINT:
                canvasMain.style.cursor = "crosshair";
                break;

            case FSM_STATES.IDLE:
        
                    octx.setTransform(1, 0, 0, 1, 0, 0);
                    octx.clearRect(0, 0, canvasOverlay.width, canvasOverlay.height);
              
                canvasMain.style.cursor = "default";
                switch (currentSelectionMode) {
                    case SelectionMode.POINT: {
                        forcePointSelection();
                        break;
                    }

                    case SelectionMode.LINE:
                        forceEdgeSelection();
                        break;

                    case SelectionMode.POLYGON:
                        forcePolygonSelection();
                        break;
                }
                break;

            case FSM_STATES.PICK_POLYGON:
                canvasMain.style.cursor = 'crosshair'
                deselectEverything();
                break;

            case FSM_STATES.PICK_POINT:
                canvasMain.style.cursor = 'crosshair'
                deselectEverything();
                break;

            default:
                canvasMain.style.cursor = "default";
                break;
        }
    } else {
        console.warn(`Invalid FSM state: ${state}`);
    }
}

// Add local screenToWorld function for use in this file
function screenToWorld(screenX, screenY) {
    const dpr = window.devicePixelRatio || 1;
    const canvasWidth = canvasOverlay.width / dpr;
    const canvasHeight = canvasOverlay.height / dpr;

    //  const relX = (screenX - canvasWidth / 2 - pan.x) / scale;
    //  const relZ = (screenY - canvasHeight / 2 - pan.y) / scale;

    const transform = navigation.getTransform();
    const relX = (screenX - canvasWidth / 2 - transform.dx) / transform.scale;
    const relZ = (screenY - canvasHeight / 2 - transform.dy) / transform.scale;

    return { x: relX, z: relZ };
}

function worldToScreen(worldX, worldZ) {
    const dpr = window.devicePixelRatio || 1;
    const canvasWidth = canvasOverlay.width / dpr;
    const canvasHeight = canvasOverlay.height / dpr;

    //const screenX = canvasWidth / 2 + pan.x + worldX * scale;
    //const screenY = canvasHeight / 2 + pan.y + worldZ * scale;

    const transform = navigation.getTransform();
    const screenX = canvasWidth / 2 + transform.dx + worldX * transform.scale;
    const screenY = canvasHeight / 2 + transform.dy + worldZ * transform.scale;

    return { x: screenX, y: screenY };
}

