
export async function getMap({ mapid, onSuccess, onError }) {
  try {
    const resp = await fetch(`/Builder/GetMap?mapid=${encodeURIComponent(mapid)}`);
    if (!resp.ok) throw new Error(`Load failed: ${resp.status} ${resp.statusText}`);
    const data = await resp.json();
    console.log("map fetched!", data);
    onSuccess?.(data);
  } catch (err) {
    onError?.(err);
  }
}

export async function postMap({ map, onSuccess, onError }) {
  try {
    const resp = await fetch(
      `/Builder/PostMap?mapid=${encodeURIComponent(map.id)}`,
      {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(map)
      }
    );
    if (!resp.ok) throw new Error(`Save failed: ${resp.status} ${resp.statusText}`);
    // Parse updated map JSON
    // Attempt to parse JSON; fallback to the original map object
    let updated;
    try {
      updated = await resp.json();
    } catch {
      updated = map;
    }

    console.log("map saved, server returned:", updated);
    onSuccess?.(updated);
  } catch (err) {
    onError?.(err);
  }
}

export async function publishMap({ mapid, onSuccess, onError }) {
  try {
    const resp = await fetch(
      `/Builder/PublishMap?mapid=${encodeURIComponent(mapid)}`,
      { method: "POST" }
    );
    if (!resp.ok) throw new Error(`Publish failed: ${resp.status} ${resp.statusText}`);
    // Parse any JSON result (e.g., published map info)
       let result;
    try {
      result = await resp.json();
    } catch {
      result = null;
    }
    console.log("map published, server returned:", result);
    onSuccess?.(result);
    console.log("map published, server returned:", result);
    onSuccess?.(result);
  } catch (err) {
    onError?.(err);
  }}