/* eslint no-undef: off */
import { FSM_STATES, setFSMState } from "../editing/state.js";
import { refreshViewport } from "../draw.js";
import { pointIds } from "../selection.js";
import { map } from "../editor.js";

/**
 * Build the "Twins" picker UI for a given point.
 * @param {HTMLElement} container   Parent element where the UI goes.
 * @param {PointModel}  point       Currently‑selected point model.
 */
export function updatePointUI() {

if(pointIds.length!==1) return;
const pointId=pointIds[0];
const point = map.points[pointId];

    const container = document.getElementById('pointContainer');
    container.innerHTML = ''; // clear old

    // --- clear any previous markup ---
    container.innerHTML = "";

    /* ╔═ Header: "Twins" label + pick button ════════════════════════════════ */
    const twinsHeader = document.createElement("div");
    twinsHeader.className = "field-group";
    twinsHeader.style.display = "grid";
    twinsHeader.style.gridTemplateColumns = "auto 1fr";

    // label
    const twinsTitle = document.createElement("label");
    twinsTitle.textContent = "Twins";
    twinsHeader.appendChild(twinsTitle);

    // pick button
    const pickTwinsBtn = document.createElement("button");
    pickTwinsBtn.type = "button";
    pickTwinsBtn.className = "btn-img";
    Object.assign(pickTwinsBtn.style, {
        backgroundImage: "url(/Images/Pick.png)",
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center",
        backgroundSize: "16px 16px",
        justifySelf: "end"
    });

    pickTwinsBtn.onclick = () => {
        setFSMState(FSM_STATES.PICK_POINT);   // switch to point‑picking mode
        refreshViewport();                    // visual cue
    };

    twinsHeader.appendChild(pickTwinsBtn);
    container.appendChild(twinsHeader);

    /* ╔═ Tags container showing current twins ═══════════════════════════════ */
    const twinsTags = document.createElement("div");
    twinsTags.id = "twinsTags";
    twinsTags.className = "tagContainer";
    Object.assign(twinsTags.style, {
        display: "flex",
        flexWrap: "wrap",
        justifyContent: "flex-start"
    });
    container.appendChild(twinsTags);

    renderTwinTags(point.twins ?? [], twinsTags, point);
}

/* Helper to show the currently selected twin IDs as pills (Access-Points style) */
function renderTwinTags(idList, parent, point) {
  parent.innerHTML = "";

  idList.forEach((pid, idx) => {
    // pill span
    const pill = document.createElement("span");
    pill.className = "tag-pill";
    pill.textContent = pid.length > 3 ? pid.slice(0, 2) + "…" : pid;

    // click selects that twin point
    pill.addEventListener("click", () => {
      pointIds.length = 0;
      setPointId(pid);
      refreshViewport();
    });

    // delete (×) button
    const delBtn = document.createElement("button");
    delBtn.type = "button";
    delBtn.className = "close-btn";
    delBtn.innerHTML = "&times;";

    delBtn.addEventListener("click", (e) => {
      e.stopPropagation();           // prevent pill click
      idList.splice(idx, 1);         // remove from array
      point.twins = [...idList];     // persist change on model
      renderTwinTags(idList, parent, point);  // re-render
      refreshViewport();
    });

    pill.appendChild(delBtn);
    parent.appendChild(pill);
  });
}